<template>
  <div class="flex items-center">
    <div class="mr-2">
      <svg
        width="80"
        height="80"
        viewBox="-18.5 -18.5 185 185"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        style="transform: rotate(-90deg)"
      >
        {{ calculatedOffset }}
        <circle
          r="64"
          cx="74"
          cy="74"
          fill="transparent"
          stroke="#e0e0e0"
          stroke-width="12"
          :stroke-dasharray="`${baseOffset}px`"
          stroke-dashoffset="0"
        ></circle>
        <circle
          r="64"
          cx="74"
          cy="74"
          :stroke="progressPercentage >= 100 && !downloadUrl ? '#e69f5b' : '#3b82f6'"
          stroke-width="12"
          stroke-linecap="round"
          :stroke-dashoffset="`${calculatedOffset}px`"
          fill="transparent"
          :stroke-dasharray="`${baseOffset}px`"
        ></circle>
        <text
          :x="
            progressPercentage < 100
              ? progressPercentage < 25
                ? '50px'
                : '42px'
              : '40px'
          "
          y="87px"
          fill="#262626"
          font-size="32px"
          font-weight="bold"
          style="
            transform: rotate(90deg) translate(0, -144px);
            text-align: center;
          "
        >
          {{ rounderProgress || 0 }}%
        </text>
      </svg>
    </div>
    <div class="mr-4" :style="{ minWidth: '18rem' }">
      <p>Status:
        {{
          progressPercentage > 0 && progressPercentage < 100
            ? 'Downloading'
            : progressPercentage === 100
              ? downloadUrl
                ? 'Downloaded'
                : 'Download Failed'
              : 'Download'
        }}
        Invalid Template
      </p>
      <h3 class="text-xl">Price Editor</h3>
      <p>
        {{
          progressPercentage === 100
            ? downloadUrl
              ? 'Downloaded'
              : 'Download Failed'
            : 'Downloading'
        }}
        {{ progressPercentage === 100 && downloadUrl ? typeFile : '' }}
      </p>
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, computed, watch } from 'vue'
import { ElMessage } from 'element-plus'
import { processing as processingImport, useNotifDownload } from 'composable/pricing'

export default defineComponent({
  setup() {
    const { closeNotifInvalid, download: downloadTemplate } = useNotifDownload()
    const timer = ref(null)
    const baseOffset = ref(401.92)
    const progressPercentage = computed(
      () => processingImport.value.downloadProgressInvalid
    )
    const rounderProgress = computed(() => Number(progressPercentage.value).toFixed(0))
    const typeFile = computed(() => processingImport.value.typeDownloadInvalid)
    const downloadUrl = computed(() => processingImport.value.pathDownloadInvalid)
    const calculatedOffset = computed(
      () =>
        baseOffset.value - (baseOffset.value * progressPercentage.value) / 100
    )
    const handleInprogress = () => {
      ElMessage({
        message: 'Dowload in progress, feel free to navigate away--your download will continue',
        type: 'info'
      })
    }
    const handleSucceed = () => {
      ElMessage({
        message: 'Export Successfull!',
        type: 'success'
      })
    }

    watch(() => ({
      progress: progressPercentage.value, file: downloadUrl.value
    }), (val) => {
      if (val.progress >= 100) {
        handleSucceed()
        if (!!val.file) downloadTemplate(val.file)
        timer.value = setTimeout(() => closeNotifInvalid(), 5000)
      } else !!clearTimeout.value && clearTimeout(timer.value)
    })

    return {
      progressPercentage,
      rounderProgress,
      downloadUrl,
      typeFile,
      baseOffset,
      calculatedOffset,
      handleInprogress,
      handleSucceed
    }
  }
})
</script>
<style lang="scss"></style>
