import { reactive } from 'vue'
import { isWhiteLabel } from 'composable/whitelabel'

const state = reactive({
  show: false,
  title: '',
  description: '',
  image_url: '',
  package_name: 'Pro'
})

const usePaywall = () => {
  const open = (options = { title: '', description: '', image_url: '', package_name: 'Pro' }) => {
    if (!isWhiteLabel.value) {
      state.show = true
      state.title = options.title
      state.description = options.description
      state.image_url = options.image_url
      state.package_name = options.package_name || 'Pro'
    }
  }
  return {
    state,
    open
  }
}

export { usePaywall }
