import request  from 'utils/request'

const state = () => ({
  all: {},
  show: {}
})

const getters = {
}

const mutations = {
  SET_ALL(state, payload){
    state.all = payload
  },
  SET_SHOW(state, payload){
    state.show = payload
  }
}

const actions = {
  async all({commit}, payload) {
    const response = await request({
      url: `/v1/pos/settings/faq`,
      method: 'get',
      params: payload
    })
    commit('SET_ALL', response)
  },
  async show({commit}, payload) {
    const response = await request({
      url: `/v1/pos/settings/faq/${payload}`,
      method: 'get'
    })
    commit('SET_SHOW', response)
  },
  async create({}, payload){
    await request({
      url: `/v1/pos/settings/faq`,
      method: 'post',
      data: payload
    })
  },
  async update({}, payload){
    await request({
      url: `/v1/pos/settings/faq/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method:'PUT'
      }
    })
  },
  async delete({}, payload){
    await request({
      url: `/v1/pos/settings/faq/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}