import request  from 'utils/request'

const state = () => ({
  statuses: [],
  optionStatuses: [],
  paymentMethods: [],
  all: [],
  show: {
    data: null,
    items: [],
    statuses: {
      internal: [],
      customer: []
    },
    pictures: {
      custom_offer: [],
      nad: []
    },
    notes: null,
    paymentMethods: null
  }
})

const getters = {
}

const mutations = {
  SET_STATUSES(state, payload) {
    state.statuses = payload
  },
  SET_OPTION_STATUSES(state, payload) {
    state.optionStatuses = payload
  },
  SET_PAYMENT_METHODS(state, payload) {
    state.paymentMethods = payload.map(item => ({
      ...item,
      component: item.is_custom ? 'Custom' : item.name === 'eCheck' ? 'Echeck' : item.name.replace(/ /g, '')
    }))
  },
  SET_ALL(state, payload) {
    state.all = {
      ...payload,
      data: payload.data.map(i => ({
        ...i,
        page: payload.current_page
      }))
    }
  },
  SET_SHOW(state, payload) {
    state.show.data = payload
  },
  SET_SHOW_ITEMS(state, payload) {
    state.show.items = payload
  },
  SET_SHOW_STATUSES(state, payload) {
    state.show.statuses = payload
  },
  SET_SHOW_STATUS(state, payload) {
    state.show.notes[payload.id] = payload.data
  },
  SET_SHOW_NOTES(state, payload) {
    state.show.notes = payload
  },
  SET_SHOW_PAYMENT_METHODS(state, payload) {
    state.show.paymentMethods = payload.map(item => ({
      ...item,
      component: item.is_custom ? 'Custom' : item.name === 'eCheck' ? 'Echeck' : item.name.replace(/ /g, '')
    }))
  },
  SET_SHOW_PICTURES(state, payload){
    state.show.pictures = payload
  }
}

const actions = {
  async all({ commit }, payload) {
    const response = await request({
      url: '/v2/pos/local-order',
      method: 'get',
      params: payload
    })

    commit('SET_ALL', response)
  },
  async create({}, payload) {
    return await request({
      url: '/v1/pos/local-order',
      method: 'post',
      data: payload
    })
  },

  async delete({}, payload) {
    return await request({
      url: `/v1/pos/local-order/${payload.id}/delete`,
      method: 'post',
      data: {
        box_id: payload.box_id
      }
    })
  },

  async paymentMethods({ commit }) {
    const response = await request({
      url: '/v1/pos/local-order/payment-method',
      method: 'get'
    })

    commit('SET_PAYMENT_METHODS', response)
  },
  async show({ commit }, payload) {
    commit('SET_SHOW', {})
    const newPayload = typeof payload === 'object' ? {...payload} : undefined

    if (newPayload) delete newPayload.id

    const response = await request({
      url: `/v1/pos/local-order/show/${payload.id || payload}`,
      params: newPayload || {},
      method: 'get'
    })

    commit('SET_SHOW', response)
  },
  async showItems({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/order/order-item/${payload.id}`,
      params: payload.params,
      method: 'get'
    })
    commit('SET_SHOW_ITEMS', response)
    return response
  },
  async showItemUpdate({}, payload) {
    return await request({
      url: `/v1/pos/local-order/product/${payload.box_id}/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async showItemCreate({}, payload){
    return await request({
      url: `/v1/pos/local-order/add-product/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data
      }
    })
  },
  async showItemDelete({}, payload) {
    return await request({
      url: `/v1/pos/local-order/product/${payload.box_id}/${payload.id}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async showStatuses({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/order/status/${payload}`,
      method: 'get',
      params: {
        buyback: 'local'
      }
    })

    commit('SET_SHOW_STATUSES', response)
  },
  async showStatusUpdate({}, payload) {
    return await request({
      url: `/v1/pos/order/status/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async showNotes({ commit }, payload) {
    const newPayload = typeof payload === 'object' ? {...payload} : undefined

    if (newPayload) delete newPayload.id

    const response = await request({
      url: `/v1/pos/order-notes/${payload.id || payload}`,
      params: newPayload,
      method: 'get'
    })

    commit('SET_SHOW_NOTES', response)
  },
  async showNoteCreate({}, payload) {
    return await request({
      url: `/v1/pos/order-notes`,
      method: 'post',
      data: payload
    })
  },
  async showNoteUpdate({}, payload) {
    return await request({
      url: `/v1/pos/order-notes/update/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async showNoteDelete({}, payload) {
    return await request({
      url: `/v1/pos/order-notes/delete/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async showPaymentMethods({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/local-order/payment-method/order/${payload.id}`,
      method: 'get',
      params: payload.params
    })

    commit('SET_SHOW_PAYMENT_METHODS', response)
  },
  async showPaymentMethodUpdate({}, payload) {
    return await request({
      url: `/v1/pos/local-order/customer/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async printCheckBatch({ commit }, _) {
    return await request({
      url: `/v1/pos/print/local-check-batch`,
      method: 'get',
      responseType: 'arraybuffer'
    })
  },
  async showPictures({commit}, payload){
    const newPayload = typeof payload === 'object' ? {...payload} : undefined

    if (newPayload) delete newPayload.id

    const response = await request({
      url: `/v1/pos/order/picture/${payload.id || payload}`,
      params: newPayload,
      method: 'get'
    })
    commit('SET_SHOW_PICTURES', response)
  },
  async showPictureCreate({commit}, payload){
    const response = await request({
      url: `/v1/pos/order/picture/${payload.id}`,
      method: 'post',
      data: payload.data,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    return response
  },
  async showPictureDelete({commit}, payload){
    const response = await request({
      url: `/v1/pos/order/picture/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
    return response
  },
  async showOrderOfferUpdate({commit}, payload){
    const response = await request({
      url: `/v1/customer/order-offer/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
    return response
  },
  async updateAbandonBuybackCustomer(_, payload){
    await request({
      url: `/v1/pos/local-order/customer-temp/${payload.id}`,
      method: 'put',
      data: payload.data
    })
  },
  async generateQR() {
    return await request({
      url: `/v1/pos/diagnostic/generate-qr`,
      method: 'post'
    })
  },
  async getDiagnosticBBB(_, id) {
    return await request({
      url: `/v1/pos/diagnostic/${id}`,
      method: 'get'
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}