import { sizes } from '../util/variable'

export default (prefix = null) => ({
  props: {
    size: {
      type: String,
      default: 'sm'
    }
  },
  computed: {
    isSizeExist() {
      return Object.keys(sizes()).includes(this.size)
    },
    classSize() {
      return this.isSizeExist ? `${prefix + '-' || ''}${this.size}` : ''
    }
  }
})