export default (prefix = null) => ({
  props: {
    top: {
      type: Boolean,
      default: false
    },
    right: {
      type: Boolean,
      default: false
    },
    bottom: {
      type: Boolean,
      default: false
    },
    left: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    classPosition() {
      return [
        this.top ? `${prefix + '-' || ''}top` : '',
        this.right ? `${prefix + '-' || ''}right` : '',
        this.bottom ? `${prefix + '-' || ''}bottom` : '',
        this.left ? `${prefix + '-' || ''}left` : ''
      ].join(' ')
    }
  }
})