<script>
import { defineComponent, h, toRef, computed } from 'vue'

export default defineComponent({
  name: 'Loading',
  props: {
    size: {
      type: Number,
      default: 20
    },
    width: {
      type: Number,
      default: 2
    }
  },
  setup(props) {
    const size = toRef(props, 'size')
    const halfSize = computed(() => {
      return size.value / 2
    })
    const radius = computed(() => {
      return halfSize.value - (halfSize.value * 0.1)
    })
    const c = computed(() => {
      return 2 * 3.1416 * radius.value
    })
    const c34 = computed(() => {
      return 3 / 4 * c.value
    })

    return {
      size,
      halfSize,
      radius,
      c,
      c34
    }
  },
  render() {
    return h('svg', {
      xmlns: 'http://www.w3.org/2000/svg',
      height: this.size,
      width: this.size,
      class: [
        'loading'
      ]
    }, h('circle', {
      cx: this.halfSize,
      cy: this.halfSize,
      fill: 'none',
      stroke: '#818181',
      'stroke-width': this.$props.width,
      r: this.radius,
      'stroke-dasharray': `${this.c34} ${this.c}`
    }, h('animateTransform', {
      attributeName: 'transform',
      type: 'rotate',
      repeatCount: 'indefinite',
      dur: '.75s',
      values: `0 ${this.halfSize} ${this.halfSize}; 360 ${this.halfSize} ${this.halfSize}`,
      keyTimes: '0; 1'
    })))
  }
})
</script>