import request from 'utils/request'
import { useCookie } from 'utils/cookie'
const cookie = useCookie()

const defaultStatePriceSyncPreview = {
  selected_device: {
    id: null,
    name: null,
    emc_number: [],
    base_price: 0,
    health_rating: 0,
    health_percentage: 0
  },
  available_devices: [],
  price_preview: {
    base_price: null,
    condition_price: [],
    id: null,
    mac_category_id: null,
    name: null,
    specs: {}
  }
}

const defaultStateCompanyPricingSource = {
  country_code: null,
  country_name: null,
  currency: null,
  logo: null,
  status: null
}

const state = () => ({
  current_step: {
    last_register_step: 1,
    is_onboarding: true,
    code_encrypted: null
  },
  syncable_devices: {
    current_page: 0,
    data: [],
    first_page_url: '',
    from: null,
    last_page: 1,
    last_page_url: '',
    next_page_url: null,
    path: '',
    per_page: 0,
    prev_page_url: null,
    to: null,
    total: 0
  },
  pricing_source_companies: [],
  company_pricing_source: { ...defaultStateCompanyPricingSource },
  price_sync: {
    arr_devices: []
  },
  price_sync_preview: { ...defaultStatePriceSyncPreview },
  payout_methods: [],
  popular_payout_methods: [],
  selected_payout_methods: {
    arr_payment_methods: []
  },
  selected_widget_code_email: {
    arr_emails: []
  },
  widget_code: '',
  currency_symbol: null,
  currency: null,
  company_referrer: null,
  company_referrer_detail: {
    logo: null,
    name: null
  }
})
const getters = {
  hasCompanyReferrer(state) {
    return (
      state.company_referrer ||
      (state.company_referrer_detail.logo && state.company_referrer_detail.name)
    )
  }
}
const mutations = {
  SET_SYNCABLE_DEVICES(state, payload) {
    const currentDevices = [...state.syncable_devices.data]
    state.syncable_devices = payload
    state.syncable_devices.data = [...currentDevices, ...payload.data]
  },
  SET_PRICING_SOURCE_COMPANIES(state, payload) {
    state.pricing_source_companies = payload
  },
  SET_PRICE_SYNC_DEVICE(state, payload) {
    state.price_sync.arr_devices.push(payload)
  },
  SET_COMPANY_PRICING_SOURCE(state, payload) {
    state.company_pricing_source = payload
  },
  SET_PAYOUT_METHODS(state, payload) {
    state.payout_methods = payload
  },
  SET_POPULAR_PAYOUT_METHODS(state, payload) {
    state.popular_payout_methods = payload
  },
  SET_SELECTED_PAYOUT_METHODS(state, payload) {
    state.selected_payout_methods.arr_payment_methods = payload
  },
  SET_WIDGET_CODE(state, payload) {
    state.widget_code = payload.source
  },
  SET_CURRENT_STEP(state, payload) {
    state.current_step = payload
  },
  SET_WIDGET_CODE_EMAIL_RECEPIENT(state, emails) {
    state.selected_widget_code_email.arr_emails = emails
  },
  UNSET_WIDGET_CODE_EMAIL_RECEPIENT(state) {
    state.selected_widget_code_email.arr_emails = []
  },
  UNSET_PRICE_SYNC_DEVICE(state) {
    state.company_pricing_source = { ...defaultStateCompanyPricingSource }
  },
  UNSET_PRICE_SYNC(state) {
    state.price_sync = {
      arr_devices: []
    }
  },
  UNSET_PRICING_SOURCE_COMPANIES(state) {
    state.pricing_source_companies = []
  },
  REMOVE_PRICE_SYNC_DEVICE(state, payload) {
    const matchedIndex = state.price_sync.arr_devices.findIndex(
      (product) => product.category_id === payload.category_id
    )
    const isValidIndex = matchedIndex > -1
    if (!isValidIndex) return
    state.price_sync.arr_devices.splice(matchedIndex, 1)
  },
  SET_AVAILABLE_DEVICES_PRICE_SYNC_PREVIEW(state, devices) {
    state.price_sync_preview.available_devices = devices
  },
  SET_SELECTED_DEVICE_PRICE_SYNC_PREVIEW(state, device) {
    state.price_sync_preview.selected_device = device
  },
  SET_DEVICE_PRICE_SYNC_PREVIEW(state, devicePricePreview) {
    state.price_sync_preview.price_preview = devicePricePreview
  },
  UNSET_DEVICE_PRICE_SYNC_PREVIEW(state) {
    state.price_sync_preview = { ...defaultStatePriceSyncPreview }
  },
  SET_CURRENCY(state, payload) {
    state.currency_symbol = payload.symbol
    state.currency = payload.currency
  },
  SET_COMPANY_REFERRER(state, payload = null) {
    state.company_referrer = payload
  },
  SET_COMPANY_REFERRER_DETAIL(state, payload = null) {
    if (!payload) return
    state.company_referrer_detail = payload
  },
  UNSET_COMPANY_REFERRER(state) {
    state.company_referrer = null
    cookie.remove('company-referrer', {
      path: '/onboarding'
    })
  }
}

const actions = {
  async register({ state }, payload) {
    return await request({
      url: `/v5/register`,
      method: 'post',
      data: {
        ...payload,
        source: state.company_referrer ? state.company_referrer : null
      }
    }).then((res) => res).catch(err => {
      return {
        ...err,
        isAlready: err.response.request.response.includes('Email already exist')
      }
    })
  },
  async registerUpdate(_, payload) {
    return await request({
      url: '/v5/register',
      method: 'put',
      data: payload
    })
  },
  async registerUpdateByToken(_, payload) {
    return await request({
      url: '/v5/register/by-token',
      method: 'post',
      data: payload
    })
  },
  async verifyEmail(_, payload = { data: {}, params: {} }) {
    return await request({
      url: '/v5/register/verify-email/check',
      method: 'post',
      data: payload.data,
      params: payload.params
    })
  },
  async getSyncableDevices({ commit, state }, params = {}) {
    if (params?.page <= state.syncable_devices.current_page) return
    const response = await request({
      url: '/v5/register/price-syncs/syncable-devices',
      method: 'get',
      params
    })
    commit('SET_SYNCABLE_DEVICES', response)
  },
  async getPricingSourceCompanies({ commit }, params) {
    const response = await request({
      url: '/v5/register/price-syncs/all-companies',
      method: 'get',
      params: {
        ...params,
        syncable: true
      }
    })
    commit('SET_PRICING_SOURCE_COMPANIES', response)
  },
  async priceSyncs({ state, commit }) {
    await request({
      url: '/v5/register/price-syncs',
      method: 'post',
      data: state.price_sync
    })
    commit('UNSET_PRICE_SYNC_DEVICE')
    commit('UNSET_PRICE_SYNC')
  },
  async activeDevice({ commit }, payload) {
    const response = await request({
      url: '/v5/register/price-syncs/device-active',
      method: 'post',
      data: payload
    })
    commit('SET_COMPANY_PRICING_SOURCE', response)
  },
  async getAvailableDevicesPriceSyncPreview({ commit }, params = {}) {
    const response = await request({
      // url: '/v2/pos/pricing/computer/bulk-pricing',
      url: '/v2/pos/pricing/bulk-pricing',
      params
    })
    const { data: devices } = response
    const [device] = devices
    if (!device) return
    const { child: childrenDevices } = device
    commit('SET_AVAILABLE_DEVICES_PRICE_SYNC_PREVIEW', childrenDevices)
  },
  async devicePriceSyncPreview({ commit }, params) {
    const response = await request({
      // url: '/v2/pos/pricing/computer/bulk-device-preview',
      url: '/v2/pos/pricing/bulk-device-preview',
      method: 'get',
      params
    })
    const { devices: device } = response
    if (!device) return
    const { product: devicePricePreview } = device
    commit('SET_DEVICE_PRICE_SYNC_PREVIEW', devicePricePreview)
  },
  async ejectPriceSync() {
    await request({
      url: '/v5/register/eject',
      method: 'post'
    })
  },
  async showPayoutMethods({ commit }) {
    const response = await request({
      url: '/v5/register/payout-methods',
      method: 'get',
      params: {
        is_onboarding: true
      }
    })
    commit(
      'SET_PAYOUT_METHODS',
      Array.isArray(response.data) ? response.data : []
    )
    commit(
      'SET_POPULAR_PAYOUT_METHODS',
      Array.isArray(response.popular) ? response.popular : []
    )
  },
  async updatePayoutMethods({ state }) {
    await request({
      url: '/v5/register/payout-methods',
      method: 'put',
      data: state.selected_payout_methods
    })
  },
  async widget({ commit }) {
    const response = await request({
      url: '/v5/register/widget',
      method: 'get'
    })
    commit('SET_WIDGET_CODE', response)
  },
  async currentStep({ commit }) {
    const response = await request({
      url: '/v5/register/step',
      method: 'get'
    })
    commit('SET_CURRENT_STEP', response)
  },
  async widgetCodeEmailSend({ state }) {
    await request({
      url: '/v5/register/widget/send-email',
      method: 'post',
      data: state.selected_widget_code_email
    })
  },
  async setCurrency({ commit, state }, payload) {
    commit('SET_CURRENCY', payload)
  },
  async getCompanyReferrerDetail({ state, commit }) {
    const companyReferrerDetail = await request({
      url: '/v5/register/source-company',
      method: 'get',
      params: {
        source: state.company_referrer
      }
    })
    if (Array.isArray(companyReferrerDetail)) {
      commit('UNSET_COMPANY_REFERRER')
      return
    }
    commit('SET_COMPANY_REFERRER_DETAIL', companyReferrerDetail)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
