import request  from 'utils/request'

const state = () => ({
})

const getters = {
}

const mutations = {
}

const actions = {
  async brand({ commit }, payload) {
    const response = await request({
      // url: '/v1/pos/pricing/computer/bulk-pricing',
      url: '/v2/pos/pricing/bulk-pricing',
      method: 'get',
      params: payload,
    })

    commit('pricing/SET_BRAND', response.brand, {
      root: true
    })

    return response.data
  },
  async categoryConditionUpdate({}, payload) {
    return await request({
      url: `/v1/pos/condition/update-term/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async device({ commit }, payload) {
    return await request({
      // url: '/v2/pos/pricing/computer/bulk-device',
      url: '/v2/pos/pricing/bulk-device',
      method: 'get',
      params: {
        product_id: payload.id
      }
    })
  },
  async deviceUpdate({ commit }, payload) {
    return await request({
      // url: '/v1/pos/pricing/computer/set-bulk-pricing',
      url: '/v1/pos/pricing/set-bulk-pricing',
      method: 'post',
      data: payload
    })
  },
  async setBasePrice(_, payload) {
    return await request({
      // url: `/v1/pos/pricing/computer/set-base-pricing`,
      url: `/v1/pos/pricing/set-base-pricing`,
      method: 'post',
      data: payload
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}