import { reactive, computed, ref } from 'vue'
import { useStore } from 'vuex'
import store from 'store'
import { isFunction } from 'utils/is'
import { mapTabRoute } from 'utils/mapping'
import { anchorPrint } from 'utils/element'

export const useCouriers = () => {
	const shippingLabels = computed(() => store.state.shipping.shipping_labels || [])
	const tab = computed(() => {
		let couriers = [
			{
				id: 'all',
				short_name: 'All'
			}
		]

		couriers = couriers.concat(
			shippingLabels.value.map(label => ({
				id: label.toLowerCase(),
				short_name: label
			}))
		)

		return couriers.map(courier =>
			mapTabRoute(
				{
					...courier
				},
				'shippingTab',
				{
					courierId: courier.id
				},
				'id',
				'short_name'
			)
		)
	})

	return {
		tab
	}
}

export const useAll = () => {
	const state = computed(() => store.state.shipping.all)
	const loading = reactive({
		all: false
	})
	const data = reactive({
		page: 1,
		limit: 10,
		search: ''
	})
	const actionAll = async (courierId = null, callback = () => {}) => {
		try {
			loading.all = true
			const payload = Object.assign(
				{},
				{
					...data
				}
			)
			if (courierId !== 'all')
				Object.assign(payload, {
					shipping_courier: courierId
				})
			const response = await store.dispatch('shipping/all', payload)
			isFunction(callback) && callback(response)
		} finally {
			loading.all = false
		}
	}
	return {
		data,
		state,
		loading,
		actionAll
	}
}

export const useLabels = () => {
	const loading = ref(false)
	const state = computed(() => store.state.shipping.shipping_labels)
	const stateAdvanceShippingLabel = computed(() => store.state.shipping.advance_shipping_labels)
	const action = async (callback = () => {}) => {
		try {
			loading.value = true
			await store.dispatch('shipping/shippingLabels')
			isFunction(callback) && callback()
		} finally {
			loading.value = false
		}
	}
	return {
		loading,
		state,
		stateAdvanceShippingLabel,
		action
	}
}

export const useCreate = () => {
	const composeStore = useStore()
	const isMailIn = computed(() => {
		const menus = composeStore.state.user.me.menu_v2.menus
		if (menus) {
			if (menus.find(item => item.name === 'Mail-In')) return true
		}
		return false
	})
	const loading = ref(false)
	const data = reactive({
		box_id: null,
		shipping_address1: null,
		shipping_city: null,
		shipping_firstname: null,
		shipping_lastname: null,
		shipping_phone: null,
		shipping_state: null,
		shipping_suite: '',
		shipping_zip: null,
		shipping_country: null,
		shipping_address_text: null,
		customer_email: null,
		type: null
	})
	const action = async (callback = () => {}) => {
		try {
			loading.value = true
			const response = await store.dispatch('shipping/shippingLabelCreate', data)
			anchorPrint(response)
			isFunction(callback) && callback()
		} finally {
			loading.value = false
		}
	}
	return {
		data,
		loading,
		action
	}
}

export const usePrint = () => {
	const loading = ref(new Map())
	const action = async (id, callback = () => {}) => {
		try {
			loading.value.set(id, true)
			const response = await store.dispatch('shipping/shippingLabelRePrint', id)
			anchorPrint(response)
			isFunction(callback) && callback()
		} finally {
			loading.value.delete(id)
		}
	}
	return {
		loading,
		action
	}
}

