export const useShippingCarrier = () => {
	const loading = reactive({
		fetchShippingCarrier: false
	})
	const shippingCarriers = computed(() => store.state.shipping.shipping_carriers)
	const selectedCarrier = computed(() => store.state.shipping.selected_shipping_carrier)
	const selectCarrier = (carrier = null) => {
		if (!carrier) return
		store.commit('shipping/SET_SELECTED_SHIPPING_CARRIER', carrier)
	}
	const fetchShippingCarrier = async () => {
		try {
			loading.fetchShippingCarrier = true
			await store.dispatch('shipping/shippingLabels')
		} finally {
			loading.fetchShippingCarrier = false
		}
	}
	return {
		loading,
		shippingCarriers,
		selectedCarrier,
		selectCarrier,
		fetchShippingCarrier
	}
}

export const useShippingLabel = () => {
	const shippingLabelCostEnabled = computed({
		set: value => {
			store.commit('shipping/SET_LABEL_SHIPPING_COST', value)
		},
		get: () => store.state.shipping.shipping_label.is_enable_shipping_cost
	})
	const shippingLabelCostVisible = computed({
		set: value => {
			store.commit('shipping/SET_LABEL_SHIPPING_COST_VISIBLE')
		},
		get: () => store.state.shipping.shipping_label.is_shipping_cost_visible
	})

	return {
		shippingLabelCostEnabled,
		shippingLabelCostVisible
	}
}

export const useAdvanceShippingRules = () => {
	const loading = reactive({
		update: false
	})
	const shippingRules = computed(() => store.state.shipping.advance_shipping.rules)
	const shippingCarriers = computed(() => store.state.shipping.advance_shipping.carriers)
	const shippingEnabledStatus = computed({
		set: (value) => {
			store.commit('shipping/SET_ADVANCE_SHIPPING_ENABLED_STATUS', value)
		},
		get: () => store.state.shipping.advance_shipping.is_enable
	})
	const setShippingRules = (rules = []) => {
		store.commit('shipping/SET_ADVANCE_SHIPPING_RULES', rules)
	}
	const unsetShippingRules = () => {
		store.commit('shipping/UNSET_ADVANCE_SHIPPING_RULES')
	}
	const setShippingMultiple = (rules = []) => {
		// store.commit('shipping/SET_ADVANCE_SHIPPING_RULES', rules)
	}
	const actionUpdate = async (payload, callback = () => {}) => {
		try {
			loading.update = true
			await store.dispatch('shipping/updateAdvanceShippingLabel', payload)
			isFunction(callback) && callback(null)
		} catch (err) {
			isFunction(callback) && callback(err)
		} finally {
			loading.update = false
		}
	}
	return {
		loading,
		shippingRules,
		shippingCarriers,
		shippingEnabledStatus,
		setShippingRules,
		setShippingMultiple,
		unsetShippingRules,
		actionUpdate
	}
}

export const useAdvanceShippingMultipleLabel = () => {
	const loading = ref(false)
	const data = reactive({
		payload: {
			is_enable_multiple_label: 0,
			rules: [],
		},
		accordion: false,
		models: [],
		labels: []
	})

	const initData = (params = undefined) => {
		data.payload.is_enable_multiple_label = Number(params.param.is_enable_multiple_label) ?? 0
		data.payload.rules = params.param.shipping_rules_multiple_label
		data.models = params.param.devices
		data.labels = params.shipping_rules_carriers
		if (data.payload.is_enable_multiple_label === 1) {
			data.accordion = true
		} else data.accordion = false
	}

	return {
		loading,
		data,
		initData
	}
}

export const useShippingKitDownload = () => {
	const processing = computed(() => ({
		uploadProgress: store.state.shipping?.processing?.uploadProgress || 0,
		invalid: store.state.shipping?.processing?.invalid || false,
		totalInvalid: store.state.shipping?.processing?.totalInvalid || 0,
		dateInvalid: store.state.shipping?.processing?.dateInvalid || null,
		typeInvalid: store.state.shipping?.processing?.typeInvalid || 'zip',
		downloadProgress: store.state.shipping?.processing?.downloadProgress || 0,
		pathDownload: store.state.shipping?.processing?.pathDownload || null,
		typeDownload: store.state.shipping?.processing?.typeDownload || 'zip',
		loadingDownload: store.state.shipping?.processing?.loadingDownload || false,
		downloadProgressInvalid: store.state.shipping?.processing?.downloadProgressInvalid || 0,
		pathDownloadInvalid: store.state.shipping?.processing?.pathDownloadInvalid || null,
		typeDownloadInvalid: store.state.shipping?.processing?.typeDownloadInvalid || 'zip',
		loadingDownloadInvalid: store.state.shipping?.processing?.loadingDownloadInvalid || false
	}))

  const download = (url) => {
    const parts = url.split('/')
    const name = parts[parts.length - 1] || null
    if (!name) return

    const aux = document.createElement('a')
    aux.href = url
    aux.download = name
    aux.click()

    window.URL.revokeObjectURL(url)
  }
  const closeNotifBasic = () => {
    store.dispatch('notification/closeNotification', 'NotificationShippingKitDownload', { root: true })
    store.commit('shipping/SET_PROCESSING_DOWNLOAD_SHIPPING_KIT_PROGRESS', {
      progress: 0,
      path: null,
      type: 'zip',
      loading: false
    })
  }

	return {
		download,
		closeNotifBasic,
		processing
	}
}
