<script>
import { defineComponent, h, ref, watch } from 'vue'
import {
	mixinPosition,
	mixinBlock,
	mixinDense,
	mixinDisabled,
	mixinType,
	mixinIndeterminate,
	mixinLoading
} from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

import Loading from '../loading'

export default defineComponent({
	name: 'Checkbox',
	mixins: [
		...addMixinPrefix(
			[
				mixinPosition,
				mixinBlock,
				mixinDense,
				mixinDisabled,
				mixinType,
				mixinIndeterminate,
				mixinLoading
			],
			'checkbox'
		)
	],
	props: {
		modelValue: {
			type: [Array, Boolean, String, Number],
			default: () => []
		},
		value: {
			default: true
		},
		valueOff: {
			default: true
		},
		by: {
			type: String,
			default: null
		},
		loading: {
			type: Boolean,
			default: false
		}
	},
	emits: ['update:modelValue', 'input'],
	setup(props, { slots, emit }) {
		const types = ref(['default', 'chip', 'toggle'])
		const value = ref(props.value)
		const genContent = () => {
			// return slots.default()
			return h(
				'div',
				{
					class: 'checkbox__content'
				},
				slots.default()
			)
		}
		const changeValue = $e => {
			const isObject = typeof props.modelValue === 'object' && props.modelValue !== null
			const isBoolean = typeof props.modelValue === 'boolean'
			let temp
			if (isObject) {
				temp = props.modelValue
				if (props.by) {
					const check = props.modelValue.map(item => item[props.by])
					if (check.includes(props.value[props.by]))
						temp = temp.filter(item => item[props.by] !== props.value[props.by])
					else temp.push(props.value)
				} else {
					if (temp.includes(props.value)) temp = temp.filter(item => item !== props.value)
					else temp.push(props.value)
				}
			} else if (isBoolean) temp = !props.modelValue
			else temp = $e.target.checked ? props.value : props.valueOff
			emit('update:modelValue', temp)
			emit('input', temp)
		}

		watch(
			() => props.value,
			() => {
				value.value = props.value
			}
		)

		return {
			types,
			value,
			genContent,
			changeValue
		}
	},
	render() {
		const isChecked =
			typeof this.$props.modelValue === 'object'
				? this.$props.by
					? this.$props.modelValue
							.map(item => item[this.$props.by])
							.includes(this.value[this.$props.by])
					: this.$props.modelValue && this.$props.modelValue.includes(this.value)
				: this.$props.modelValue == this.value

		const children = [
			this.$props.loading && h(Loading),
			this.$slots.default && this.genContent(),
			h('input', {
				name: this.$props.name,
				type: 'checkbox',
				checked: isChecked,
				disabled: this.$props.loading || this.disabled,
				value: this.value,
				onInput: this.changeValue
			}),
			h('span', {
				class: 'checkbox__mark'
			})
		]
		const listClass = [
			'checkbox',
			this.classType === '' ? 'checkbox-default' : this.classType,
			this.classPosition,
			this.classIndeterminate,
			this.classBlock,
			this.classDisabled,
			this.classLoading,
			isChecked ? 'checked' : '',
			isChecked && this.$props.indeterminate ? 'checkbox-active' : ''
		]

		return h(
			'label',
			{
				class: listClass
			},
			children
		)
	}
})
</script>
