import request  from 'utils/request'

const state = () => ({})
const getters = {}
const mutations = {}
const actions = {
  async show(){
    const response = await request({
      url: '/v1/pos/settings/bank-transfer',
      method: 'get'
    })
    return response
  },
  async update(_, payload){
    const response = await request({
      url: '/v1/pos/settings/bank-transfer',
      method: 'put',
      data: payload
    })
    return response
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}