export const setSessionExpiration = (expirationTime) => {
  if(typeof expirationTime === 'number') localStorage.setItem(window.btoa('sessionExpiredAt'), window.btoa(expirationTime))
}

export const getSessionExpiration = () => {
  const sessionExpiredAt = parseInt(window.atob(localStorage.getItem(window.btoa('sessionExpiredAt'))))
  if(sessionExpiredAt ===  NaN) return false
  return sessionExpiredAt
}
export const unsetSession = () => {
  localStorage.removeItem(window.btoa('sessionExpiredAt'))
}