import request from 'utils/request'

const state = () => ({
  loading: {
    connect: false,
    shippingProvider: {
      fetchAll: false,
      updateCarrier: false
    },
    shippingKit: {
      fetch: false,
      update: false
    },
    integrationChecks: false
  },
  connect: {
    credentials: {
      api_key: null
    }
  },
  shippingProvider: {
    carriers: [],
    filters: [],
    selectedCarrier: null,
    selectedCarrierCache: null
  },
  shippingKit: {
    id: null,
    key: null,
    name: null,
    sort_code: [],
    textinfo: null,
    created_at: null,
    updated_at: null,
    subject: null,
    textinfo_order: null
  },
  status: {
    is_ready: 0,
    is_usps_return_actived: false,
    is_usps_return_processing: false
  }
})

const getters = {
  selectedCarrier(state) {
    if (state.shippingProvider.selectedCarrier === null)
      return {
        carrier_code: null,
        carrier_icon: null,
        carrier_name: null,
        carrier_uuid: null,
        service_code: null,
        serivce_name: null,
        required_services: null
      }
    return state.shippingProvider.selectedCarrier
  }
}

const mutations = {
  SET_CREDENTIAL(state, credentials) {
    state.connect.credentials.api_key = credentials.api_key
  },
  SET_CONNECT_LOADING(state, loadingState = false) {
    state.loading.connect = loadingState
  },
  SET_LOADING_FETCH_ALL(state, loadingState = false) {
    state.loading.shippingProvider.fetchAll = loadingState
  },
  SET_LOADING_FETCH_SHIPPING_KIT(state, loadingState = false) {
    state.loading.shippingKit.fetch = loadingState
  },
  SET_LOADING_UPDATE_CARRIER(state, loadingState = false) {
    state.loading.shippingProvider.updateCarrier = loadingState
  },
  SET_LOADING_UPDATE_SHIPPING_KIT(state, loadingState = false) {
    state.loading.shippingKit.update = loadingState
  },
  SET_LOADING_INTEGRATION_CHECKS(state, loadingState = false) {
    state.loading.integrationChecks = loadingState
  },
  SET_CARRIERS(state, data) {
    state.shippingProvider.carriers = data.carriers
  },
  SET_FILTERS(state, filters) {
    state.shippingProvider.filters = filters
  },
  SET_SELECTED_CARRIER(state, carrier = null) {
    if (!carrier) return
    state.shippingProvider.selectedCarrier = carrier
  },
  SET_SELECTED_CARRIER_BY_UUID(state, carrierUUID = null) {
    if (!carrierUUID) return
    const selectedCarrier = state.shippingProvider.carriers.find(
      (carrier) => carrier.carrier_uuid === carrierUUID
    )
    if (!selectedCarrier) return
    state.shippingProvider.selectedCarrier = selectedCarrier
  },
  SET_SELECTED_CARRIER_CACHE_BY_UUID(state, carrierUUID = null) {
    if (!carrierUUID) return
    const selectedCarrier = state.shippingProvider.carriers.find(
      (carrier) => carrier.carrier_uuid === carrierUUID
    )
    if (!selectedCarrier) return
    state.shippingProvider.selectedCarrierCache = selectedCarrier
  },
  SET_SHIPPING_KIT(state, data) {
    state.shippingKit = data
  },
  SET_STATUS(state, data) {
    state.status.is_ready = data?.is_ready
    state.status.is_usps_return_actived = data?.is_usps_return_actived
    state.status.is_usps_return_processing = data?.is_usps_return_processing
  },
  RESET_SELECTED_CARRIER(state) {
    state.shippingProvider.selectedCarrier =
      state.shippingProvider.selectedCarrierCache || null
  }
}

const actions = {
  async connect({ state }) {
    await request({
      url: `/v1/pos/integrations/easypost/connect`,
      method: 'post',
      data: {
        api_key: state.connect.credentials.api_key
      }
    })
  },
  async fetchAll({ commit }) {
    const response = await request({
      url: `/v1/pos/integrations/easypost`,
      method: 'get'
    })
    commit('SET_CREDENTIAL', {
      api_key: response?.account?.easypost_api_key
        ? response.account.easypost_api_key
        : null
    })
    commit('SET_CARRIERS', response)

    if (response.filters) commit('SET_FILTERS', response.filters)

    commit(
      'SET_SELECTED_CARRIER_BY_UUID',
      response.account.selected_carrier_uuid
    )
    commit(
      'SET_SELECTED_CARRIER_CACHE_BY_UUID',
      response.account.selected_carrier_uuid
    )
    commit('SET_STATUS', response)
  },
  async updateCarrier({ state }) {
    if (!state.shippingProvider.selectedCarrier) return
    await request({
      url: `/v1/pos/integrations/easypost`,
      method: 'put',
      data: {
        carrier_name: state.shippingProvider.selectedCarrier.carrier_name,
        carrier_code: state.shippingProvider.selectedCarrier.carrier_code,
        service_code: state.shippingProvider.selectedCarrier.service_code,
        service_name: state.shippingProvider.selectedCarrier.service_name,
        required_services:
          state.shippingProvider.selectedCarrier.required_services
      }
    })
  },
  async fetchShippingKit({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/text-info/easypost-shipping-kit`,
      method: 'get'
    })
    commit('SET_SHIPPING_KIT', response)
  },
  async updateShippingKit({ state }) {
    await request({
      url: `/v1/pos/settings/text-info/easypost-shipping-kit`,
      method: 'put',
      data: state.shippingKit
    })
  },
  async fetchActivationUSPS() {
    const response = await request({
      url: `/v1/pos/integrations/easypost/usps-return`,
      method: 'get'
    })
    return response
  },
  async createActivationUSPS({}, payload) {
    const response = await request({
      url: `/v1/pos/integrations/easypost/usps-return`,
      method: 'post',
      data: payload
    })
    return response
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
