import request from 'utils/request'
import moment from 'moment'

const state = () => ({
  all: [],
  show: {
    data: null
  },
  devices: [],
  selected_more_device: {
    group_name: '',
    devices: []
  },
  is_available_device: true,
  price_sync_company_source: null,
  source: {
    companies: [],
    conditions: [],
    conditionsV2: []
  }
})

const getters = {}

const mutations = {
  SET_ALL(state, payload) {
    state.all = payload
  },
  SET_PRICE_SYNC_COMPANY_SOURCE(state, companySource=null){
    if(!companySource) return
    state.price_sync_company_source = companySource
  },
  SET_SHOW(state, payload) {
    state.show.data = payload
  },
  SET_DEVICES(state, payload) {
    state.devices = payload
  },
  SET_MORE_DEVICE(state, payload) {
    state.selected_more_device.group_name = payload.group_name
    state.selected_more_device.devices = payload.devices || []
  },
  SET_DEVICE_AVAILABILITY(state, payload) {
    state.is_available_device = payload.is_device_available
  },
  SET_SOURCE_COMPANY(state, payload) {
    state.source.companies = payload
  },
  SET_SOURCE_CONDITIONS(state, payload) {
    state.source.conditions = payload
  },
  SET_SOURCE_CONDITIONSV2(state, payload) {
    if (state.source.conditionsV2.length) {
      state.source.conditionsV2.push(payload)
    } else state.source.conditionsV2 = [payload]
  }
}

const actions = {
  async all({ commit }, payload) {
    const newPayload = { ...payload }
    if (newPayload.cancelToken) delete newPayload.cancelToken

    const response = await request({
      url: '/v2/pos/condition/categories',
      method: 'get',
      params: newPayload,
      cancelToken: payload.cancelToken
    })
    commit(
      'SET_ALL',
      (response && response.data) || []
    )

    return response
  },
  async create({}, payload) {
    return await request({
      url: `/v1/pos/condition/add-category`,
      method: 'post',
      data: payload
    })
  },
  async update({}, payload) {
    return await request({
      url: `/v1/pos/condition/edit-category/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async delete({}, payload) {
    return await request({
      url: `/v1/pos/condition/delete-category/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async show({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/condition/edit-category/${payload.id}`,
      method: 'get',
      params: payload.params
    })

    commit('SET_SHOW', response)
    commit('SET_PRICE_SYNC_COMPANY_SOURCE', response.price_sync_source)

    return response
  },
  async devices({ commit }) {
    const response = await request({
      url: '/v1/pos/condition/devices',
      method: 'get'
    })

    commit('SET_DEVICES', response)
    return response
  },
  async conditionCreate({}, payload) {
    const response = await request({
      url: `/v1/pos/condition/add/${payload.id}`,
      method: 'post',
      data: payload.data
    })
    return response
  },
  async conditionUpdate({}, payload) {
    return await request({
      url: `/v1/pos/condition/edit/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async conditionDelete({}, payload) {
    return await request({
      url: `/v1/pos/condition/delete/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async sort({}, payload) {
    return await request({
      url: `/v1/pos/condition/sort`,
      method: 'post',
      data: payload
    })
  },
  async moreDevice({ commit }, payload={}) {
    const response = await request({
      url: `/v1/pos/condition/all-devices/${payload.id}`,
      method: 'get',
      params: payload.params
    })
    commit('SET_MORE_DEVICE', response)
  },
  async availabilityCheck({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/condition/check-available-device`,
      method: 'get',
      cancelToken: payload.cancelToken
    })
    commit('SET_DEVICE_AVAILABILITY', response)
  },
  async availabilityCheckWidget({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/condition/check-available-device-widget`,
      method: 'get',
      cancelToken: payload.cancelToken
    })
    commit('SET_DEVICE_AVAILABILITY', response)
  },
  async availabilityCheckDeviceCondition() {
    return await request({
      url: '/v1/pos/pricing/check-available-device-condition',
      method: 'get'
    })
  },
  async showSourceCompanies({ commit }, payload) {
    payload = Array.isArray(payload) ? [...payload]?.shift() : payload
    const response = await request({
      url: `/v1/pos/pricing/syncable-sources`, //`/v1/pos/settings/get-all-companies`,
      method: 'get', //post
      params: {
        category_id: payload.category_id,
        logo: true
      }
      // data: {
      //   category_id: payload.category_id,
      //   is_mac: payload.is_mac,
      //   syncable: 1
      // }
      // cancelToken: payload.cancelToken
    })
    commit('SET_SOURCE_COMPANY', response)
    return response || []
  },
  async showSourceConditions({ state, commit }, payload) {
    const response = await request({
      url: `/v1/pos/pricing/pricesync-conditions`,
      method: 'get',
      params: payload.data
    })
    const { conditions, name } = response || {}
    const newCondition = conditions?.map(condition => ({
      ...condition,
      group_name: name,
      sub_domain: payload?.data?.sub_domain || payload?.sub_domain
    }))

    return newCondition || []
  },
  async showSourceManualConditions({ commit }, payload) {
    const response = await request({
      url: `/v2/pos/pricing/bulk-pricing`,
      method: 'get',
      params: payload
      // cancelToken: payload.cancelToken
    })
    const { data } = response
    const newCondition = data?.find(item => item.id == payload.category_id)?.conditions?.map(condition => ({
      ...condition,
      // group_id,
      // group_name
    }))
    commit('SET_SOURCE_CONDITIONS', newCondition)
    return newCondition || []
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
