export default (prefix = null) => ({
  props: {
    type: {
      type: String,
      default: 'filled'
    }
  },
  computed: {
    isTypeExist() {
      return this.types.includes(this.type)
    },
    classType() {
      return this.isTypeExist ? `${prefix + '-' || ''}${this.type}` : ''
    }
  }
})