import request from 'utils/request'

const state = () => ({
  code: {
  },
  claimed: false,
  subAccounts: [],
  pipelines: [],
  isUninstalled: false
})

const getters = {}

const mutations = {
  SET_CODE(state, payload) {
    state.code = { ...payload }
  },
  SET_CLAIMED(state, payload) {
    state.claimed = payload
  },
  SET_SUB_ACCOUNTS(state, payload) {
    state.subAccounts = payload
  },
  SET_PIPELINES(state, payload) {
    state.pipelines = payload
  },
  SET_UNINSTALL(state, payload) {
    state.isUninstalled = payload
  }
}

const actions = {
  setCode({ commit, state }) { /* await request() */ },
  async setClaimed({ commit, state }, payload) {
    return await request({
      url: `/v1/pos/integrations/ghl/token-claims`,
      method: 'post',
      data: payload
    }).then((response) => {
      commit('SET_CLAIMED', true)
      commit('SET_CODE', {
        ...state.code,
        claims: payload.code
      })

      return response
    }).catch((error) => {
      const newCode = { ...state.code }
      !!newCode.claims && delete newCode.claims

      commit('SET_CLAIMED', false)
      commit('SET_CODE', newCode)
      return error
    })
  },
  async setSubAccounts({ commit, state }) {
    return await request({
      url: `/v1/pos/integrations/ghl/sub-accounts`,
      method: 'get'
    }).then((response) => {
      commit('SET_SUB_ACCOUNTS', response.locations)
      commit('SET_CODE', {
        ...state.code,
        sub_account: response.traceId
      })

      return state.subAccounts
    }).catch(() => {
      const newCode = { ...state.code }
      !!newCode.sub_account && delete newCode.sub_account

      commit('SET_SUB_ACCOUNTS', [])
      commit('SET_CODE', newCode)
      return state.subAccounts
    })
  },
  async setPipelines({ commit, state }, payload) {
    return await request({
      url: `/v1/pos/integrations/ghl/pipelines`,
      method: 'get',
      params: { location_id: payload.location_id }
    }).then((response) => {
      commit('SET_PIPELINES', response.pipelines)
      commit('SET_CODE', {
        ...state.code,
        pipeline: response.traceId
      })
      return state.pipelines
    }).catch((error) => {
      const newCode = { ...state.code }
      !!newCode.pipeline && delete newCode.pipeline

      commit('SET_PIPELINES', [])
      commit('SET_CODE', newCode)
      return state.pipelines
    })
  },
  async init() {
    return await request({
      url: `/v1/pos/integrations/ghl`,
      method: 'get',
    }).then((response) => {
      // commit(, response)

      return response
    }).catch((error) => {
      // commit(, [])

      return error
    })
  },
  async updateGHL({}, payload) {
    return await request({
      url: `/v1/pos/integrations/ghl`,
      method: 'put',
      data: payload
    }).then((response) => response
    ).catch((error) => error)
  },
  async setUninstall({ commit }, payload) {
    commit('SET_UNINSTALL', payload)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
