// store/modules/alert.ts

const STORAGE_KEY = 'alerts_status'

const loadFromStorage = () => {
  try {
    const raw = localStorage.getItem(STORAGE_KEY)
    return raw ? JSON.parse(raw) : {}
  } catch {
    return {}
  }
}

const saveToStorage = (data) => {
  localStorage.setItem(STORAGE_KEY, JSON.stringify(data))
}

export default {
  namespaced: true,

  state: () => ({
    alerts: loadFromStorage() // { pricing: { invalid_price_upload: true } }
  }),

  mutations: {
    SET_ALERT(state, { module, id, value }) {
      if (!state.alerts[module]) {
        state.alerts[module] = {}
      }
      state.alerts[module][id] = value
      saveToStorage(state.alerts)
    },
    RESET_ALERTS(state) {
      state.alerts = {}
      localStorage.removeItem(STORAGE_KEY)
    }
  },

  actions: {
    triggerAlert({ commit }, { module, id }) {
      commit('SET_ALERT', { module, id, value: true })
    },
    dismissAlert({ commit }, { module, id }) {
      commit('SET_ALERT', { module, id, value: false })
    }
  },

  getters: {
    isAlertActive: (state) => (module = undefined, id = undefined) => {
      if (!module && !id) return state

      if (!module) return state.alerts

      if (!id) return state.alerts[module]

      return state.alerts[module] && state.alerts[module][id]
    }
  }
}
