import request from 'utils/request'
import local from './buyback/local'
import online from './buyback/online'
import invoice from './buyback/invoice'
import bulkQuote from './buyback/bulk-quote'

const state = () => ({
  show: {
    changelog: []
  },
  order_condition: [],
  orders_to_reloads: undefined
})

const mutations = {
  SET_SHOW_CHANGELOG(state, payload) {
    state.show.changelog = payload
  },
  SET_SHOW_ORDER_CONDITION(state, payload) {
    state.order_condition = payload
  },
  SET_SHOW_ORDER_TO_RELOADS(state, payload) {
    state.orders_to_reloads = payload
  },
  RESET_SHOW_ORDER_TO_RELOADS(state) {
    state.orders_to_reloads = undefined
  }
}

const actions = {
  async statuses({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/order/order-status',
      method: 'get',
      params: payload.params
    })

    commit(`${payload.buyback}/SET_STATUSES`, response)
  },
  async statusesOption({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/order/order-status',
      method: 'get',
      params: {
        type: 'option',
        ...payload.params
      }
    })

    commit(`${payload.buyback === 'easy-offer' ? 'online' : payload.buyback}/SET_OPTION_STATUSES`, response)
  },
  async printPaperCheck({ commit }, payload) {
    return await request({
      url: `/v1/pos/print/check/${payload}`,
      method: 'get',
      responseType: 'arraybuffer'
    })
  },
  async printReceipt({ commit }, payload) {
    const newPayload = typeof payload === 'object' ? { ...payload } : undefined

    if (newPayload) delete newPayload.id

    return await request({
      url: `/v1/pos/local-order/receipt/${payload.id || payload}`,
      method: 'get',
      params: newPayload,
      responseType: 'arraybuffer'
    })
  },
  async exportPaypalMass({ commit }, payload) {
    return await request({
      url: '/v1/pos/order/paypal-mass',
      method: 'get',
      params: payload,
      responseType: 'blob'
    })
  },
  async exportCSV({ commit }, params) {
    return await request({
      url: `v1/pos/order/generate-csv`,
      method: 'get',
      params,
      responseType: 'blob'
    })
  },
	async exportCSVTremendous({ commit }, params) {
		return await request({
			url: `/v1/pos/order/tremendous-failures/download`,
			method: 'get',
			params
		})
	},
  async exportCSVDOTS({ commit }, params) {
    return await request({
      url: `/v1/pos/order/masspay`,
      method: 'get',
      params,
      responseType: 'blob'
    })
  },
  async showBankInfo({ commit }, payload) {
    return await request({
      url: `/v1/pos/show-bank`,
      method: 'POST',
      data: {
        id: payload,
        type: 'order'
      }
    })
  },
  async showCheckSettings({}) {
    return await request({
      url: '/v1/pos/settings/check',
      method: 'get'
    })
  },
  async showChangelog({ commit }, payload) {
    const newPayload = typeof payload === 'object' ? { ...payload } : undefined

    if (newPayload) delete newPayload.id

    const response = await request({
      url: `/v1/pos/order/change-log/${payload.id || payload}`,
      method: 'get',
      params: newPayload
    })
    commit('SET_SHOW_CHANGELOG', response)
  },
  async showOrderCondition({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/order/condition-price`,
      method: 'post',
      params: payload
    })
    commit('SET_SHOW_ORDER_CONDITION', response)
  },
  async hardDeleteOrder(_, payload) {
    await request({
      url: `/v1/pos/order/hardDelete/${payload.id}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async bulkImeiAddDevice({}, payload) {
    return await request({
      url: `v1/pos/order/bulk-imei/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async whiteLabelPrintShippingKit({}, payload) {
    const newPayload = { ...payload }
    if (newPayload.box_id) delete newPayload.box_id

    return await request({
      url: `v1/shipping/kit/print/${payload.box_id}`,
      method: 'get',
      headers: {
        Accept: 'application/pdf'
      },
      responseType: 'arraybuffer',
      params: {
        ...newPayload
        // ...(payload.company_token && { company_token: payload.company_token }),
        // ...(payload.token && { token: payload.token })
      }
    })
  },
  async whiteLabelPrintShippingQRCode({}, payload) {
    const newPayload = { ...payload }
    if (newPayload.box_id) delete newPayload.box_id

    return await request({
      url: `v1/shipping/qr-code/${payload.box_id}`,
      method: 'get',
      headers: {
        'Content-Type': 'image/png'
      },
      responseType: 'blob',
      params: {
        ...newPayload
        // ...(payload.company_token && { company_token: payload.company_token }),
        // ...(payload.token && { token: payload.token })
      }
    })
  },
  async applyCoupon(_, payload) {
    return await request({
      url: `/v1/pos/order/coupon/${payload.box_id}`,
      method: 'post',
      data: payload.data
    })
  },
  async removeCoupon(_, payload) {
    return await request({
      url: `/v1/pos/order/coupon/${payload.box_id}`,
      method: 'delete'
    })
  },
  async getGolfSpecs(_, payload){
    return await request({
      url: '/v1/pos/order/golf-option',
      method: 'post',
      data: payload
    })
  },
  async regenerateShipping(_, payload) {
    return await request({
      url: `/v1/pos/order/regenerate-shipping`,
      method: 'post',
      data: {
        box_id: payload.box_id
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  modules: {
    local,
    online,
    invoice,
    'bulk-quote': bulkQuote
  }
}
