import request from 'utils/request'

const state = () => ({
  show: [],
  update: '',
  updateStatus: '',
})

const getters = {}

const mutations = {
  SET_SHOW(state, response) {
    state.show = response
  },
  SET_UPDATE(state, response) {
    state.update = response
  },
  SET_UPDATE_STATUS(state, response) {
    state.updateStatus = response
  }
}

const actions = {
  async show({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/review-email`,
      method: 'get'
    })

    commit('SET_SHOW', response)
  },
  async update({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/review-email`,
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload
      }
    })

    commit('SET_UPDATE', response)
  },
  async updateStatus({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/review-email/status`,
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload
      }
    })

    commit('SET_UPDATE_STATUS', response)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}