export const loadCustomCSS = () => {
  const shade = (color, name, once = false) => {
		const _shade = (color, amount) => {
			return (
				'#' +
				color
					.replace(/^#/, '')
					.replace(/../g, color =>
						('0' + Math.min(255, Math.max(0, parseInt(color, 16) + amount)).toString(16)).substr(-2)
					)
			)
		}

    if (!once) {
      return (
        `--color-${name}-01: ${_shade(color, 200)};` +
        `--color-${name}-02: ${_shade(color, 100)};` +
        `--color-${name}-02: ${_shade(color, 75)};` +
        `--color-${name}-03: ${_shade(color, 50)};` +
        `--color-${name}-04: ${_shade(color, 0)};` +
        `--color-${name}-1: ${_shade(color, 200)};` +
        `--color-${name}-2: ${_shade(color, 100)};` +
        `--color-${name}-2: ${_shade(color, 75)};` +
        `--color-${name}-3: ${_shade(color, 50)};` +
        `--color-${name}-4: ${_shade(color, 0)};` +
        `--color-${name}-base: ${_shade(color, -25)};` +
        `--color-${name}-darken: ${_shade(color, -50)};` +
        `--color-${name}-lighten: ${_shade(color, -75)};`
      )
    }
    return `--color-${name}: ${_shade(color, 0)};`
	}

	const themeColor = (color) => {
		// convert to rgb
		const converted = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(color)
		const rgb = converted
			? {
					r: parseInt(converted[1], 16),
					g: parseInt(converted[2], 16),
					b: parseInt(converted[3], 16)
			  }
			: null

		const hsp = rgb
			? Math.sqrt(0.299 * (rgb.r * rgb.r) + 0.587 * (rgb.g * rgb.g) + 0.114 * (rgb.b * rgb.b))
			: 127.6

		if (hsp > 127.5) {
			return 'light'
		} else return 'dark'
	}

	return {
		shade,
		themeColor
	}
}

export const observeMutation = () => {
  if (!window.isWhiteLabel) return
  const targetNode = document.body

  const callback = (mutations) => {
    mutations.forEach(mutation => {
      switch (mutation.type) {
        case "childList":
          const targets = mutation.target.querySelectorAll('[whitelabel]')
          if (targets && targets.length) {
            targets.forEach(target => {
              switch (target.getAttribute('whitelabel')) {
                case 'remove':
                  target.remove();
                  break;
                case 'image-replace':
                  if (target.dataset.imageType) {
                    target.src = `${content[window.whiteLabel.company][target.dataset.imageType]}`;
                  }
                  break;
                case 'text-replace':
                  const dataText = target.dataset.label.replaceAll(' ', '_') || undefined;
                  const label = {
                    to: content[window.whiteLabel.company].text[dataText] || undefined,
                    from: content.default.text[dataText] || undefined
                  }
                  const text = target.innerText
                  if (label.to && label.from && text) {
                    const newText = text.replace(label.from, label.to);
                    target.innerText = newText;
                  }
                  if (target.dataset.url) {
                    target.href = content[window.whiteLabel.company][target.dataset.url]
                  }
                  break;
              }
            });
          }
        break;
      }
    })
  }

  const observer = new MutationObserver(callback)

  observer.observe(targetNode, {
    attributeFilter: [],
    attributeOldValue: true,
    attributes: true,
    characterData: true,
    characterDataOldValue: true,
    childList: true,
    subtree: true,
  })
}

export const injectTheme = (config = {
  'teal': '#424242',
  'brand-base': '#424242'
}) => {
  if (!window.isWhiteLabel) return
  /* For Customize Color */
  const customCSS = document.createElement('style')
  customCSS.setAttribute('id', 'reusely-style-var')
  const loaderCSS = loadCustomCSS()
  let generatedColor = ''

  for (const index in config) {
    generatedColor += loaderCSS.shade(config?.[index], index, index === 'brand-base')
  }
  customCSS.innerHTML = `
    @layer base {
      :root {
        ${generatedColor}
        $--checkbox-base-mark-check-bg: ${config.teal};
      }
    }
  `
  const parentEl = document.body
  parentEl.appendChild(customCSS)
  /* For Customize Color */
}

const getWhiteLabelEndpointBaseUrl = async () => {
  const defaultAPIBaseUrl = new URL(import.meta.env.VITE_BASE_URL_API)
  const defaultAPIOrigin = defaultAPIBaseUrl.origin
  try {
    const response = await fetch([defaultAPIOrigin, 'api/v2/domain'].join('/'), {
      credentials: 'include'
    })
    return (await response.json()).domain || null
  } catch (err) {
    console.error(`Failed fetching white-label API endpoint: `, err)
    return null
  }
}

export const whiteLabelConfig = async () => {
  window.isWhiteLabel = false
  window.whiteLabel = whiteLabels.find((item) =>
    window.location.host.includes(item.company)
  )
  if (window.whiteLabel) {
    window.isWhiteLabel = true
    window.localStorage.setItem('white-label', true)
    const whiteLabelEndpointBaseUrl = await getWhiteLabelEndpointBaseUrl()
    if (whiteLabelEndpointBaseUrl)
      window.API_WHITELABEL_BASEURL = [whiteLabelEndpointBaseUrl, 'api'].join(
        '/'
      )
    else window.API_WHITELABEL_BASEURL = import.meta.env.VITE_BASE_URL_API
  } else window.localStorage.setItem('white-label', false)

  generateFavicons(
    (window.whiteLabel && {
      company_name: window.whiteLabel.name,
      company_username: window.whiteLabel.company
    }) ||
    undefined
  )
}
