import request  from 'utils/request'

const state = () => ({
  show: {
    receipt: {}
  }
})

const getters = {
}

const mutations = {
  SET_SHOW_RECEIPT(state, payload){
    state.show.receipt = payload
  }
}

const actions = {
  async showReceipt({commit}, payload) {
    const response = await request({
      url: `/v1/pos/settings/receipt-text`,
      method: 'get'
    })
    commit('SET_SHOW_RECEIPT', response)
    return response
  },
  async showReceiptUpdate({}, payload) {
    return await request({
      url: `/v1/pos/settings/update`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}