<script>
import { defineComponent, h, ref, watch } from 'vue'
import {
  mixinBlock,
  mixinDense,
  mixinDisabled,
  mixinIcon,
  mixinPosition,
  mixinView
} from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'
import { money, toNumber } from '../../../../src/utils/number'
import { currencies, currencyByCountries } from '../../../../src/constants/currency'

export default defineComponent({
    name: "Input",
    mixins: [
      ...addMixinPrefix([
        mixinBlock,
        mixinDisabled,
        mixinView('default'),
        mixinPosition,
        mixinIcon,
        mixinDense
      ], "input")
    ],
    props: ['label', 'placeholder', 'type', 'modelValue', 'required', 'value', 'loading', 'readonly', 'min', 'max', 'maxlength', 'autocomplete', 'rows', 'disallowCopy', 'rtl', 'clearable', 'clearableFixed', 'appendFlex', 'prependFlex', 'currency'],
    emits: ['update:modelValue', 'focusIn', 'focusOut', 'icon-click', 'clear'],

    setup(props, { slots, emit }) {
        const currencyCode = window.COMPANY_CURRENCY
        const countryCode = window.COMPANY_COUNTRY_CODE
        const currencyRules =
          [...currencyByCountries].find(
            (item) => item.country === countryCode
          ) ||
          [...currencies].find((item) => item.currency === currencyCode) ||
          [...currencies].find((item) => item.currency === 'ALL')
        const values = ref(
          props.currency
            ? {
              format: money(props.modelValue || props.value, {
                currency: currencyCode,
                country: countryCode
              }, 0),
              origin: props.modelValue || props.value,
              rules: currencyRules
            }
            : props.modelValue || props.value
        )
        const uid = Math.floor(Math.random() * 1000000)
        const views = ref(['default', 'success', 'warning', 'danger'])
        const refInput = ref(null)

        const labelSlot = () => {
          return slots.label && slots.label()
        }
        const iconSlot = () => {
          return slots.icon && slots.icon()
        }
        const prependSlot = () => {
          return slots.prepend && slots.prepend()
        }
        const appendSlot = () => {
          return slots.append && slots.append()
        }
        const withIcon = ref(false)
        const onKeyup = $event => {
          if(props.type === 'number' && $event.key !== 'Backspace'){
            if(Number($event.target.value) > Number(props.max)) $event.target.value = props.max
            if(Number($event.target.value) < Number(props.min)) $event.target.value = props.min
          }

          if(props.min === '0' && props.currency) {
              $event.target.value = Math.abs($event.target.value)
          }

          if (props.currency) {
              values.value.origin = toNumber({ rule: values.value.rules, value: $event.target.value })
              values.value.format = money(values.value.origin, {
                currency: currencyCode,
                country: countryCode
              }, 0)
              return emit('update:modelValue', values.value.origin)
          }

          emit('update:modelValue', $event.target.value)
        }
        const onKeydown = $event => {
            if(props.type === 'number' && $event.key !== 'Backspace'){
              if(Number($event.target.value) > Number(props.max)) $event.target.value = props.max
              if(Number($event.target.value) < Number(props.min)) $event.target.value = props.min
            }

            if (!props.currency) return

            let number = toNumber({ rule: values.value.rules, value: $event.target.value })
            const adder = () => {
                if (number % 1 === 0) return 1

                return 0.01
            }
            if ($event.key === 'ArrowUp') {
                number = number + adder()
            } else if ($event.key === 'ArrowDown') number = number - adder()

            values.value.origin = number
            values.value.format = money(values.value.origin, {
              currency: currencyCode,
              country: countryCode
            }, 0)

            return emit('update:modelValue', values.value.origin)
        }
        const onFocusIn = $event => {
            emit('focusIn', $event)
        }
        const onFocusOut = $event => {
            setTimeout(() => emit('focusOut', $event), 200)
        }
        const focus =  $event => {
            refInput.value.focus()
        }
        const blur = () => refInput.value.blur()
        const onIconClick = () => emit('icon-click')
        const onClear = () => {
            refInput.value.value = ''
            emit('clear', true)
            emit('update:modelValue', '')
        }

        watch(() => props.modelValue || props.value, (newValue) => {
          if(props.currency){
            values.value.format = money(newValue, {
              currency: currencyCode,
              country: countryCode
            }, 0)
            values.value.origin = newValue
          }
        })

        return {
          uid,
          values,
          labelSlot,
          iconSlot,
          prependSlot,
          appendSlot,
          withIcon,
          onKeyup,
          onKeydown,
          onFocusIn,
          onFocusOut,
          onIconClick,
          onClear,
          views,
          focus,
          blur,
          refInput
        }
    },


    render() {
        const isIconExist = this.$slots.icon
        const isWithIcon = this.icon
        let classIcon = isWithIcon && !isIconExist ? '' : this.classIcon
        const listClass = [
          "input",
          this.classBlock,
          this.classDisabled,
          this.classView,
          classIcon,
          this.classIconBlock,
          this.classPosition,
          this.classDense
        ]

        const inputLabel = ({placeholder, classes, uid, label, slotLabel}) => {
            return h("label", {
                placeholder: placeholder || '',
                class: ["input__label", classes],
                for: uid,
            }, slotLabel || label)
        }
        const inputWrapper = ({childs}) => {
            return h("div", {
                class: 'input__wrapper'
            }, childs)
        }
        const inputInstance = ({uid, disabled, placeholder, type, required, value, readonly, min, max, maxlength, autocomplete, rows, disallowCopy}) => {
            const isRequired = required !== undefined
            const isReadonly = readonly !== undefined
            const isDisallowCopy = disallowCopy !== undefined

            return h(type === 'textarea' ? 'textarea' : 'input', {
                id: uid,
                disabled: disabled,
                placeholder: placeholder || '',
                type: type || 'text',
                onInput: this.onKeyup,
                onKeydown: this.onKeydown,
                onFocus: this.onFocusIn,
                onBlur: this.onFocusOut,
                onPaste: isDisallowCopy ? (ev) => ev.preventDefault() : null,
                onWheel: (ev) => ev.target.blur(),
                required: isRequired && true || false,
                readonly: isReadonly && true || false,
                value: value,
                min: min,
                max: max,
                maxlength: maxlength,
                autocomplete: autocomplete || 'false',
                rows: rows || null,
                ref: 'refInput',
                class: [
                    type == 'number' || this.$props.rtl || this.$props.currency ? ['text-right'] : [],
                    this.$slots.prepend && `input--prepend`,
                    this.$slots.append && `input--append`
                ]
            })
        }
        const inputIcon = ({childs}) => {
            return h('div', { class: ['input__icon'], onClick: this.onIconClick }, childs)
        }
        const inputPrepend = ({childs}) => {
            return h('div', { class: ['input__prepend', this.$props.prependFlex !== undefined ? 'input__prepend--flex' : ''], onClick: this.onIconClick }, childs)
        }
        const inputAppend = ({childs}) => {
            return h('div', { class: ['input__append', this.$props.appendFlex !== undefined ? 'input__append--flex' : ''], onClick: this.onIconClick }, childs)
        }
        const inputClear = () => {
            return h('div', {class: 'input__clear',  onClick: this.onClear}, '')
        }

        let input = inputInstance({
            uid: this.uid,
            disabled: this.$props.disabled,
            placeholder: this.$props.placeholder,
            type: this.$props.currency ? 'text' : this.$props.type,
            required: this.$props.required,
            readonly: this.$props.readonly,
            value: this.$props.currency
                ? this.values.format
                : (this.$props.modelValue || this.$props.modelValue === 0 ? this.$props.modelValue : this.$props.value),
            min: this.$props.min,
            max: this.$props.max,
            maxlength: this.$props.maxlength,
            autocomplete: this.$props.autocomplete,
            rows: this.$props.rows,
            disallowCopy: this.$props.disallowCopy
        })
        let withIcon = inputIcon({childs: [this.iconSlot()]})
        const withPrepend = inputPrepend({ childs: [this.prependSlot()] })
        const withAppend = inputAppend({ childs: [this.appendSlot()] })
        const clearable = inputClear()
        const isNotEmptyValue = this.$props.modelValue || this.$props.value
        const children = [
            (this.$props.label || this.$slots.label) && inputLabel({
              placeholder: this.$props.placeholder,
              classes: '' /*this.classType*/,
              uid: this.uid,
              label: this.$props.label,
              slotLabel: this.$slots.label && this.$slots.label()
            }),
            inputWrapper({
                childs: [ 
                  this.$slots.prepend && withPrepend, 
                  input, 
                  this.$slots.append && withAppend, 
                  withIcon,
                  (this.$props.clearable && !this.$props.disabled)
                  ? this.$props.clearableFixed
                    ? clearable
                    : isNotEmptyValue
                    ? clearable
                    : ''
                  : ''
                ]
            })

        ];

        return h("div", { class: ['input__container', listClass] }, children);
    },
});
</script>