import { defineStore } from 'pinia'
import { phoneNumber } from 'utils/number'
import request from 'utils/request'
import { ElMessage } from 'element-plus'

export const useSetting = defineStore('setting', {
  state: () => ({
    menu: {
      main: [],
      setting: [],
      iconsSetting: [
        {
          key: 57,
          icon: '/icon/settings-icon/locations.svg'
        },
        {
          key: 56,
          icon: '/icon/settings-icon/users.svg'
        },
        {
          key: 58,
          icon: '/icon/settings-icon/billing.svg'
        },
        {
          key: 59,
          icon: '/icon/settings-icon/buyback-devices.svg'
        },
        {
          key: 60,
          icon: '/icon/settings-icon/device-conditions.svg'
        },
        {
          key: 72,
          icon: '/icon/settings-icon/pricing_editor.svg'
        },
        {
          key: 62,
          icon: '/icon/settings-icon/payouts.svg'
        },
        {
          key: 69,
          icon: '/icon/settings-icon/widget.svg'
        },
        {
          key: 85,
          icon: '/icon/settings-icon/shipping.svg'
        },
        {
          key: 87,
          icon: '/icon/settings-icon/device-workflow.svg'
        },
        {
          key: 82,
          icon: '/icon/settings-icon/email-notifications.svg'
        },
        {
          key: 93,
          icon: '/icon/settings-icon/inventory.svg'
        },
        {
          key: 98,
          icon: '/icon/settings-icon/order-settings.svg'
        },
        {
          key: 71,
          icon: '/icon/settings-icon/widget.svg'
        },
        {
          key: 101,
          icon: '/icon/settings-icon/customer-portal.svg'
        },
        {
          key: 102,
          icon: '/icon/settings-icon/marketplace.svg'
        },
        {
          key: 104,
          icon: '/icon/settings-icon/device-conditions.svg'
        },
        {
          key: 106,
          icon: '/icon/settings-icon/offers.svg'
        }
      ],
      disabledMessages: {
        settingBulkPriceEditor:
          'You must activate at least one item in your Catalog settings before you can access the Price Editor.'
      }
    },
    company: {
      isLoading: false,
      name: null,
      email: null,
      address: null,
      phone: null,
      logo: null,
      form: null,
      refresh: true
    },
    billing: {
      isLoading: false,
      result: null
    },
    countries: {
      isLoading: false,
      result: null
    },
    currencies: {
      isLoading: false,
      result: null
    },
    timezone: {
      isLoading: false,
      result: null
    },
    distance: {
      isLoading: false,
      result: null
    }
  }),
  getters: {},
  actions: {
    setFormData(payload, logo) {
      let formData = new FormData()
      Object.entries(payload).forEach(item => {
        if(logo && item[0] === 'logo' && item[1] === null) {
          formData.append(item[0], 0)
          return
        }
        if(item[1] !== null) {
          if(item[0] !== 'logo' || (item[0] === 'logo' && typeof item[1] === 'object')) formData.append(item[0], item[1])
        }
      })

      return formData
    },
    setMenu(data) {
      this.menu.main = data.menus
      this.menu.setting = data.setting.map(i => ({
        ...i,
        child: i.child.map(j => {
          return ({
            ...j,
            tag: j.is_disabled == 1 ? 'a' : 'router-link',
            icon: this.menu.iconsSetting.find(k => k.key == j.id)?.icon || ''
          })
        })
      }))
    },
    async $getSettingCompanyProfile(payload) {
      if (!this.company.refresh) return

      this.company.isLoading = true
      try {
        const response = await request({
          url: '/v1/config/company',
          params: payload,
          method: 'get'
        })
        this.company.name = response.name
        this.company.email = response.company_email
        this.company.address = response.address_text
        this.company.phone = phoneNumber(response.phone)
        this.company.logo = response.logo
        this.company.form = response
        this.company.refresh = false
      } catch (error) {
        console.error('Failed to fetch company configuration', error)
      } finally {
        this.company.isLoading = false
      }
    },
    async $getUrlBilling() {
      const response = await request({
        url: `/v1/pos/billing/customer/portal`,
        method: 'get'
      })
      this.billing.result = response
    },
    async $updateSettingCompanyProfile(payload, refresh = undefined, logo = true) {
      this.company.isLoading = true
      const formData = this.setFormData(payload, logo)
      try {
        await request({
          url: `/v1/config/company`,
          method: 'post',
          data: formData,
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        this.company.refresh = refresh != undefined ? refresh : true
        await this.$getSettingCompanyProfile(),
        !!payload.logo && logo && ElMessage({
          type: 'success',
          message: 'Company logo changed'
        })
      } catch (error) {
        console.error('Failed to update company profile', error)
      } finally {
        this.company.isLoading = false
      }
    },
    async $getSettingCountry() {
      if (!!this?.countries?.result && this.countries?.result?.length) {
        return this.countries.result
      }

      const response = await request({
        url: `/v1/pos/settings/country`,
        method: 'get'
      })
      this.countries.result = response

      return response
    },
    async $getSettingCurrency() {
      if (!!this?.currencies?.result && this.currencies?.result?.length) {
        return this.currencies.result
      }

      const response = await request({
        url: `/v1/pos/settings/currency`,
        method: 'get'
      })
      this.currencies.result = response

      return response
    },
    async $getSettingTimeZone() {
      if (!!this?.timezone?.result && this.timezone?.result?.length) {
        return this.timezone.result
      }

      const response = await request({
        url: `/v1/pos/settings/timezone`,
        method: 'get'
      })
      this.timezone.result = response

      return response
    },
    async $getSettingDistance() {
      if (!!this?.distance?.result?.current && this.distance?.result?.units?.length) {
        return this.distance.result
      }

      const response = await request({
        url: `/v1/pos/settings/distance-units/show`,
        method: 'get'
      })
      this.distance.result = response
      if (this.distance?.result?.units) this.distance.result.units = response.units?.map(unit => ({
        value: unit,
        label: unit === 'Km' ? 'Kilometers' : unit
      })) || null

      return response
    }
  }
})