<template>
  <transition name="fade">
    <div v-show="state.splashLoading.isLoading" class="loading-screen">
      <div class="loading-screen__message-box">
        <!-- <lottie-player
          class="mx-auto my-12"
          src="/lottie-assets/buyback-loading.json"
          background="transparent"
          style="width: 80px; height: 80px"
          loop
          autoplay
        ></lottie-player> -->
        <div class="flex items-center justify-center mb-10">
          <BaseLoading />
        </div>
        <transition name="slide-fade">
          <div
            v-if="!isTimeout"
            class="loading-screen__message-content text-center"
          >
            <h2 class="text-lg my-1">DID YOU KNOW</h2>
            <transition name="slide-fade">
              <div
                :key="currentLoadingMessage.message"
                :class="[
                  'loading-screen__message',
                  `--${currentLoadingMessage.direction}`
                ]"
              >
                <p
                  v-html="currentLoadingMessage.message"
                  class="px-4 py-2 my-4"
                ></p>
                <img v-bind="currentLoadingMessage.image" />
                {{ currentLoadingMessage.image.height }}
              </div>
            </transition>
          </div>
          <div v-else>
            <Alert color="orange" view="smooth">
              <div class="flex items-center text-left">
                <i class="material-icons pr-8">warning</i>
                <p>
                  If you are having issues loading or logging in, try reloading
                  <br />
                  the page or clearing the cache on your browser and try again
                </p>
              </div>
            </Alert>
          </div>
        </transition>
      </div>
    </div>
  </transition>
</template>
<script>
import { defineComponent, ref, onUnmounted, watch } from 'vue'
import { state, useSplashLoading } from 'composable/loading'
import BaseLoading from 'components/BaseLoading.vue'
import Alert from 'bbUi/components/alert/alert.vue'
export default defineComponent({
  components: {
    Alert,
    BaseLoading
  },
  setup() {
    const {
      loadingMessages,
      currentLoadingMessage,
      changeLoadingMessage,
      setLoadingState
    } = useSplashLoading()
    let intervalState = null
    let timeoutState = null
    const isTimeout = ref(false)
    watch(
      () => state.splashLoading.isLoading,
      (isLoading) => {
        if (isLoading) {
          intervalState = setInterval(changeLoadingMessage, 6000)
          timeoutState = setTimeout(() => {
            isTimeout.value = true
          }, 35000)
          return null
        }
        clearInterval(intervalState)
        clearTimeout(timeoutState)
      },
      {
        immediate: true
      }
    )
    onUnmounted(() => {
      clearInterval(intervalState)
      clearTimeout(timeoutState)
    })
    return {
      state,
      isTimeout,
      loadingMessages,
      currentLoadingMessage,
      setLoadingState
    }
  }
})
</script>
