import request from 'utils/request'

const state = () => ({
  all: {
    data: []
  },
  franchisee: null,
  show: {
    data: null
  },
  filter: {}
})

const getters = {}

const mutations = {
  SET_ALL(state, payload) {
    state.all = payload
  },
  SET_SHOW(state, payload) {
    state.show.data = payload
  },
  SET_FRANCHISEE(state, payload) {
    state.franchisee = payload
  },
  SET_LOCATION_FILTER(state, payload) {
    state.filter = payload
  }
}

const actions = {
  async all({ commit }, payload) {
    // TODO: Search by search instead separate address name etc
    const response = await request({
      url: '/v1/pos/settings/store-location',
      method: 'get',
      params: payload
    })

    commit('SET_ALL', response)
  },
  async create({ }, payload) {
    return await request({
      url: `/v1/pos/settings/store-location`,
      method: 'post',
      data: payload
    })
  },
  async update({ }, payload) {
    return await request({
      url: `/v1/pos/settings/store-location/${payload.id}`,
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload.data
      }
    })
  },
  async delete({ }, payload) {
    return await request({
      url: `/v1/pos/settings/store-location/${payload.id}`,
      method: 'post',
      data: {
        _method: 'DELETE',
        ...payload.data
      }
    })
  },
  async show({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/store-location/${payload.id}`,
      params: {
        ...(payload.is_store && { is_store: payload.is_store })
      },
      method: 'get'
    })

    commit('SET_SHOW', response)
    return response
  },
  async showOnBoarding({ }) {
    return await request({
      url: '/v1/pos/settings/store-location/on-boarding',
      method: 'get'
    })
  },
  async updateOnBoarding({ }, payload) {
    return await request({
      url: '/v1/pos/settings/store-location/on-boarding',
      method: 'post',
      data: payload
    })
  },
  async mailLocation(_, payload) {
    return await request({
      url: '/v1/pos/settings/store-location/mail-in',
      method: 'post',
      data: payload
    })
  },
  async requestFranchisee(_, payload) {
    return await request({
      url: `/v1/pos/franchisee/send-request/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async getFranchiseeStore({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/franchisee/listing',
      method: 'get',
      params: payload
    })

    commit('SET_FRANCHISEE', response)
  },
  async approveFranchisee(_, payload) {
    const response = await request({
      url: `/v1/pos/franchisee/accept/${payload}`,
      method: 'post'
    })
    return response
  },
  async rejectFranchisee(_, payload) {
    const response = await request({
      url: `/v1/pos/franchisee/decline/${payload}`,
      method: 'post'
    })
    return response
  },
  async revokeFranchisee(_, payload) {
    const response = await request({
      url: `/v1/pos/franchisee/delete/${payload}`,
      method: 'post'
    })
    return response
  },
  async cancelFranchisee(_, payload) {
    const response = await request({
      url: `/v1/pos/franchisee/cancel-request/${payload}`,
      method: 'post'
    })
    return response
  },
  async unlinkFranchisee(_, payload) {
    const response = await request({
      url: `/v1/pos/franchisee/unlink/${payload}`,
      method: 'post'
    })
    return response
  },
  async widgetCodeFranchisee(_, payload) {
    const response = await request({
      url: `/v1/pos/franchisee/update-widget/${payload.id}`,
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload.data
      }
    })
    return response
  },
  async locationFilter({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/store-location/filter',
      method: 'get',
      params: payload
    })
    commit('SET_LOCATION_FILTER', response)
  },
  async switchLocation(_, payload) {
    return await request({
      url: '/v1/pos/settings/store-location/switch/location-type',
      method: 'post',
      data: payload
    })
  },
  async getProrateAmount(_, params) {
    return await request({
      url: '/v1/pos/settings/store-location/proration',
      method: 'get',
      params
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
