import request  from 'utils/request'

const state = () => ({
  status: 0,
  update: null
})

const getters = {}

const mutations = {
  SET_ROUNDUP_STATUS(state, payload) {
    state.status = payload
  },
  SET_ROUNDUP_UPDATE(state, payload) {
    state.update = payload
  }
}

const actions = {
  async get({ commit }) {
    const response = await request({
      url: '/v1/pos/settings/round-up-price',
      method: 'get'
    })

    commit('SET_ROUNDUP_STATUS', response)
  },
  async update({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/update',
      method: 'put',
      data: payload
    })

    commit('SET_ROUNDUP_UPDATE', response)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}