import style from '../theme/export.module.scss'

const filterBy = name => {
  const regex = new RegExp(`^${name}-`, 'gi')
  return Object.entries(style).filter(item => item[0].match(regex))
}

const colors = () => {
  return filterBy('color').map(item => {
    const key = item[0].match(/^color-([a-zA-Z]+)_*(.*)/)

    return [key[1], isNaN(parseInt(key[2])) ? key[2] : `0${key[2]}`, item[1]]
  }).reduce((acc, curr) => {
    acc[curr[0]] = acc[curr[0]] || {}
    if(curr[1].length === 0) acc[curr[0]] = curr[2]
    else {
      acc[curr[0]][curr[1]] = acc[curr[1]] || {}
      acc[curr[0]][curr[1]] = curr[2]
    }

    return acc
  }, new Object(null))
}

const sizes = () => {
  return Object.fromEntries(filterBy('screen').map(item => [item[0].replace(/screen-/, ''), item[1]]))
}

