const useBrand = () => {
  const loading = ref(false)
  const action = async (params, callback = () => {}) => {
    try {
      loading.value = true
      await store.dispatch(`pricing/${useDevice(params).param(params)}/brand`)
      isFunction(callback) && callback()  
    } catch (error) {
      console.log(error)
    } finally {
      loading.value = false
    }
  }
  const state = computed(() => {
    return store.state.pricing.brand
  })
  const tab = computed(() => {
    return [{
      name: 'All',
      to: {
        name: 'settingBulkPriceEditorTabManufacturer',
        params: {
          manufacturer: 'all'
        }
      }
    }].concat(state.value.map(item => ({
      name: item,
      to: {
        name: 'settingBulkPriceEditorTabManufacturer',
        params: {
          manufacturer: item
        }
      }
    })))
  })

  return {
    state,
    loading,
    tab,
    action
  }
}

const useCategories = () => {
  const data = reactive({
    search: null,
    page: 1,
    perPage: 10,
    pagination: {},
    isScraper: 0
  })
  const loading = reactive({
    all: false,
    update: false
  })
  const state = ref([])
  const actionAll = async (route, callback = () => {}) => {
    try {
      const path = `pricing/${useDevice(route.tab).param(route.tab)}/brand`
      loading.all = true
      let response = await store.dispatch(path, {
        manufacturer: route.manufacturer == 'all' ? '' : route.manufacturer,
        search: data.search,
        categories: route.tab,
        page: data.page,
        limit: data.perPage
      })
      response = JSON.parse(JSON.stringify(response))
      const listItems = response.data
      Object.keys(response).forEach(key => {
        if (key !== 'data') data.pagination[key] = response[key]
      })
      data.page = response.current_page
      data.perPage = response.per_page
      if (response.is_scraper) data.isScraper = response.is_scraper
      state.value = listItems.map((item, index) => {
        return generateItemList(item)
      })
      globals.categories.state.all = state.value
      isFunction(callback) && callback(response)
    } catch {
      isFunction(callback) && callback({})
    } finally {
      loading.all = false
    }
  }

  const generateItemList = (item, route) => {
    return {
      ...item,
      loading: false,
      collapse: false,
      level_names: [item.name]
    }
  }
  const updateItemPreservingState = (existingItem, newItem, rerender = false) => {
    const preservedFields = [
      'collapse', 'children', 'conditions', 'conditions_group',
      'condition_options', 'alert', 'disabled', 'renderKey', 'devices'
    ]

    for (const key in newItem) {
      if (!preservedFields.includes(key)) {
        existingItem[key] = newItem[key]
      }
    }

    if (rerender) existingItem.renderKey = Date.now()
  }
  const mergeChildrenPreserveInPlaceChunked = (existingChildren, newChildren, chunkSize = 100, callback) => {
    let i = 0

    // This for chuncked sistem if too many children to update
    const processChunk = () => {
      const len = newChildren.length
      const end = Math.min(i + chunkSize, len)

      for (; i < end; i++) {
        const newChild = newChildren[i]
        const idx = existingChildren.findIndex(c => c.id === newChild.id)
        
        if (idx !== -1) {
          Object.assign(existingChildren[idx], newChild)
          // For Debugging NAME if data has updated
          // existingChildren[idx].name = `${newChild.name} [UPDATED]`

          // Recursive update for child
          if (Array.isArray(newChild.children) && newChild.children.length) {
            if (!Array.isArray(existingChildren[idx].children)) {
              existingChildren[idx].children = []
            }
            mergeChildrenPreserveInPlaceChunked(
              existingChildren[idx].children,
              newChild.children,
              chunkSize
            )
          }
        }
      }

      if (i < len) {
        setTimeout(processChunk, 0) // break event into next loop
      } else {
        callback?.()
      }
    }

    processChunk()
  }
  const actionSpecificV2 = async (payload, state, callback = () => {}) => {
    try {
      const path = `pricing/gadgets/brand`
      loading.all = true
      const response = await store.dispatch(path, {...payload})
      const _item = response?.data?.shift()
  
      const newItem = generateItemList(_item, {
        tab: payload.categories
      })

      const indexOld = [...state].findIndex(item => item.id === newItem.id)
      if (indexOld >= 0) {
        updateItemPreservingState(state[indexOld], newItem)
        const responseChild = await store.dispatch(`pricing/gadgets/brandChild`, { id: newItem.id })

        mergeChildrenPreserveInPlaceChunked(state[indexOld].children, responseChild.children)
        
        state[indexOld].conditions_group = responseChild?.conditions_group ?? state[indexOld].conditions_group
        state[indexOld].conditions_group_id = responseChild?.conditions_group_id ?? state[indexOld].conditions_group_id
        state[indexOld].conditions = responseChild?.conditions ?? state[indexOld].conditions
        state[indexOld].group = responseChild?.group ?? state[indexOld]?.group
        
        return isFunction(callback) && callback(true)
      } else return isFunction(callback) && callback(false)
    } catch {
      return isFunction(callback) && callback(false)
    } finally {
      loading.all = false
    }
  }

  const actionCategories = async (callback = () => {}) => {
    try {
      await store.dispatch(`pricing/categories`, {
        enabled_only: 1
      })
      isFunction(callback) && callback()
    } catch(err) {
      console.log(err)
    } finally {

    }
  }

  const stateCategories = computed(() => store.state.pricing.categories)

  const actionConditionUpdate = async (data, callback = () => {}) => {
    try {
      loading.update = true
      await store.dispatch(`pricing/${useDevice(data.route.tab).param(data.route.tab)}/categoryConditionUpdate`, data.data)
      isFunction(callback) && callback()
    } catch(err) {
      console.log(err)
    } finally {
      loading.update = false
    }
  }

  const getRule = async(payload) => {
    try {
      const response = await store.dispatch(`pricing/getRule`, payload)
      return response
    } catch {
      return {}
    }
  }

  return {
    data,
    state,
    loading,
    actionAll,
    actionSpecificV2,
    actionCategories,
    stateCategories,
    actionConditionUpdate,
    getRule,
    updateItemPreservingState,
    mergeChildrenPreserveInPlaceChunked
  }
}

const useDevice = (params) => {
  const delay = ref(null)
  const loading = reactive({
    all: false,
    update: false,
    custom_price: false,
    rule: false,
    rules: [],
    set_base_price: new Map(),
    manual_price: false
  })

  const param = ((params) => {
    // if (params === 'mac') {
    //   return 'computers'
    // }
    return 'gadgets'
  })

  const actionAll = async (ids, filters, callback = () => {}) => {
    loading.all = true
    const isValidFilters = typeof filters === 'object' && !Array.isArray(filters) && filters !== null
    if(!isValidFilters) {
      filters = {
        page: 1,
        limit: 100,
        search: ''
      }
    }
    const payload = {
      id: ids.id,
      params: {
        ...filters
      }
    }
    try {
      let response = await store.dispatch(`pricing/${param(params)}/device`, payload)
      response = JSON.parse(JSON.stringify(response))
      isFunction(callback) && callback(response)
      loading.all = false
      return response
    } catch (error) {
      loading.all = false
      isFunction(callback) && callback(error)
    }
  }
  const actionGetChildren = async (payload, callback = () => {}) => {
    try {
      let response = await store.dispatch(`pricing/${param(params)}/brandChild`, payload)
      response = JSON.parse(JSON.stringify(response))

      // === SET CONDITION FOR MANUAL METHOD ===
      const onSetConditions = (conditions) => {
        return conditions.map(condition => {
          return {
            ...condition,
            set_percentage: Number(condition.condition_sub_value) > 0 ? 'plus' : 'min',
            loading: false
          }
        }) ?? []
      }
      // === SET CONDITION FOR MANUAL METHOD ===

      // === SET CONDITION FOR SELECT OPTIONS MANUAL METHOD ===
      const setConditionsOptions = (conditions) => {
        let conditionOptions = conditions ? [...conditions] : []
        let indexManual = conditionOptions.findIndex(condition => condition.name === 'Manual')
        
        if (indexManual === -1) conditionOptions.push({name: 'Manual', id: null, parent_id: null})

        return conditionOptions
      }
      // === SET CONDITION FOR SELECT OPTIONS MANUAL METHOD ===

      const setResult = (response) => {
        return {
          ...response,
          alert: response?.conditions_group?.length !== 0,
          disabled: response.conditions ? response.conditions.filter(condition => condition.parent_id != null) : [],
          conditions: onSetConditions(response.conditions),
          condition_options: setConditionsOptions(response.conditions)
        }
      }
      const result = setResult(response)
      console.log(`pricing/${param(params)}/SET_DEVICE_CHILD`)
      // store.commit(`pricing/${param(params)}/SET_DEVICE_CHILD`)

      isFunction(callback) && callback(result)
      return result
    } catch (error) {
      isFunction(callback) && callback({})
      return {}
    }
  }
  const actionUpdate = async (data, callback = () => {}) => {
    loading.update = true
    loading.custom_price = false 
    try {
      const response = await store.dispatch(`pricing/${param(params)}/deviceUpdate`, data)
      isFunction(callback) && callback(null, response)
    } catch(err){
      isFunction(callback) && callback(err, null)
    }finally {
      loading.update = false
      loading.custom_price = false 
    }
  }
  const actionUpdateRoundup = async (data, callback = () => {}) => {
    try {
      loading.update = true
      const response = await store.dispatch(`pricing/${param(params)}/deviceUpdateRoundup`, data)
      isFunction(callback) && callback(response)
    } catch (error) {
      console.log(error)
    } finally {
      loading.update = false
    }
  }

  const actionPreview = async(payload, callback = () => {}) => {
    try {
      const response = await await store.dispatch(`pricing/getPreviewComputer`, payload)
      isFunction(callback) && callback(response)
      return response  
    } catch (error) {
      console.log(error)
      return {}
    }
  }

  const actionSetRule = async (data, callback = () => {}) => {
    try {
      loading.rule = true
      const response = await store.dispatch(`pricing/gadgets/setRule`, data)
      isFunction(callback) && callback(response)
      loading.rule = false
      return response
    } catch (error) {
      isFunction(callback) && callback()
      loading.rule = false
      return false
    }
  }

  const actionUnsetRule = async (data, callback = () => {}) => {
    try {
      const response = await store.dispatch(`pricing/gadgets/unsetRule`, data)
      isFunction(callback) && callback(response)
    } catch (error) {
      console.log(error)
    }
  }

  const actionSetBasePrice = async (data={}, callback=() => {}) => {
    const { id: product_id, base_price } = data
    try{
      loading.set_base_price.set(product_id, true)
      const response = await store.dispatch(`pricing/${param(params)}/setBasePrice`, {
        product_id,
        base_price
      })
      isFunction(callback) && callback(false, response)
    }catch(err){
      isFunction(callback) && callback(err, null)
    }finally{
      loading.set_base_price.delete(product_id)
    }
  }
  const actionSetLevel = (item) => {
    const { parent_id, children } = item

    if (parent_id === null && (typeof children === 'undefined' || Array.isArray(children))) return 'device'

    if (parent_id !== null && Array.isArray(children) && children.length > 0) return 'model'

    return 'last_child'
  }
  const actionManualPrice = async (payload, callback = () => {}) => {
    try {
      loading.manual_price = true
      const response = await store.dispatch(`pricing/getManualPrice`, payload)
      isFunction(callback) && callback(false, response)
    } catch(err) {
      isFunction(callback) && callback(err, null)
    } finally {
      loading.manual_price = false
    }
  }

  return {
    loading,
    actionAll,
    actionGetChildren,
    actionUpdate,
    actionUpdateRoundup,
    actionSetRule,
    actionUnsetRule,
    actionPreview,
    actionSetBasePrice,
    param,
    actionSetLevel,
    actionManualPrice
  }
}

const usePriceSyncCustomPrice = () => {
  const items = ref([])
  const sources = ref({
    modal: false,
    item: undefined,
    isEdit: false,
    key: undefined
  })
  const resetCustomPrice = async (payload={}, callback =()=>{}) => {
    try{
      if(payload.product_id) globals.customPrice.loading.reset.set(payload.product_id)
      const response = await store.dispatch('pricing/gadgets/resetCustomPrice', payload)
      isFunction(callback) && callback(null, response)
    }catch(err){
      isFunction(callback) && callback(err, null)
    }finally{
      if(payload.product_id) globals.customPrice.loading.reset.delete(payload.product_id)
    }
  }
  const resetBulkCustomPrice = async(payload={}, callback=()=>{}) => {
    try{
      if(payload.category_id) globals.customPrice.loading.resetBulk.set(payload.category_id)
      const response = await store.dispatch('pricing/gadgets/resetBulkCustomPrice', payload)
      isFunction(callback) && callback(null, response)
    }catch(err){
      isFunction(callback) && callback(err, null)
    }finally{
      if(payload.category_id) globals.customPrice.loading.resetBulk.delete(payload.category_id)
    }
  }
  const resetBulkCustomPriceV2 = async(payload={}, callback=()=>{}) => {
    try {
      const response = await store.dispatch('pricing/gadgets/resetBulkCustomPrice', payload)
      isFunction(callback) && callback(null, response)
      return response
    } catch(err) {
      isFunction(callback) && callback(err, null)
    }
    // finally {
    //   if(payload.category_id) globals.customPrice.loading.resetBulk.delete(payload.category_id)
    // }
  }
  const handleJSONParse = (terms) => {
    try {
      return JSON.parse(terms)
    } catch {
      return null
    }
  }
  const handleTimeOut = (item, index, time) => {
    if (item.timer) clearTimeout(item.timer)
    item.price[index].success = true

    item.price[index].timer = setTimeout(() => {
      item.price[index].success = false
    }, time)

    return item
  }
  const handleSortByKey = (data, key) => {
    return data?.sort((a, b) => (a.key || 0) - (b.key || 0)) || []
  }
  const handleFormatCondition = (conditions, parentId) => {
    return handleSortByKey(conditions?.map(item => {
      const terms = handleJSONParse(item.condition_term) || []
      return ({
      ...item,
      termVhtml: terms.length > 1 ? sanitize(terms[0]) : '',
      disabled: !!parentId,
      })
    }) ?? [], 'sort')
  }
  const handleSetPriceSyncCustomPrice = (item, index) => {
    if (index === undefined || index === null) return

    if (item.price[index].is_editable_custom_price === 1) return

    item.price[index].is_editable_custom_price = 1

    return item
  }
  const handleUnsetPriceSyncCustomPrice = (item, index) => {
    if (index === undefined || index === null) return
    
    item.price[index].is_editable_custom_price = 0
    item.price[index].price = item.price[index].original_price || 0

    return item
  }
  const handleResetPriceSyncCustomPrice = async (item, index, product) => {
    if ([undefined, null].includes(index)) return

    const { id, source } = item.price[index]
    const payload = {
      product_id: item.id,
      condition_id: id,
      source: !source && product.is_price_sync ? '-' : source,
      last_child_id: product.id
    }

    try {
      item.price[index].loading = true
      let response = {}
      await resetCustomPrice(payload, (err, res) => {
        if (!err) {
          response = res
        }
      })

      Object.assign(item.price[index], {
        is_editable_custom_price: 0,
        is_custom_price: 0,
        price: response?.data?.price || 0,
        original_price: response?.data?.price || 0
      })
      return response
    } catch (error) {
      console.error('Error resetting custom price:', error)
    } finally {
      item.price[index].loading = false
      item = handleTimeOut(item, index, 1000)

    }
  }
  const handleSavePriceSyncCustomPrice = async (item, index, product, callback = {}) => {
    if ([undefined, null].includes(index)) return

    const { id, price, source } = item.price[index]
    const payload = {
      product_id: item.id,
      condition_id: id,
      price: price,
      ...( source && { is_sync: product?.is_price_sync ? 1 : 0}),
      ...( !source && { is_sync: product?.is_price_sync ? 2 : 0})
    }
    item.price[index].loading = true

    try {
      item.price[index].loading = true
      let response = undefined
      await callback(payload, (_, res) => {
        response = res
      })

      if (!response?.data?.pricing?.length) return

      const condition = response.data.pricing.find(res => res.id === id)
      Object.assign(item.price[index], {
        is_custom_price: condition.is_custom_price,
        price: condition.price,
        last_update: condition.last_update,
        original_price: condition.price || 0,
        success: true,
      })
      return response.data
    } catch (error) {
      console.error('Error saving price:', error)
    } finally {
      item.price[index].loading = false
      item = handleTimeOut(item, index, 1000)
    }
  }
  const handleSetItems = (value, roundUp) => {
    if (!!value?.children?.length) {
      items.value = [...(value.children ?? value.children)]?.map(item => ({
          ...item,
          top_id: value.id,
          is_child: true,
          loading: false,
          rounde_up: roundUp,
          conditions: handleFormatCondition(value.conditions, value.parent_id),
          total_device_manual: value.total_device_manual,
          total_device_sync: value.total_device_sync,
          level_names: [...value.level_names, item.name],
          refDom: undefined
      }))
    } else {
      items.value = [{
        ...value,
        top_id: value.id,
        collapse: false,
        rounde_up: roundUp,
        loading: false,
        conditions: handleFormatCondition(value.conditions, value.parentId),
        refDom: undefined
      }]
    }
  }
  const handleSetDevices = (res) => {
    return ({
      ...res,
      data: res?.data?.map(deviceData => {
        const priceSorted = (deviceData?.price?.map(price => {
          const rules = handleJSONParse(price?.rule) || []
          return {
            ...price,
            is_disabled: !!price?.is_subcondition,
            rules: Array.isArray(rules) ? rules.filter((_, key) => key > 0) : [],
            title_rule: Array.isArray(rules) && rules?.length > 0 ? rules[0] : '',
            success: false,
            original_price: price.price
          };
        }) || []).sort((a, b) => a.sort - b.sort)

        return {
          ...deviceData,
          timer: null,
          price: priceSorted,
        }
      }) || [],
    })
  }

  return {
    items,
    sources,
    loading: globals.customPrice.loading,
    resetCustomPrice,
    resetBulkCustomPrice,
    resetBulkCustomPriceV2,
    handleJSONParse,
    handleTimeOut,
    handleSortByKey,
    handleFormatCondition,
    handleSetPriceSyncCustomPrice,
    handleUnsetPriceSyncCustomPrice,
    handleResetPriceSyncCustomPrice,
    handleSavePriceSyncCustomPrice,
    handleSetItems,
    handleSetDevices
  }
}

