const usePriceMappingMethod = () => {
  const data = reactive({
    method: null,
    loading: false,
    conditions: [],
    sourceConditions: [],
    conditionIDS: []
  })

  const conditions = computed({
    get: () => {
      if (data?.conditions?.length && [1, 2].includes(Number(data.method))) {
        return data.conditions
      }

      return []
    },
    set: (val) => data.conditions = val
  })
  const sourceConditions = computed({
    get: () => {
      return [...data.sourceConditions].map(item => {
        const condition = item?.condition?.map(cond => ({
          ...cond,
          disabled: false // update from 'https://app.clickup.com/t/86cxr4kj2' !handleMappedItem(cond) ? false : true
        })) || undefined

        return {
          ...item,
          ...(condition && { condition }),
          ...(!condition && { disabled: false }) // update from 'https://app.clickup.com/t/86cxr4kj2' !handleMappedItem(item) ? false : true })
        }
      })
    },
    set: (val) => {
      data.sourceConditions = val
    }
  })

  const isValidJSON = (jsonString) => {
    try {
      return jsonString != null && JSON.parse(jsonString) !== undefined
    } catch (e) {
      return false
    }
  }
  const getConditions = async (payload = {}, callback = () => {}) => {
    return await store.dispatch('setting/condition/showSourceConditions', {
      data: {
        sub_domain: payload.sub_domain,
        category_id: payload.category_id,
      }
    }).then(res => {
      return res?.map(item => {
        const terms = isValidJSON(item.condition_term)
          ? JSON.parse(item.condition_term).filter((_, key) => key >= 1)
          : []
        const term = isValidJSON(item.condition_term)
          ? JSON.parse(item.condition_term)[0]
          : null
        return ({
          ...item,
          condition_term: terms,
          term
        })
      })
      isFunction(callback) && callback(condition)
    }).catch(() => {
      isFunction(callback) && callback()
    })
  }
  const getManualConditions = async (payload = {}, callback = () => {}) => {
    return await store.dispatch('setting/condition/showSourceManualConditions', payload).then(res => {
      return res?.map(item => {
        const terms = isValidJSON(item.condition_term)
          ? JSON.parse(item.condition_term).filter((_, key) => key >= 1)
          : []
        const term = isValidJSON(item.condition_term)
          ? JSON.parse(item.condition_term)[0]
          : null
        return ({
          ...item,
          condition_term: terms,
          term
        })
      })
    }).catch(err => err)
  }
  const handleMappedItem = (source) => {
    if (data?.conditions) {
      return [...data.conditions].find(cond => cond?.source_condition_id == source.id)
    }
    return undefined
  }
  const handleExisting = (value, existing) => {
    if (!value?.length || !existing?.length) return false

    const sources = [...value].map(i => i.sub_domain)
    const existings = [...new Set([...existing].map(i => i.source))]

    if (sources.length !== existings.length) return false

    return sources.every((value) => existings.includes(value))
  }
  const handleWithLoading = async (callback) => {
    data.loading = true
    try {
      await callback()
    } finally {
      data.loading = false
    }
  }
  const handleParsingJSON = (value) => {
    let parsed = value

    if (!Array.isArray(value)) {
      const isValid = isValidJSON(value)
      if (isValid) {
        parsed = JSON.parse(value)
      } else parsed = value
    }

    const terms = Array.isArray(parsed) && parsed?.length ? [...parsed]?.filter((_, key) => key >= 1) : []
    const term = Array.isArray(parsed) && parsed?.length ? [...parsed]?.shift() : null

    return { term, terms }
  }

  return {
    data,
    conditions,
    sourceConditions,
    getConditions,
    getManualConditions,
    isValidJSON,
    handleExisting,
    handleWithLoading,
    handleParsingJSON
  }
}

const useMassPricing = () => {
  const cancelToken = ref(undefined)
  const loading = ref({
    search: false,
    conditions: false,
    all: false
  })
  const modal = ref(false)
  const data = ref({
    products: {},
    search: null,
    selectedProducts: [],
    conditions: [],
    selectedConditions: [],
    type: null,
    amountType: '1',
    amountPrice: 0
  })
  const readyToSave = computed(() => {
    const price = Number(data.value.amountPrice)

    const isValid =
      !loading.all &&
      ['1', '2'].includes(data.value.type) &&
      ['1', '0'].includes(data.value.amountType) &&
      data.value.amountPrice !== '' &&
      !isNaN(price) &&
      price >= 0

    return !isValid // true = not ready
  })

  const getModelItems = async(payload = '', callback = () => {}) => {
    loading.value.search = true
    try {
      if (cancelToken.value) cancelToken.value.cancel()
      
      cancelToken.value = axios.CancelToken.source()
      const response = await store.dispatch('pricing/getMassModelItems', {
        search: payload,
        cancelToken: cancelToken.value.token
      })
      data.value.products = response

      isFunction(callback) && callback(response)
      loading.value.search = false
    } catch (err) {
      data.value.products = []
      if (!axios.isCancel(err)) {
        isFunction(callback) && callback(undefined)
        loading.value.search = false
      }
    }
  }
  const getConditions = async () => {
    loading.value.conditions = true
    data.value.selectedConditions = []
    const promises = data.value.selectedProducts.map(async (product) => {
      try {
        const response = await store.dispatch('pricing/getMassConditions', {
          device_id: product.device_id,
          category_id: product.category_id
          // ...(product.level === 'model' && {
          //   name: product.name,
          // })
        })
        data.value.conditions.push({
          ...product,
          conditions: response
        })
      } catch {
        data.value.conditions.push({
          ...product,
          conditions: []
        })
      }
    })

    await Promise.all(promises)

    loading.value.conditions = false
  }
  const selectingProducts = () => {
    data.value.search = null
    data.value.products = {}
    data.value.conditions = []
    getConditions()
  }
  const canceling = () => {
    data.value = {
      search: null,
      products: {},
      conditions: [],
      selectedProducts: [],
      selectedConditions: [],
      type: null,
      amountType: '1',
      amountPrice: 0
    }
    loading.value = {
      search: false,
      conditions: false,
      all: false
    }
  }
  const setRules = async (callback = canceling) => {
    loading.value.all = true
    try {
      const payload = data.value.selectedProducts.map(product => {
        const conditions = deepClone(data.value.selectedConditions)
          .filter(condition => condition.category_id && product.category_id // product.device_id
        ).map(({ id }) => id)

        return {
          device_id: product.device_id,
          level: product.level,
          category_id: product.category_id,
          type: data.value.type,
          amount_type: data.value.amountType,
          amount_price: data.value.amountPrice,
          conditions
        }
      })

      const response = await store.dispatch('pricing/setMassRules', { rules: payload })

      isFunction(callback) && callback(response)
      loading.value.all = false
    } catch(err) {
      loading.value.all = false
      isFunction(callback) && callback({err})
    }
  }

  return {
    data,
    modal,
    loading,
    readyToSave,
    canceling,
    getModelItems,
    getConditions,
    selectingProducts,
    setRules
  }
}

const usePriceUploadDownloadTemplate = () => {
  const cancelToken = ref(null)
  const showInstructions = ref(false)
  const progress = ref(0)
  const templates = ref([
    {
      id: 'csv',
      label: 'CSV',
      name: 'CSV Template',
      loading: false,
      file: 'application/zip',
      tooltip: 'The template will be directly downloaded. You will receive a zip file, and each condition group will have its own template.'
    },
    {
      id: 'excel',
      label: 'XLSX',
      name: 'XLSX Template',
      loading: false,
      file: 'text/xlsx',
      tooltip: 'The template will be directly downloaded. You will receive one file, and each condition group will have their own sheet.'
    }
  ])
  const templateSelected = ref(null)
  const state = computed(() => store?.state?.pricing?.processing)

  const action = async (e) => {
    const temp = [...templates.value].findIndex(i => i?.id === e)
    if (temp >= 0) {
      templateSelected.value = templates.value[temp].label
      try {
        templates.value[temp].loading = true
        
        cancelToken.value = axios.CancelToken.source()
        
        await store.dispatch("pricing/priceDownloadTemplate", { type: e })
        store.commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS', {
          progress: 0.1,
          path: state.value?.pathDownload,
          type: state.value?.typeDownload,
          loading: state.value?.loadingDownload
        })
        ElMessage({
          message: 'Download in progress... Feel free to navigate away or close the window',
          type: 'info'
        })
        socket.emits('user-info', { uuid: localStorage.getItem('download_session_id') })
      } catch {
      } finally {
        templates.value[temp].loading = false
      }
    }
  }
  const cancelDownload = async () => {}
  const saveStateDownload = () => {}

  return {
    state,
    progress,
    templates,
    templateSelected,
    showInstructions,
    action,
    cancelDownload,
    saveStateDownload
  }
}

const usePriceUpload = () => {
  const modal = ref(false)
  const modalCancelDownload = ref(false)
  const loading = ref(false)
  const loadingDownload = ref(false)
  const state = ref({
    latestUploadedFile: [],
    isUploadError: false,
    isUploading: false,
    inProgress: false
  })
  const _state = computed(() => store?.state?.pricing?.processing)

  const handleModalShow = async () => {
    modal.value = !modal.value
  }
  const handleUploadCompleted = async (e) => {
    state.value.isUploadError = false
    state.value.latestUploadedFile = e
  }
  const handleUploadError = () => {
    state.value.isUploadError = true
  }
  const handleUploading = (e = false) => {
    state.value.isUploading = e
  }
  const handleRemovedFile = async (file) => {
    state.value.latestUploadedFile = state.value.latestUploadedFile.filter(i => i.id !== file.id)
    state.value.isUploadError = false
  }
  const handleClose = async () => {
    if (loadingDownload.value) {
      modalCancelDownload.value = true
      return
    }

    modal.value = false
    loading.value = false
    loadingDownload.value = false
    state.value.latestUploadedFile = []
    state.value.isUploadError = false
    state.value.isUploading = false
  }
  const handleCloseWarning = () => {
    modalCancelDownload.value = false
  }
  const handleUpdatePricing = async (payload, callback = () => {}) => {
    loading.value = true
    try {
      await store.dispatch('pricing/setPriceByUpload', payload)
      handleClose()
      isFunction(callback) && callback('submit')
    } finally {
      loading.value = false
    }
  }

  return {
    modal,
    state,
    _state,
    loading,
    loadingDownload,
    modalCancelDownload,
    handleModalShow,
    handleUploadCompleted,
    handleUploadError,
    handleUploading,
    handleRemovedFile,
    handleClose,
    handleCloseWarning,
    handleUpdatePricing
  }
}

const usePriceUploadInvalid = () => {
  const modal = ref(false)
  const loading = ref(false)
  const progress = ref(0)
  const state = ref({
    loadingDownload: false,
    items: [],
    pagination: {
      limit: 10,
      page: 1,
      state: {}
    },
    totalItems: 0,
    typeFile: processing.value.typeInvalid,
    dateUpdate: null
  })
  const modalWarning = ref(false)
  const statePricing = computed(() => store?.state?.pricing?.processing)

  const getInvalidData = async () => {
    loading.value = true
    try {

      const { data } = await store.dispatch('pricing/getPriceUploadInvalid', {
        page: state.value.pagination.page,
        limit: state.value.pagination.limit
      })

      state.value.totalItems = data?.total || 0
      state.value.items = data.data
      state.value.pagination.page = data.current_page,
      state.value.pagination.limit = data.per_page
      state.value.pagination.state = data
      state.value.typeFile = processing.value?.typeInvalid || 'CSV'
      state.value.dateUpdate = processing.value.dateInvalid
    } catch {
      state.value.totalItems = 0
      state.value.items = []
      state.value.pagination = {
        page: 1,
        limit: 10,
        state: {}
      }
      state.value.typeFile = processing.value?.typeInvalid || 'CSV'
      state.value.dateUpdate = null
    } finally {
      loading.value = false
    }
  }
  const getInvalidDataDownload = async () => {
    state.value.loadingDownload = true
    try {
      await store.dispatch("pricing/getPriceUploadInvalidDownload", {})
      store.commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID', {
        progress: 0.1,
        path: statePricing.value?.pathDownload,
        type: statePricing.value?.typeDownload,
        loading: statePricing.value?.loadingDownload
      })
      socket.emits('user-info', { uuid: localStorage.getItem('download_session_id') })
      ElMessage({
        message: 'Download in progress... Feel free to navigate away or close the window',
        type: 'info'
      })
    } finally {
      state.value.loadingDownload = false
    }
  }
  const handleDownload = async () => await getInvalidDataDownload()
  const handleShow = async () => {
    modal.value = !modal.value
    if (modal.value) await getInvalidData()
  }
  const handleClose = () => {
    if (state.value.loadingDownload) {
      modalWarning.value = true
      return
    }

    loading.value = false
    modal.value = false
    modalWarning.value = false
    state.value = {
      loadingDownload: false,
      loading: false,
      items: [],
      pagination: {
        limit: 10,
        page: 1,
        state: {}
      },
      totalItems: 120,
      typeFile: 'CSV',
      dateUpdate: null
    }
  }
  const handleCloseWarning = () => {
    modalWarning.value = false
  }
  const handleCancelDownload = async () => {
    progress.value = 0
    modalWarning.value = false
    await nextTick()
    handleClose()
  }

  return {
    statePricing,
    progress,
    state,
    modal,
    loading,
    modalWarning,
    handleShow,
    handleClose,
    handleDownload,
    handleShowList: getInvalidData,
    handleCloseWarning,
    handleCancelDownload
  }
}

const useNotifDownload = () => {
  const download = (url) => {
    const parts = url.split('/')
    const name = parts[parts.length - 1] || null
    if (!name) return

    const aux = document.createElement('a')
    aux.href = url
    aux.download = name
    aux.click()

    window.URL.revokeObjectURL(url)
  }
  const closeNotifBasic = () => {
    store.dispatch('notification/closeNotification', 'NotificationPriceUploadDownload', { root: true })
    store.commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS', {
      progress: 0,
      path: null,
      type: 'csv',
      loading: false
    })
  }
  const closeNotifInvalid = () => {
    store.dispatch('notification/closeNotification', 'NotificationPriceUploadDownloadInvalid', { root: true })
    store.commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID', {
      progress: 0,
      path: null,
      type: 'csv',
      loading: false
    })
  }

  return {
    download,
    closeNotifBasic,
    closeNotifInvalid
  }
}

