export default (defaultValue = 'filled') => (prefix = null) => ({
  props: {
    view: {
      type: String,
      default: defaultValue
    }
  },
  computed: {
    isViewExist() {
      return this.views.includes(this.view)
    },
    classView() {
      return this.isViewExist ? `${prefix + '-' || ''}${this.view}` : ''
    }
  }
})