import moment from 'moment'
import { nextTick } from 'vue'

const bufferToBlob = (buffer, type) =>
  new Blob([buffer], {
    type: type
  })
const blobToUrl = (blob) => window.URL.createObjectURL(blob)

const anchorDownload = (buffer, name, type = 'application/pdf', noDate = true) => {
  const blob = bufferToBlob(buffer, type)
  const url = blobToUrl(blob)
  const timestamp = moment(Date.now()).format('YYYY-MM-DD-HHmmss')
  
  const extensionMap = {
    'application/zip': '.zip',
    'application/pdf': '.pdf',
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': '.xlsx',
    'application/vnd.ms-excel': '.xls',
    'text/csv': '.csv',
    'text/xlsx': '.xlsx', // optional if used somewhere
    'text/xls': '.xls'    // optional if used somewhere
  }

  const aux = document.createElement('a');
  aux.href = url;
  aux.download = noDate ? `${name}${extensionMap[type]}` : `${name}-${timestamp}${extensionMap[type]}`
  aux.click()

  window.URL.revokeObjectURL(url)
}

const anchorPrint = (buffer, type = 'application/pdf', openWindow = true) => {
  const blob = bufferToBlob(buffer, type)
  const url = blobToUrl(blob)
  if (openWindow) return window.open(url, '_blank')

  return window.location.replace(url)
}

const scrollToElement = (element = null) => {
  if (!element) return
  element.scrollIntoView({
    behaviour: 'smooth'
  })
}

const scrollIntoElement = (
  selector = null,
  options = {
    useNextTick: false
  }
) => {
  if (!selector) return
  let elements = []

  if (options.useNextTick) {
    nextTick(() => {
      elements = document.querySelectorAll(selector)
      if (elements.length === 0) return
      scrollToElement(elements[0])
    })
  } else {
    elements = document.querySelectorAll(selector)
    scrollToElement(elements[0])
  }
}

const nameCapitalize = (name) => {
  const names = name.split(' ')

  if (names.length <= 1 || name.includes('@')) return []

  return names
}

export { blobToUrl, anchorDownload, anchorPrint, scrollIntoElement, nameCapitalize }
