<script>
import { defineComponent, h, ref } from 'vue'
import { RouterLink } from 'vue-router'
import { mixinColor, mixinDisabled, mixinBlock, mixinLoading, mixinDense, mixinView, mixinType } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

import Loading from '../loading'

export default defineComponent({
  name: 'Button',
  mixins: [
    ...addMixinPrefix([
      mixinColor,
      mixinDisabled,
      mixinBlock,
      mixinLoading,
      mixinDense,
      mixinView('filled'),
      mixinType,
    ], 'btn')
  ],
  props: {
    link: {
      type: Boolean,
      default: false
    },
    tag: {
      type: String,
      default: 'button'
    },
    to: {
      type: Object,
      default: () => ({})
    },
    for: {
      type: String,
      default: null
    }
  },
  setup(props, { slots }) {
    const views = ref(['filled', 'smooth', 'outlined', 'raised'])
    const types = ref(['circle', 'pill'])
    
    const genContent = () => {
      return h('span', {
        class: 'btn__content',
      }, slots.default && slots.default() || '')
    }
    const genLoad = () => {
      return h('span', {
        class: ['btn__loader']
      }, slots.loader && slots.loader() || h(Loading))
    }

    const genIcon = () => {
      return h('span', {
        class: ['btn__icon']
      }, slots.icon && slots.icon())
    }

    return {
      genContent,
      genLoad,
      genIcon,
      views,
      types
    }
  },
  render() {
    const isCircle = this.type === 'circle'
    const isExistIcon = this.$slots.icon

    const classType = isCircle && !isExistIcon ? 'btn-filled' : this.classType
    const children = [
      isCircle && isExistIcon  ? this.genIcon() : this.genContent(),
      this.loading && this.genLoad()
    ]

    const listClass = [
      'btn',
      this.classColor,
      this.classBlock,
      this.classLoading,
      this.classDense,
      this.classView,
      classType
    ]

    return h(this.$props.link ? RouterLink : this.$props.tag, {
      for: this.$props.for,
      class: listClass,
      disabled: this.$props.disabled,
      to: this.$props.link ? this.$props.to : null,
      activeClass: ''
    }, {
      default: () => children
    })
  }
})
</script>