import { /* defaultRoutes, */ authRoutes, superRoutes } from 'router'
import { createStore, createLogger } from 'vuex'
import example from './modules/example'
import user from './modules/user'
import dashboard from './modules/dashboard'
import customer from './modules/customer'
import buyback from './modules/buyback'
import device from './modules/device'
import condition from './modules/condition'
import inventory from './modules/inventory'
import setting from './modules/setting'
import report from './modules/report'
import pricing from './modules/pricing'
import billing from './modules/billing'
import integration from './modules/integration'
import shipping from './modules/shipping'
import onboarding from './modules/onboarding'
import product from './modules/product'
import socket from './modules/socket'
import notification from './modules/notification'
import customTable from './modules/customTable'
import bulkQuote from './modules/bulkquote'
import alert from './modules/alert'
import request  from 'utils/request'

const debug = import.meta.env.DEV

export const filterRoutes = (routes, menu) => {
  const temp = []
  routes.forEach((route) => {
    const _route = Object.assign({}, route)
    // TODO: if(menu.includes())
    if (_route.children) {
      _route.children = filterRoutes(_route.children, [])
    }
    temp.push(_route)
  })
  return temp
}

const state = () => ({
  routes: [],
  languages: []
})

const getters = {
  getterExample(state, getters, rootState) {
    return
  }
}

const mutations = {
  SET_ROUTES(state, payload) {
    // state.routes = payload.concat(defaultRoutes)
    state.routes = payload
  },
  SET_LANGUAGES(state, languages = []) {
    state.languages = languages
  }
}

const actions = {
  async generateRoutes({ state, commit }, payload) {
    const generatedRoutes = filterRoutes(
      import.meta.env.MODE === 'super' ? superRoutes : authRoutes,
      payload
    )

    commit('SET_ROUTES', generatedRoutes)
  },
  async getLanguages({ commit }) {
    const response = await request({
      url: '/v1/pos/settings/lang/list',
      method: 'get'
    })
    commit('SET_LANGUAGES', response)
    return response
  }
}

export default createStore({
  state,
  getters,
  mutations,
  actions,
  modules: {
    example,
    alert,
    user,
    customer,
    dashboard,
    buyback,
    device,
    condition,
    inventory,
    setting,
    pricing,
    billing,
    report,
    integration,
    shipping,
    onboarding,
    product,
    socket,
    notification,
    customTable,
    bulkQuote
  },
  strict: debug,
  plugins: debug ? [createLogger()] : []
})
