import request from 'utils/request'

const state = () => ({
  show: null,
  loading: false,
  error: null,
})

const getters = {
  isLoading: state => state.loading,
  hasError: state => !!state.error,
  getError: state => state.error,
  getShow: state => state.show,
}

const mutations = {
  SET_SHOW(state, payload) {
    state.show = payload
  },
  SET_LOADING(state, status) {
    state.loading = status
  },
  SET_ERROR(state, error) {
    state.error = error
  },
  RESET_STATE(state) {
    state.show = null
    state.loading = false
    state.error = null
  }
}

const actions = {
  async getBulkQuote({ commit }) {
    try {
      commit('SET_LOADING', true)
      commit('SET_ERROR', null)
      
      const response = await request({
        url: `/v1/pos/settings/bulk-quote-receipt`,
        method: 'get',
      })
      
      commit('SET_SHOW', response)
      return response
    } catch (error) {
      commit('SET_ERROR', error.message || 'Failed to fetch bulk quote')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async saveBulkQuote({ commit }, payload) {
    try {
      commit('SET_LOADING', true)
      commit('SET_ERROR', null)

      const response = await request({
        url: '/v1/pos/settings/bulk-quote-receipt',
        method: 'post',
        data: payload
      })
      
      return response
    } catch (error) {
      commit('SET_ERROR', error.message || 'Failed to save bulk quote')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async previewBulkQuote({ commit }, payload) {
    try {
      commit('SET_LOADING', true)
      commit('SET_ERROR', null)

      const response = await request({
        url: '/v1/pos/settings/bulk-quote-receipt/preview',
        method: 'post',
        data: payload,
        responseType: 'arraybuffer'
      })
      
      return response
    } catch (error) {
      commit('SET_ERROR', error.message || 'Failed to preview bulk quote')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async resetBulkQuote({ commit }) {
    try {
      commit('SET_LOADING', true)
      commit('SET_ERROR', null)

      const response = await request({
        url: '/v1/pos/settings/bulk-quote-receipt/reset',
        method: 'put',
      })
      
      return response
    } catch (error) {
      commit('SET_ERROR', error.message || 'Failed to reset bulk quote')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  resetState({ commit }) {
    commit('RESET_STATE')
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}