import { reactive, computed, ref } from 'vue'
import { randomNumberBetween } from '../utils/number'

const state = reactive({
  splashLoading: {
    isLoading: true,
    loadingIndex: 0
  }
})

const useSplashLoading = () => {
  const loadingMessages = ref([
    {
      message: 'You can quickly request new devices in',
      direction: 'vertical',
      image: {
        src: '/loading/page-loading/request-device.svg',
        style: {
          height: '50px'
        }
      }
    },
    {
      message:
        'Quickly price your devices with our Pricing Sync tool, check it out in',
      direction: 'vertical',
      image: {
        src: '/loading/page-loading/device-pricing.svg',
        style: {
          height: '50px'
        }
      }
    },
    {
      message:
        'If you have any ideas or suggestions for us click the <b>"Feedback"</b> button on the right side of your screen.',
      direction: 'horizontal',
      image: {
        src: '/loading/page-loading/intercom-bubble.svg',
        style: {
          height: '90px'
        }
      }
    },
    {
      message:
        'If you have any questions for our team click on the message bubble on the bottom right side of your screen and click <b>"Send us a message"</b>',
      direction: 'horizontal',
      image: {
        src: '/loading/page-loading/intercom-bubble.svg',
        style: {
          height: '54px'
        }
      }
    },
    {
      message:
        'Check out the help articles by using the message bubble on the bottom right of your screen and navigate to <b>"Search for Help"</b>',
      direction: 'horizontal',
      image: {
        src: '/loading/page-loading/intercom-bubble.svg',
        style: {
          height: '54px'
        }
      }
    }
  ])
  const currentLoadingMessage = computed(
    () => loadingMessages.value[state.splashLoading.loadingIndex]
  )
  const setLoadingState = (newState = false) => {
    if (typeof newState === 'boolean') state.splashLoading.isLoading = newState
  }
  const resetLoadingIndex = () => {
    state.splashLoading.loadingIndex = 0
  }
  const changeLoadingMessage = () => {
    const loadingIndex = state.splashLoading.loadingIndex
    const lastIndexOfLoadingMessage = loadingMessages.value.length - 1
    if (loadingIndex === lastIndexOfLoadingMessage) return state.splashLoading.loadingIndex = 0
    state.splashLoading.loadingIndex++
  }

  state.splashLoading.loadingIndex = randomNumberBetween(0, loadingMessages.value.length-1)
  return {
    loadingMessages,
    currentLoadingMessage,
    resetLoadingIndex,
    changeLoadingMessage,
    setLoadingState
  }
}

export { useSplashLoading, state }
