<template>
  <div class="border border-grey-01 rounded-lg p-4">
    <div class="space-y-4">
      <p class="text-right">Last Update: <b>{{ date || '-' }}</b></p>
      <Table
        class="w-full"
        :data="data">
        <TableColumn
          label="ID">
          <template #default="scope">
            <span>{{ scope.row?.product_id || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="Item Name">
          <template #default="scope">
            <span>{{ scope.row?.product_name || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="Network">
          <template #default="scope">
            <span>{{ scope.row?.network_name || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="Storage">
          <template #default="scope">
            <span>{{ scope.row?.size_name || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="RAM">
          <template #default="scope">
            <span>{{ scope.row?.memory_name || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="Processor">
          <template #default="scope">
            <span>{{ scope.row?.processor_name || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="Graphic Card">
          <template #default="scope">
            <span>{{ scope.row?.graphics_card_name || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="ISBN10">
          <template #default="scope">
            <span>{{ scope.row?.isbn10 || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="ISBN13">
          <template #default="scope">
            <span>{{ scope.row?.isbn13 || '-' }}</span>
          </template>
        </TableColumn>
        <TableColumn
          label="reason">
          <template #default="scope">
            <span>{{ scope.row.reason }}</span>
          </template>
        </TableColumn>
      </Table>
      <Pagination
        v-model:page="paginationState.page"
        v-model:limit="paginationState.limit"
        :data="paginationState.state"
        @update:page="val => handleChangePage(paginationState, val, 'page')"
        @update:limit="val => handleChangeLimit(paginationState, val, 'limit')" 
      />
    </div>
  </div>
</template>
<script>
import { computed, defineComponent } from 'vue'

import Pagination from 'components/Pagination.vue'

export default defineComponent({
  name: 'PriceUploadInvalidTable',
  components: {
    Pagination
  },
  props: {
    items: {
      type: Array,
      default: () => []
    },
    pagination: {
      type: Object,
      default: {
        page: 1,
        limit: 10,
        state: {}
      }
    },
    date: {
      type: String,
      default: null
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:page', 'change-page', 'change-limit-page'],
  setup(props, ctx) {
    const data = computed(() => props.items)
    const paginationState = computed({
      get: () => props.pagination,
      set: (val) => ctx.emit('update:page', val)
    })

    const handleChangePage = (state, val, type) => {
      ctx.emit('change-page', val)
    }
    const handleChangeLimit = (state, val, type) => {
      ctx.emit('change-limit-page', val)
    }

    return {
      data,
      paginationState,
      handleChangePage,
      handleChangeLimit
    }
  }
})
</script>