import request  from 'utils/request'
import dateData  from 'utils/date'
import gadgets from "./pricing/gadgets"
import computers from "./pricing/computers"
import roundup from './pricing/roundup'

const state = () => ({
  brand: [],
  categories: [],
  company_sharing_member: {
    companies: [],
    total: null
  },
  pricing_source_companies: [],
  processing: {
    uploadProgress: 0,
    downloadProgress: 0,
    invalid: false,
    totalInvalid: 0,
    totalSuccess: 0,
    dateInvalid: null,
    typeInvalid: 'CSV',
    loadedCheckProgress: false,
    pathDownload: null,
    typeDownload: 'CSV',
    downloadProgressInvalid: 0,
    pathDownloadInvalid: null,
    typeDownloadInvalid: 'CSV',
    loadingDownload: false,
    loadingDownloadInvalid: false
  }
})

const getters = {
}

const mutations = {
  SET_BRAND(state, payload) {
    state.brand = payload
  },
  SET_CATEGORIES(state, data) {
    state.categories = data.data
  },
  SET_COMPANY_SHARING_MEMBER(state, data){
    state.company_sharing_member.companies = data.data
    state.company_sharing_member.total = data.total
  },
  SET_PRICING_SOURCE_COMPANIES(state, data){
    state.pricing_source_companies = data
  },
  SET_PROCESSING_UPLOAD_PROGRESS(state, data) {
    state.processing.uploadProgress = data.progress || 0
  },
  SET_PROCESSING_UPLOAD_INVALID(state, data) {
    const [date, clock] = (data?.date || '0000-00-00 00:00:00').split(' ')
    const [YYYY, MM, DD] = date.split('-')
    const [hh, mm] = clock.split(':')
    const ampm = parseInt(hh) >= 12 ? 'PM' : 'AM'

    state.processing.invalid = data?.invalid || false
    state.processing.totalInvalid = data?.total || 0
    state.processing.totalSuccess = data?.totalSuccess || 0
    state.processing.dateInvalid = `${MM || '-'}/${DD || '-'}/${YYYY || '-'} ${hh || '-'}:${mm || '-'} ${ampm}`
    state.processing.typeInvalid = data?.type === 'excel' ? 'XLSX' : 'CSV'
    state.processing.loadedCheckProgress = true
  },
  SET_PROCESSING_DOWNLOAD_PROGRESS(state, data) {
    state.processing.downloadProgress = data.progress || 0
    state.processing.pathDownload = data?.path || null
    state.processing.typeDownload = data.type === 'excel' ? 'XLSX' : 'CSV'
    state.processing.loadingDownload = data.loading || false
  },
  SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID(state, data) {
    state.processing.downloadProgressInvalid = data.progress || 0
    state.processing.pathDownloadInvalid = data?.path || null
    state.processing.typeDownloadInvalid = data.type === 'excel' ? 'XLSX' : 'CSV',
    state.processing.loadingDownloadInvalid = data.loading || false
  }
}

const actions = {
  async getRule({}, payload){
    return await request({
      url: `/v1/pos/pricing/get-rule`,
      method: 'post',
      data: payload
    })
  },
  async getPreviewComputer({}, payload){
    return await request({
      // url: '/v2/pos/pricing/computer/bulk-device-preview',
      url: '/v2/pos/pricing/bulk-device-preview',
      method: 'get',
      params: payload
    })
  },
  async categories({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/category/list',
      method: 'get',
      params: payload,
    })

    commit('SET_CATEGORIES', response)
  },
  async getCompanySharingMember ({commit}){
    const response = await request({
      url: '/v1/pos/pricing/sharing-member',
      method: 'get'
    })

    commit('SET_COMPANY_SHARING_MEMBER', response)
  },
  async getPricingSourceCompanies ({commit}, payload){
    const response = await request({
      url: '/v1/pos/pricing/syncable-sources',
      method: 'get',
      params: payload
    })

    commit('SET_PRICING_SOURCE_COMPANIES', response)
  },
  async getSourcePriceCoverage({ commit }, payload) {
    const response =  await request({
      url: `/v1/pos/pricing/price-coverage`,
      method: 'get',
      params: payload
    })

    return response
  },
  async getPricingSourceDeviceActive(_, payload){
    return await request({
      url: '/v1/pos/settings/check-device-active',
      method: 'post',
      data: payload
    })
  },
  // Mass Pricing
  async getMassModelItems(_, payload) {
    if (payload.search.length >= 3) {
      return await request({
        url: '/v1/pos/pricing/search-models',
        method: 'get',
        params: {
          search: payload.search
        },
        ...(payload.cancelToken && { cancelToken: payload.cancelToken })
      })
    } else {
      throw new Error('The search must be at least 3 characters.')
    }
  },
  async getMassConditions(_, payload) {
    return await request({
      url: '/v1/pos/pricing/manual-conditions',
      method: 'get',
      params: payload
    })
  },
  async setMassRules(_, payload) {
    return await request({
      url: '/v1/pos/pricing/set-rule-manual',
      method: 'post',
      data: payload
    })
  },
  // # Mass Pricing
  // Price Upload
  async priceDownloadTemplate(_, payload) {
    return await request({
      url: '/v1/pos/pricing/export',
      method: 'get',
      params: {
        type: payload.type
      },
      responseType: 'blob',
      ...( payload.cancelToken && { cancelToken: payload.cancelToken })
    })
  },
  async getPriceUploadInvalid(_, payload) {
    return await request({
      url: '/v1/pos/pricing/get-failed-import-prices',
      method: 'get',
      params: payload
    })
  },
  async getPriceUploadInvalidDownload(_, payload) {
    return await request({
      url: '/v1/pos/pricing/export/failed',
      method: 'get',
      params: payload,
      responseType: 'blob',
      ...( payload.cancelToken && { cancelToken: payload.cancelToken })
    })
  },
  async getPriceUploadInvalidCheck() {
    return await request({
      url: '/v1/pos/pricing/check-failed-import-prices',
      method: 'get'
    })
  },
  async setPriceByUpload(_, payload) {
    return await request({
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      url: '/v1/pos/pricing/import',
      method: 'post',
      data: payload
    })
  },
  // Price Upload
  async getManualPrice(_, payload) {
    return await request({
      url: '/v1/pos/pricing/check-manual-price',
      method: 'get',
      params: payload
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
  modules: {
    gadgets,
    computers,
    roundup
  }
}