<template>
  <transition name="fade">
    <div v-if="isShowNotification" class="notification-wrapper fixed right-0 bottom-0 z-10 flex items-end flex-col p-6 gap-4" style="max-width: 50%;">
      <div v-for="component in state.component" :key="`comp_${component?.name}`" class="flex-1 w-full">
        <div
          class="notification w-full">
          <div class="notification__container">
            <div v-if="!component?.closable">
              <transition name="slide-fade">
                <div
                  v-if="component?.isMinimized === false"
                  class="notification__minimize"
                  role="button"
                  tabindex="0"
                  @click="minimizeNotification(component.name)"
                >
                  <i class="material-icons icon-sm">minimize</i>
                </div>
              </transition>
            </div>
            <div v-else>
              <transition name="slide-fade">
                <div
                  class="notification__minimize"
                  role="button"
                  tabindex="0"
                  @click="closeNotification(component.name)"
                >
                  <i class="material-icons icon-sm">close</i>
                </div>
              </transition>
            </div>
            <transition name="slide-fade">
              <div
                v-if="component?.isMinimized"
                @click="maximizeNotification(component.name)"
                role="button"
                tabindex="0"
                class="notification__maximize"
              >
                <i class="material-icons icon-sm">open_in_full</i>
              </div>
              <component v-else :is="component.name" />
            </transition>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import { defineComponent, computed } from 'vue'
import { useNotification } from 'composable/notification'

import NotificationPricingBooks from 'components/Notification/Pricing/NotificationPricingBooks.vue'
import NotificationPricingBooksInvalid from 'components/Notification/Pricing/NotificationPricingBooksInvalid.vue'
import NotificationPriceUpload from 'components/Notification/Pricing/NotificationPriceUpload.vue'
import NotificationPriceUploadInvalid from 'components/Notification/Pricing/NotificationPriceUploadInvalid.vue'
import NotificationPriceUploadDownload from 'components/Notification/Pricing/NotificationPriceUploadDownload.vue'
import NotificationPriceUploadDownloadInvalid from 'components/Notification/Pricing/NotificationPriceUploadDownloadInvalid.vue'
import NotificationDatafeed from 'components/Notification/Datafeed/NotificationDatafeed.vue'
import NotificationShippingKitDownload from 'components/Notification/Shipping/NotificationShippingKitDownload.vue'
import { useRoute } from 'vue-router'

export default defineComponent({
  components: {
    NotificationPriceUploadDownload,
    NotificationPriceUpload,
    NotificationPriceUploadInvalid,
    NotificationPricingBooks,
    NotificationPricingBooksInvalid,
    NotificationDatafeed,
    NotificationPriceUploadDownloadInvalid,
    NotificationShippingKitDownload
  },
  setup() {
    const route = useRoute()
    const {
      state,
      minimizeNotification,
      maximizeNotification,
      closeNotification
    } = useNotification()
    const isShowNotification = computed(() => {
      return state.value.isShow && state.value.component?.length > 0
    })
    return {
      route,
      state,
      isShowNotification,
      minimizeNotification,
      maximizeNotification,
      closeNotification
    }
  }
})
</script>
<style lang="scss">
.notification {
  @apply m-0 bg-white p-4 border border-grey-lighten rounded-lg shadow transition-all duration-300 overflow-hidden;
  z-index: 2001;
  // max-width: 50%;
  overflow-wrap: break-word;
  &__container {
    @apply relative;
  }
  &__minimize {
    @apply cursor-pointer absolute top-0 right-0 p-1;
  }
  &__maximize {
    @apply cursor-pointer px-2 py-1;
  }
}
</style>
