import axios from 'axios'
import request  from 'utils/request'

let cancelTokenSource = undefined

const state = () => ({

})

const getters = {

}

const mutations = {

}

const actions = {
  async search({ rootState }, payload) {
    // use cancel token only for device search
    if (!payload.hasOwnProperty('type') || payload.type === 'devices') {
      if (cancelTokenSource) {
        cancelTokenSource.cancel('Cancelled')
      }
      cancelTokenSource = axios.CancelToken.source()
      const response = await request({
        url: `/v1/buyback/devices/all`,
        method: 'get',
        params: payload,
        cancelToken: cancelTokenSource.token
      })
      cancelTokenSource = undefined
      return response
    }
    const response = await request({
      url: `/v1/buyback/devices/all`,
      method: 'get',
      params: payload
    })
    cancelTokenSource = undefined
    return response
  },
  async conditions({ rootState }, payload) {
    return await request({
      url: `/v1/buyback/devices/all`,
      method: 'get',
      params: {
        ...payload,
        condition: 1
      }
    })
  },
  async customConditions({}, params){
    return await request({
      url: `/v1/buyback/devices/condition-category`,
      method: 'get',
      params
    })
  },
  async statuses({ rootState }, payload) {
    return await request({
      url: `/v1/pos/order/product-status`,
      method: 'get'
    })
  },
  async colors(_, payload) {
    return await request({
      url: `/v1/pos/order/color-option`,
      method: 'get',
      params: {
        ...payload
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}