import request  from 'utils/request'

const state = () => ({
})

const getters = {
}

const mutations = {
}

const actions = {
  async showCheckRegister({commit}, payload){
    const response = await request({
      url: '/v1/pos/order/check-register-history',
      method: 'get',
      params: {
        buyback_type: 2,
        ...payload
      }
    })
    commit('SET_SHOW_CHECK_REGISTER', response)
    return response
  },
  async showCheckSetting({}) {
    return await request({
      url: '/v1/pos/settings/check',
      method: 'get'
    })
  },
  async updateCheckSetting({}, payload) {
    let formData = new FormData()
    for( let [key, val] of Object.entries(payload) ){
      if( key === 'local_check_logo' || key === 'check_logo' ){
        if( !(val instanceof Blob) ) continue
      } 
      formData.append(key, val)
    }
    formData.append('_method', 'PUT')
    return await request({
      url: `/v1/pos/settings/update`,
      method: 'post',
      data: formData
    })
  },
  async localCheckPreview({}){
    return await request({
      url: `/v1/pos/print/local-check-preview`,
      method: 'get',
      responseType: 'arraybuffer'
    })
  },
  async onlineCheckPreview({}){
    return await request({
      url: `/v1/pos/print/check-preview`,
      method: 'get',
      responseType: 'arraybuffer'
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}