import Echo from 'laravel-echo'
import io from 'socket.io-client'
import moment from 'moment'
import Pubsub from 'utils/pubsub'
import { generateSessionId } from 'utils/keys'

const pubsub = Pubsub()

const setupSocket = () => ({
  echo: undefined,
  socket: undefined,
  async init(payload) {
    window.io = io
    if (this.echo) {
      this.echo.connector.leave()
      this.echo.connector.disconnect()
    }

		const socketUrl = window.localStorage.getItem('region_socket') ?? import.meta.env.VITE_SOCKET_URL

    this.echo = new Echo({
      withCredentials: false,
      broadcaster: 'socket.io',
      transports: ['websocket'],
      host: socketUrl,
      auth: {
        headers: {
          Authorization: payload.token,
          'X-Buyback-Token': payload.buyback_company_token
        }
      }
    })

    this.socket = this.echo.connector.socket

    if (!localStorage.getItem('download_session_id')) {
      localStorage.setItem('download_session_id', await generateSessionId())
    }
  },
  get storage() {
    return JSON.parse(localStorage.getItem('bbb_scan')) || undefined
  },
  connecting(chanelId, tenantId) {
    const ref = this
    if (chanelId) {
    this.echo.private(chanelId).listen('AppEvent', function (res) {
      if (res && res.data) {
        if (res.data.data && res.data.type === 'device-diagnostic') {
          pubsub.publish(res.data.type, {
            code: ref.storage && ref.storage.code,
            device: {
              ...res.data.data,
              ...(!res.data.data.Name && {
                Name: `${res.data.data.Brand} ${res.data.data.Model}`
              }),
              Date: moment(new Date(Date.now())).format('MM/DD/YYYY hh:mm A')
            }
          })
          return
        }
        pubsub.publish(res.data.type, res.data)
      }
    })
  }
    if (tenantId) {
      this.echo.private(tenantId).listen('AppEvent', function(res) {
        if (res && res.data) {
          pubsub.publish(res.data.type, res.data);
        }
      });
    }

    return this.socket
  },
  emits(name = undefined, payload = undefined) {
    if (!this.socket && !name) return

    if (payload) this.socket.emit(name, payload)
      else this.socket.emit(name)
  }
})

export const socket = setupSocket()
export const events = pubsub
