import request from 'utils/request'
import companyProfile from './setting/companyProfile'
import manageUser from './setting/manageUser'
import manageLocation from './setting/manageLocation'
import buybackDevice from './setting/buybackDevice'
import checkSetting from './setting/checkSetting'
import condition from './setting/condition'
import paymentMethod from './setting/paymentMethod'
import plan from './setting/plan'
import widget from './setting/widget'
import manageShipping from './setting/manageShipping'
import buybackReceipt from './setting/buybackReceipt'
import emailTemplate from './setting/emailTemplate'
import workflow from './setting/workflow'
import reviewEmail from './setting/reviewEmail'
import generalSettings from './setting/generalSettings'
import seo from './setting/seo'
import inventoryCondition from './setting/inventoryCondition'
import couponCode from './setting/couponCode'
import faq from './setting/faq'
import legalTerms from './setting/legalTerms'
import websitePages from './setting/websitePages'
import minimumOrder from './setting/minimumOrder'
import bankTransfer from './setting/bankTransfer'
import customerPortal from './setting/customerPortal'
import marketplace from './setting/marketplace'
import revisedOffer from './setting/revisedOffer'
import receipts from './setting/receipts'

const state = () => ({
  imageLabel: null,
  paymentMethods: [],
  countries: []
})

const getters = {}

const mutations = {
  SET_PAYMENT_METHODS(state, payload) {
    state.paymentMethods = payload.data?.data?.map((item) => ({
      ...item,
      component: item.is_custom ? 'Custom' : item.name.replace(/ /g, '')
    }))
  },
  SET_IMAGE_LABEL(state, payload) {
    state.imageLabel = payload
  },
  SET_COUNTRIES(state, payload) {
    state.countries = payload
  }
}

const actions = {
  async paymentMethods({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/payment-method`,
      method: 'get'
    })

    commit('SET_PAYMENT_METHODS', response)
    return response
  },
  async imageLabel({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/image-label`,
      method: 'get',
      params: payload.data
    })

    commit('SET_IMAGE_LABEL', response)
  },
  async countries({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/country`,
      method: 'get'
    })
    commit('SET_COUNTRIES', response)
    return response
  },
  async update(_, payload) {
    const response = await request({
      url: `/v1/pos/settings/update`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async mail() {
    const response = await request({
      url: `/v1/pos/settings/mail`,
      method: 'get'
    })
    return response
  },
  async sendEmailVerification(_, payload) {
    const response = await request({
      url: `/v1/pos/settings/verify-email/send`,
      method: 'post',
      data: payload
    })
    return response
  },
  async checkEmailVerification(_, payload) {
    return await request({
      url: `/v1/pos/settings/verify-email/check`,
      method: 'post',
      data: payload
    })
  },
  async getMarketPlaceSetting() {
    return await request({
      url: '/v1/pos/settings/marketplace',
      method: 'get'
    })
  },
  async updateMarketPlaceSetting(_, payload) {
    return await request({
      url: '/v1/pos/settings/marketplace',
      method: 'put',
      data: {
        ...payload,
        '_method': 'PUT'
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
  modules: {
    companyProfile,
    manageUser,
    manageLocation,
    buybackDevice,
    plan,
    checkSetting,
    condition,
    paymentMethod,
    widget,
    manageShipping,
    buybackReceipt,
    emailTemplate,
    workflow,
    reviewEmail,
    generalSettings,
    seo,
    inventoryCondition,
    couponCode,
    faq,
    legalTerms,
    websitePages,
    minimumOrder,
    bankTransfer,
    customerPortal,
    marketplace,
		revisedOffer,
    receipts
  }
}
