import request from 'utils/request'

const state = () => ({
  processing: {
    progress: 0
  },
  latestISBNUploadedFile: undefined
})
const mutations = {
  SET_PROCESSING_PROGRESS(state, data) {
    state.processing.progress = data.progress || 0
  },
  SET_LATEST_ISBN_UPLOAD(state, data) {
    state.latestISBNUploadedFile = data
  }
}
const actions = {
  async getLatestISBNUploadedFile({ commit, state }) {
    const response = await request({
      method: 'get',
      url: '/v1/pos/pricing/automatically-data'
    })

    commit('SET_LATEST_ISBN_UPLOAD', response)

    return response
  },
  async getInvalidISBNData() {
    return await request({
      method: 'get',
      url: '/v1/pos/pricing/automatically-data-invalid'
    })
  },
  async removeUploadedFile() {
    return await request({
      url: '/v1/pos/pricing/upload-pricing',
      method: 'post',
      data: {
        csv: 0
      }
    })
  },
  async downloadTemplateFile() {
    return await request({
      url: '/v1/pos/pricing/automatically-data-sample',
      method: 'get',
      responseType: 'arraybuffer'
    })
  },
  async processISBNUploadedFile() {
    return await request({
      url: '/v1/pos/pricing/start-automatically-pricing',
      method: 'post'
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
