import request from 'utils/request'
import book from './product/book'

const state = () => ({})

const actions = {
  async search(_, params) {
    const newParams = {...params}
    if (params.cancelToken) delete newParams.cancelToken

    const response = await request({
      url: '/v1/buyback/devices/search',
      method: 'get',
      params: newParams,
      cancelToken: params.cancelToken
    })
    return response
  }
}

export default {
  namespaced: true,
  state,
  actions,
  modules: {
    book
  }
}
