import request from 'utils/request'

const state = () => ({
  pricing_unique_link: null,
  marketplace_selling_type: null
})

const getters = {}

const mutations = {
  SET_COMPANY_PRICING_UNIQUE_LINK(state, link = null) {
    if (!link) return
    state.pricing_unique_link = link
  },
  SET_COMPANY_MARKETPLACE(state, payload) {
    state.marketplace_selling_type = payload
  }
}

const actions = {
  async show({ commit }, payload) {
    const response = await request({
      url: `/v1/config/company`,
      method: 'get',
      params: payload
    })
    commit('SET_COMPANY_PRICING_UNIQUE_LINK', response?.unique_link)
    commit('SET_COMPANY_MARKETPLACE', response.marketplace_selling_type)
    return response
  },
  async showByToken({}, payload) {
    const response = await request({
      url: `/v1/config/company/show`,
      method: 'get',
      params: payload
    })
    return response
  },
  async showUpdate({}, payload) {
    return await request({
      url: `/v1/config/company`,
      method: 'post',
      data: payload,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  async showStoreOpenHours() {
    return await request({
      url: `/v1/pos/settings/page-hours`,
      method: 'get'
    })
  },
  async updateStoreOpenHours(_, payload) {
    await request({
      url: `v1/pos/settings/page-hours`,
      method: 'post',
      data: {
        data: payload
      }
    })
  },
  async updateStoreOpenHoursIndividual(_, payload) {
    await request({
      url: `v1/pos/settings/update`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
  },
  async closeCompany(_, payload) {
    return await request({
      url: `/v1/config/company`,
      method: 'delete',
      data: {
        reason_close: 'Close My Account'
      }
    })
  },
  async unlinkAccount({}, payload) {
    return await request({
      url: `/v1/config/company/unlink`,
      method: 'post',
      data: payload
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
