<script>
import { defineComponent, h, ref } from 'vue'
import { mixinColor, mixinView } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Alert',
  mixins: [
    ...addMixinPrefix([
      mixinColor,
      mixinView()
    ], 'alert')
  ],
  setup(props, { slots }) {
    const views = ref(['smooth', 'filled', 'outlined'])
    const genContent = () => {
      return h('div', {
        class: 'alert__content'
      }, slots.default())
    }
    const genContentLeft = () => {
      return h('div', {
        class: 'alert__content-left'
      }, slots.left())
    }
    const genContentRight = () => {
      return h('div', {
        class: 'alert__content-right'
      }, slots.right())
    }

    return {
      views,
      genContent,
      genContentLeft,
      genContentRight
    }
  },
  render() {
    const children = [
      this.$slots.left && this.genContentLeft(),
      this.genContent(),
      this.$slots.right && this.genContentRight()
    ]
    const listClass = [
      'alert',
      this.classView,
      this.classColor
    ]

    return h('div', {
      class: listClass
    }, children)
  }
})
</script>