import request  from 'utils/request'

const state = () => ({
  show: {}
})

const mutations = {
  SET_SHOW_MINIMUM_ORDER(state, payload) {
    state.show = payload
  }
}

const actions = {
  async showMinimumOrder({ commit }) {
    const response = await request({
      url: '/v1/pos/settings/minimum-order',
      method: 'get',
    })

    commit('SET_SHOW_MINIMUM_ORDER', response.data)
  },
  async updateMinimumOrder({commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/minimum-order',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
