import { colors } from '../util/variable'

export default (prefix = null) => ({
  props: {
    color: {
      type: String,
      default: 'blue'
    }
  },
  computed: {
    isColorExist() {
      return Object.keys(colors()).includes(this.color)
    },
    classColor() {
      return this.isColorExist ? `${prefix + '-' || ''}${this.color}` : ''
    }
  }
})