<template>
  <div
    ref="refSelectParent"
    v-outside="handleClickOutside"
    @keydown.esc="isCollapse = false"
    :class="[
      'select',
      block && '--block',
      disabled && '--disabled',
      loading && '--loading',
      error && '--error'
    ]"
  >
    <div
      class="select__head"
      @click="handleToggleCollapse"
      @keydown.space="isCollapse = true"
      tabindex="0"
    >
      <div v-if="$props.multiple">
        <slot v-bind="{ selectedOption: selectedMultiple.length > 1 ? selectedMultiple : selectedMultiple[0] || {} }" name="selected">
          <span v-if="!selectedMultiple?.length" style="letter-spacing: normal" :class="!selectedOption?.length && 'text-grey-02'">
            {{ placeholder }}
          </span>
          <span v-else-if="!selectedAll" style="letter-spacing: normal">
            {{ selectedMultiple?.length }} Selected
          </span>
          <span v-else style="letter-spacing: normal">
            All Locations
          </span>
        </slot>
      </div>
      <div v-else>
        <slot v-bind="{ selectedOption }" name="selected">
          <span style="letter-spacing: normal" :class="!selectedOption[name] && 'text-grey-02'">
            {{ selectedOption[name] ? selectedOption[name] : placeholder }}
          </span>
        </slot>
      </div>
      <div class="relative">
        <transition name="fade">
          <Loading v-if="loading"></Loading>
          <i v-else class="material-icons">keyboard_arrow_down</i>
        </transition>
      </div>
    </div>
    <teleport to="body">
      <transition name="fade">
        <div
          ref="refSelectSheet"
          v-show="isCollapse"
          :class="[
            'select__sheet',
            $props.noBorder ? 'select__sheet--noborder' : '',
            loading ? '--loading' : ''
          ]"
        >
          <div v-if="$slots.prepend" class="select__prepend">
            <slot name="prepend"></slot>
          </div>
          <div v-if="searchable" class="p-4">
            <Input v-model="query" placeholder="Search" icon icon-left block>
              <template #icon>
                <i class="material-icons">search</i>
              </template>
            </Input>
          </div>
          <div class="select__options-wrapper relative">
            <transition name="fade">
              <div
                v-if="loading"
                class="absolute w-full h-full left-0 top-0"
                style="background-color: rgba(0, 0, 0, 0.1); z-index: 1;"
              >
                <Loading />
              </div>
            </transition>
            <div
              class="select__options relative"
              :style="{ maxHeight: maxHeight }"
            >
              <slot name="sheet">
                <div v-if="filteredItems.length > 0">
                  <template v-if="groupBy">
                    <div v-for="(group, groupIndex) of filteredItems" :key="groupIndex">
                      <slot name="group-prepend" v-bind="{ group }"></slot>
                      <div
                        v-for="(item, itemIndex) of group[groupBy]"
                        :key="itemIndex"
                        :class="[
                          'select__option',
                          item[disabledItem] && '--disabled'
                        ]"
                        @click="
                          (_) => !item[disabledItem] && handleSelectOption(item)
                        "
                        @keydown.space="handleSelectOption(item)"
                        tabindex="0"
                      >
                        <slot name="item" v-bind="{ item }">
                          {{ name ? item[name] : item }}
                        </slot>
                      </div>
                    </div>
                  </template>
                  <template v-else-if="$props.checkbox">
                    <ElCheckbox
                      v-model="selectedAll"
                      :indeterminate="isIndeterminate"
                      class="block select__option"
                      @change="handleSelectOptionAll">
                      <slot name="item-all" />
                    </ElCheckbox>
                    <div class="border-b border-grey-01 mb-4" />
                    <ElCheckboxGroup
                      v-model="selectedMultiple"
                      @change="handleSelectOption">
                      <ElCheckbox
                        v-for="(item, itemIndex) of filteredItems"
                        :key="itemIndex"
                        :label="item[$props.by]"
                        :disabled="item.disabled"
                        class="block select__option">
                        <slot name="item" v-bind="{ item }">
                          {{ name ? item[name] : item }}
                        </slot>
                      </ElCheckbox>
                    </ElCheckboxGroup>
                  </template>
                  <template v-if="$props.optgroup">
                    <div
                      v-for="(item, itemIndex) of filteredItems"
                      :key="itemIndex">
                      <div class="select__option__group">
                        <slot name="item" v-bind="{ item }">
                          {{ name ? item[name] : item }}
                        </slot>
                      </div>
                      <template v-if="item[$props.child]?.length">
                        <div
                          v-for="(childItem, nChild) in item[$props.child]"
                          :key="`${itemIndex}_${nChild}`"
                          v-bind="{
                            class: [
                              'select__option',
                              childItem[disabledItem] || childItem.disabled && '--disabled'
                            ],
                            ...(!$props.optgroupCheckbox && {
                              onclick: (_) =>
                                !childItem[disabledItem] &&
                                !childItem.disabled &&
                                handleSelectOption(childItem, item, itemIndex, nChild)
                            }),
                            ...($props.optgroupCheckbox && {
                              style: {
                                padding: '0 !important',
                                ...(
                                  (childItem[disabledItem] || childItem.disabled) &&
                                  $props.multiple &&
                                  $props.optgroupCheckbox && {
                                    backgroundColor: 'transparent !important'
                                  }
                                )
                              }
                            })
                          }"
                          tabindex="0">
                          <div
                            v-if="$props.optgroupCheckbox"
                            class="w-full px-6 py-4">
                            <Checkbox
                              v-model="childItem.checked"
                              class="block w-full"
                              :disabled="childItem.disabled"
                              @change="
                                (_) => !childItem[disabledItem] && !childItem.disabled && handleSelectOption(childItem, item, itemIndex, nChild)
                              ">
                              <div class="font-medium">
                                <slot name="child" v-bind="{ child, childItem, childIndex: nChild }">
                                  {{ name ? childItem[name] : childItem }}
                                </slot>
                              </div>
                            </Checkbox>
                          </div>
                          <slot v-else name="child" v-bind="{ child, childItem, childIndex: nChild }">
                            {{ name ? childItem[name] : childItem }}
                          </slot>
                        </div>
                      </template>
                      <template v-else>
                        <div
                          class="select__option"
                          style="background-color: transparent !important; padding-top: 2px; padding-bottom: 2px;">
                          <slot name="noChild" v-bind="{ item }">
                            No Data Show
                          </slot>
                        </div>
                      </template>
                    </div>
                  </template>
                  <template v-else>
                    <div
                      v-for="(item, itemIndex) of filteredItems"
                      :key="itemIndex"
                      :class="[
                        'select__option',
                        item[disabledItem] || item.disabled && '--disabled',
                      ]"
                      @click="
                        (_) => !item[disabledItem] && !item.disabled && handleSelectOption(item)
                      "
                      @keydown.space="handleSelectOption(item)"
                      tabindex="0"
                    >
                      <slot name="item" v-bind="{ item }">
                        {{ name ? item[name] : item }}
                      </slot>
                    </div>
                  </template>
                </div>
                <div v-else class="p-6">
                  <slot name="empty">
                    <span>No data to show</span>
                  </slot>
                </div>
              </slot>
            </div>
          </div>
          <div v-if="$slots.append" class="select__prepend">
            <slot name="append" v-bind="{ hide: handleClickOutside }"></slot>
          </div>
        </div>
      </transition>
    </teleport>
  </div>
</template>

<script>
import {
  computed,
  ref,
  defineComponent,
  watch,
  reactive,
  nextTick,
  onMounted,
  onUnmounted
} from 'vue'

import Loading from 'bbUi/components/loading/loading.vue'
import Input from 'bbUi/components/input/input.vue'

import { ElCheckbox, ElCheckboxGroup } from 'element-plus'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

import { isObject } from 'utils/is'
import debounce from 'utils/debounce'

export default defineComponent({
  components: {
    Loading,
    Input,
    Checkbox,
    ElCheckbox,
    ElCheckboxGroup
  },
  props: {
    modelValue: {
      type: [String, Object, Number, Boolean, Array]
    },
    items: {
      type: Array,
      default: () => []
    },
    block: {
      type: Boolean,
      default: false
    },
    child: {
      type: String,
      default: 'child'
    },
    name: {
      type: String,
      default: 'name'
    },
    by: {
      type: String,
      default: 'value'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    disabledItem: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    loading: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: String,
      default: '100px'
    },
    noBorder: {
      type: Boolean,
      default: false
    },
    groupBy: {
      type: String,
      default: null
    },
    selectedAll: {
      type: Boolean,
      default: false
    },
    checkbox: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: false
    },
    absolute: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    searchable: {
      type: Boolean,
      default: false
    },
    error: {
      type: Boolean,
      default: false
    },
    optgroup: {
      type: Boolean,
      default: false
    },
    optgroupBy: {
      type: String,
      default: null
    },
    optgroupCheckbox: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'change', 'change-selected-all', 'close', 'select-option', 'update:items'],
  setup(props, { emit }) {
    const refSelectParent = ref(null)
    const refSelectSheet = ref(null)
    const isCollapse = ref(false)
    const selected = ref(props.modelValue)
    const isIndeterminate = ref(false)
    const selectedAll = ref(props.selectedAll)
    const selectedMultiple = ref(props.modelValue || [])
    const selectedState = reactive({
      [props.by]: null,
      [props.name]: null
    })
    const lastSelectedState = reactive({
      [props.by]: null,
      [props.name]: null
    })
    const items = ref([])
    const query = ref('')

    const selectedOption = computed(() => {
      let option = {
        [props.name]: null,
        [props.by]: null
      }

      if ((!props.modelValue) || (isObject(props.modelValue) && !props.modelValue[props.by])) {
        return option
      }

      const filteredOption = items.value.find((item) => {
        return isObject(selected.value)
          ? item[props.by] === selected.value[props.by]
          : item[props.by] === selected.value
      })

      if (props.groupBy || props.optgroup)
        items.value.forEach((item) => {
          const matchItem = item[props.groupBy || props.optgroupBy].find((childItem) =>
            isObject(selected.value)
              ? childItem[props.by] === selected.value[props.by]
              : childItem[props.by] === selected.value
          )
          if (matchItem) {
            option = matchItem

            return
          }
        })

      if (filteredOption) option = filteredOption
      if (selectedState[props.by] && selectedState[props.name])
        option = selectedState
      if (!option[props.name] && !option[props.by]) {
        option[props.by] = isObject(props.modelValue)
          ? props.modelValue[props.by]
          : props.modelValue
        option[props.name] = isObject(props.modelValue)
          ? props.modelValue[props.name]
          : props.modelValue
      }

      return option
    })
    const filteredItems = computed(() => {
      if(props.groupBy) return props.items

      if (!query.value.split('').length) return props.items

      return props.items.filter((item) => {
        if(item.hasOwnProperty(props.name) && item.hasOwnProperty(props.by)) {
          return (
            item[props.name]?.toLowerCase?.().indexOf(query.value.toLowerCase()) >
              -1 ||
            item[props.by]?.toLowerCase?.().indexOf(query.value.toLowerCase()) >
              -1
          )
        }
        return false
      })
    })

    const calculateSheetPosition = (e) => {
      if (refSelectParent.value && refSelectSheet.value) {
        const {
          left: parentLeft,
          top: parentTop,
          height: parentHeight,
          width: parentWidth
        } = refSelectParent.value.getBoundingClientRect()
        const { height: sheetHeight } =
          refSelectSheet.value.getBoundingClientRect()
        const viewportHeight = window.innerHeight

        const isOutsideViewportY = (parentTop + sheetHeight + 80) < 0 || (parentTop + sheetHeight + 80) > viewportHeight

        let calculatedSheetPositionY = ''

        if (props.absolute) {
          if (isOutsideViewportY) refSelectParent.value.click()

          calculatedSheetPositionY = `${parentTop + parentHeight + 10}px`
        } else {
          calculatedSheetPositionY = `${
            isOutsideViewportY
              ? parentTop - sheetHeight - 10
              : parentTop + parentHeight + 10
          }px`
        }

        const calculatedSheetPositionX = `${
          window.scrollX + parentLeft + parentWidth / 2
        }px`

        if (props.absolute) refSelectSheet.value.style.position = `absolute`
        refSelectSheet.value.style.width = `${parentWidth}px`
        refSelectSheet.value.style.top = calculatedSheetPositionY
        refSelectSheet.value.style.left = calculatedSheetPositionX
      }
    }
    const handleClickOutside = (ev) => {
      if (props.disabled) return
      if (refSelectSheet.value?.contains?.(ev?.target)) return
      isCollapse.value = false
    }
    const handleToggleCollapse = () => {
      if (props.disabled) return
      isCollapse.value = !isCollapse.value
      if (isCollapse.value) {
        nextTick(() => {
          calculateSheetPosition()
        })
      }
    }
    const handleSelectOptionAll = (val) => {
      if (val) {
        selectedMultiple.value = []
        items.value.map(item => {
          selectedMultiple.value.push(item[props.by])
        })
      } else selectedMultiple.value = []
      isIndeterminate.value = false
      emit('update:modelValue', selectedMultiple.value)
      emit('change-selected-all', val)
    }
    const handleSelectOption = (option, head, indexParent, indexChild) => {
      emit('select-option', true)
      if(props.readonly) return
      if (!props.multiple) {
        isCollapse.value = false
        lastSelectedState[props.by] = selectedState[props.by]
        lastSelectedState[props.name] = selectedState[props.name]
        selectedState[props.by] = option[props.by]
        selectedState[props.name] = option[props.name]
        emit('update:modelValue', option[props.by])
        emit('change', props.optgroup ? { option, head } : option)
      } else {
        let type = 1
        const index = selectedMultiple.value.findIndex(item => item[props.by] === option[props.by])
        if (index >= 0) {
          selectedMultiple.value.splice(index, 1)
          type = 0
        } else {
          selectedMultiple.value.push(option)
        }

        const checkedCount = option.length
        selectedAll.value = checkedCount === items.value.length
        emit('change-selected-all', selectedAll.value)
        isIndeterminate.value = checkedCount > 0 && checkedCount < items.value.length
        emit('update:modelValue', selectedMultiple.value)
        emit('change', props.optgroup ? { option, head, type, index: indexParent, indexChild } : option)
      }
      emit('update:items', items.value)
    }
    const onScroll = (e) => calculateSheetPosition(e)
    const resetValue = () => {
      selectedState[props.by] = lastSelectedState[props.by]
      selectedState[props.name] = lastSelectedState[props.name]
    }
    onMounted(() => {
      calculateSheetPosition()
      window.addEventListener('scroll', debounce(onScroll, 100), true)
    })
    onUnmounted(() => window.removeEventListener('scroll', onScroll))

    watch(
      () => isCollapse.value,
      (isCollapsed) => {
        if (isCollapsed) emit('close')
      }
    )

    watch(
      () => props.modelValue,
      (newValue) => {
        selected.value = newValue
      },
      {
        immediate: true
      }
    )

    watch(() => props.items, () => {
      items.value = props.items.map((item) => {
        const isString = typeof item === 'string'
        return isString
          ? { [props.by]: item, [props.name]: item }
          : !!props.disabledItem
          ? { ...item, [props.disabledItem]: !!item[props.disabledItem] }
          : item
      })
    }, { immediate: true, deep: true })

    watch(() => props.modelValue, (val) => {
      selectedMultiple.value = val || []
    }, { immediate: true })

    watch(() => props.selectedAll, (val) => {
      selectedAll.value = val
    }, { immediate: true })

    return {
      isCollapse,
      items,
      query,
      selected,
      selectedAll,
      isIndeterminate,
      selectedMultiple,
      selectedOption,
      refSelectParent,
      refSelectSheet,
      filteredItems,
      handleClickOutside,
      handleToggleCollapse,
      handleSelectOption,
      handleSelectOptionAll,
      resetValue
    }
  }
})
</script>

<style lang="scss" scoped>
.select {
  @apply relative inline-block w-max;
  &.--block {
    @apply block w-full;
  }
  .select__head {
    @apply flex justify-between items-center w-full transition duration-300 bg-white border cursor-pointer select-none text-left;
    border-color: #dddddd;

    &:focus,
    &:active {
      @apply border-teal-04;
    }
    border-radius: 0.5rem;
		padding: 0.75rem 1rem;
    font-weight: 400;
    letter-spacing: 0.05em;
    line-height: 1.5rem;
    text-align: center;
  }

  &.--disabled {
    .select__head {
      background-color: #f5f5f575;
      border-color: #f5f5f575;

      @apply text-grey-01 cursor-not-allowed;
      &:hover,
      &:active,
      &:focus {
        @apply border border-grey-01;
      }
    }
  }

  &.--error {
    .select__head {
      @apply border-red-04;
    }
  }
}

.select__sheet {
  @apply fixed bg-white border top-16 transform -translate-x-1/2 left-1/2 shadow-2xl rounded-lg;
  border-color: #dddddd;
  max-height: 100vh;
  z-index: 2001;
  left: 50%;
  .select__options {
    @apply overflow-y-auto;
    .select__option {
			padding: 0.75rem 1rem;

      &:hover,
      &:active,
      &:focus {
        @apply bg-grey-lighten cursor-pointer;
      }
      @apply border-b text-left;
      border-color: #dddddd;
      &.--disabled {
        @apply bg-grey-lighten text-grey-03 cursor-not-allowed;
      }
    }
    .select__option__group {
      @apply px-6 pt-4 pb-1 border-b text-left;
      border-color: #dddddd;
      &.--disabled {
        @apply bg-grey-lighten text-grey-03 cursor-not-allowed;
      }
    }
  }
  &.--loading {
    .select__options {
      @apply overflow-y-hidden;
    }
  }
  &.select__sheet--noborder {
    .select__options .select__option, .select__option__group {
      border: 0;
    }
  }
}
</style>
