<script>
import { defineComponent, h, ref, toRef, onMounted, onBeforeUnmount, Transition } from 'vue'
import { RouterLink } from 'vue-router'
import { useRouter } from 'vue-router'
import { mixinColor, mixinView, mixinSize } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'
import { on, off } from '../../util/dom'

export default defineComponent({
  name: 'Modal',
  mixins: [
    ...addMixinPrefix([
      mixinColor,
      mixinView('raised'),
      mixinSize
    ], 'modal')
  ],
  props: {
    modelValue: Boolean,
    title: {
      type: String,
      default: ''
    },
    hideHeader: {
      type: Boolean,
      default: false
    },
    noClose: {
      type: Boolean,
      default: false
    },
    hideFooter: {
      type: Boolean,
      default: false
    },
    closeTo: {
      type: Object,
      default: null
    },
    withoutTransition: {
      type: Boolean,
      default: false
    },
    noPadding: {
      type: Boolean,
      default: false
    },
    bodyScroll: {
      type: Boolean,
      default: true
    }
  },
  emits: [
    'update:modelValue',
    'close',
    'click-close'
  ],
  setup(props, { slots, emit }) {
    const value = toRef(props, 'modelValue')
    const views = ref(['smooth', 'raised'])
    const router = useRouter()
    const changeValue = data => {
      emit('update:modelValue', data)
    }
    const contentClassHeader = () => {
      if(props.hideHeader) return 'modal__content--rounded-top'
      return ''
    }
    const contentClassFooter = () => {
      if(props.hideFooter) return 'modal__content--rounded-bottom'
      return ''
    }
    const genContent = () => {
      return h('div', {
        class: `modal__content ${contentClassHeader()} ${contentClassFooter()} ${props.noPadding ? 'modal__content--no-padding' : ''}`,
        style: {
          ...(!props.bodyScroll && { overflow: 'visible' })
        }
      }, slots.default({
        close: () => changeValue(false)
      }))
    }

    const getContentTitle = () => {
      const titleChild = [
        h('h5', {
          class: 'modal__content-header-title'
        }, slots.header && slots.header() || props.title),
        h('p', {
          class: 'modal__content-header-subtitle'
        }, slots.subtitle && slots.subtitle()),
      ]

      return h('div', {
        style: {
          flexGrow: '1'
        }
      }, titleChild)
    }

    const getContentHeaderWrapper = () => {
      const headerWrapper = [
        getContentTitle(),
        h('div', {}, slots.headerRight && slots.headerRight())
      ]
      return h('div', {
        class: 'modal__content-header-wrapper'
      }, headerWrapper)
    }

    const genContentHeader = () => {
      const headerChild = [
        getContentHeaderWrapper(),
        !props.noClose && h('a', {
          href: '#',
          class: 'modal__content-header-close',
          to: props.closeTo ? props.closeTo : null,
          onClick: e => {
            e.preventDefault()
            changeValue(false)
            emit('click-close')
            if (typeof props.closeTo === 'function') {
              props.closeTo()
            } else if (props.closeTo && typeof props.closeTo === 'object') {
              router.push(props.closeTo)
            }
          }
        }, '✕')
      ]

      return h('div', {
        class: 'modal__content-header'
      }, headerChild)
    }
    const genStandaloneCloseButton = () => {
      return h('a', {
        href: '#',
        class: 'modal__standalone-close',
        onClick: e => {
          e.preventDefault()
          changeValue(false)
          emit('click-close')
          if (typeof props.closeTo === 'function') {
            props.closeTo()
          } else if (props.closeTo && typeof props.closeTo === 'object') {
            router.push(props.closeTo)
          }
        }
      }, '✕')
    }
    const genContentFooter = () => {
      return h('div', {
        class: 'modal__content-footer'
      }, slots.footer())
    }
    const onKeyDown = e => {
      // if(e.key === 'Escape') changeValue(false)
    }
    const onAfterLeave = () => {
      emit('close')
    }

    onMounted(() => {
      on(document, 'keyup', onKeyDown)
    })
    onBeforeUnmount(() => {
      off(document, 'keyup', onKeyDown)
    })

    return {
      value,
      views,
      genContent,
      genContentHeader,
      genContentFooter,
      onAfterLeave,
      genStandaloneCloseButton
    }
  },
  render() {
    const children = [
      !this.$props.hideHeader && this.genContentHeader(),
      this.genContent(),
      this.$props.hideHeader && !this.$props.noClose && this.genStandaloneCloseButton(),
      !this.$props.hideFooter && this.$slots.footer && this.genContentFooter()
    ]
    const listClass = [
      'modal',
      this.classView,
      this.classColor,
      this.classSize
    ]
    let modal

    if(this.value) modal = h('div', {
      class: listClass
    }, h('div', {
      class: 'modal-dialog'
    }, children))

    if(this.$props.withoutTransition) return modal 

    // TODO: idk but transition didn't work
    return h(Transition, {
      name: 'fade',
      onAfterLeave: this.onAfterLeave
    }, {
      default: () => modal
    })
    // return modal
  }
})
</script>