import request  from 'utils/request'

const state = () => ({
})

const getters = {
}

const mutations = {
  SET_DEVICE(state, payload) {
    state.devices = payload
  },
  SET_DEVICE_CHILD(state, payload) {
    state.devices.data[payload.index] = {
      ...state.devices.data[payload.index],
      ...payload.data
    }
  }
}

const actions = {
  async brand(_, payload) {
    const url = '/v1/pos/pricing/get-devices'

    const response = await request({
      url: url,
      method: 'get',
      params: payload,
    })

    return response
  },
  async brandChild({ commit }, payload) {
    const url = `/v1/pos/pricing/get-devices/${payload.id}`
    const response = await request({
      url: url,
      method: 'get'
    }).then(res => res)
    .catch(() => {})

    return response
  },
  async categoryConditionUpdate({}, payload) {
    return await request({
      url: `/v1/pos/condition/update-subcondition/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async device({ commit }, payload) {
    return await request({
      url: '/v1/pos/pricing/get-prices',
      method: 'get',
      params: {
        last_child_id: payload.id,
        ...payload.params
      }
    })
  },
  async deviceUpdate({commit}, payload){
    return await request({
      url: '/v1/pos/pricing/set-pricing',
      method: 'post',
      data: payload
    })
  },
  async deviceUpdateRoundup({ commit }, payload) {
    return await request({
      url: '/v1/pos/pricing/set-bulk-pricing-round-up',
      method: 'post',
      data: payload
    })
  },
  async setRule({}, payload) {
    const response = await request({
      url: '/v1/pos/pricing/set-rule',
      method: 'post',
      data: payload
    })
    return response
  },
  async unsetRule({}, payload){
    const response = await request({
      url: '/v1/pos/pricing/unset-rule',
      method: 'post',
      data: payload
    })
    return response
  },
  async resetCustomPrice({}, payload){
    return await request({
      url: '/v1/pos/pricing/reset-custom-price',
      method: 'post',
      data: payload
    })
  },
  async resetBulkCustomPrice({}, payload){
    return await request({
      url: '/v1/pos/pricing/reset-custom-price-bulk',
      method: 'post',
      data: payload
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}