import request  from 'utils/request'

const state = () => ({
  marketplace_filter: undefined
})

const getters = {}

const mutations = {
  SET_MARKETPLACE_FILTER(state, payload) {
    state.marketplace_filter = payload
  }
}

const actions = {
  async getPartners({}, payload) {
    const newPayload = {}
    Object.keys(payload).forEach(key => {
      if (key !== 'cancelToken') newPayload[key] = payload[key]
    })
    return await request({
      url: `/v1/pos/settings/marketplace/marketers`,
      method: 'get',
      params: newPayload,
      ...(payload.cancelToken && { cancelToken: payload.cancelToken })
    })
  },
  async getPartnerSummary({}, payload) {
    return await request({
      url: `/v1/pos/settings/marketplace/marketers/device-conditions-summary/${payload.id}`,
      method: 'get',
      ...(payload.cancelToken && { cancelToken: payload.cancelToken })
    })
  },
  async getConditionMapping({}, payload) {
    return await request({
      url: `/v1/pos/settings/marketplace/marketers/conditions-mapping/${payload.id}`,
      method: 'get',
      params: {
        category_id: payload.category_id,
        is_mac: payload.is_mac || 0
      }
    })
  },
  async updateConditionMapping({}, payload) {
    return await request({
      url: `/v1/pos/settings/marketplace/marketers/conditions-mapping/${payload.id}`,
      method: 'put',
      data: {
        ...payload.params
      }
    })
  },
  async resetAllConditionMapping({}, payload) {
    return await request({
      url: `/v1/pos/settings/marketplace/marketers/device-conditions/reset/${payload.id}`,
      method: 'delete'
    })
  },
  async resetConditionMapping({}, payload) {
    return await request({
      url: `/v1/pos/settings/marketplace/marketers/conditions-mapping/reset/${payload.id}`,
      method: 'delete',
      params: {
        ...(payload.category_id && { category_id: payload.category_id }),
        ...((payload.is_mac == 1 || payload.is_mac == 0) && { is_mac: payload.is_mac })
      }
    })
  },
  async getMarketplaces({}, payload) {
    const newPayload = {}
    Object.keys(payload).forEach(key => {
      if (key !== 'cancelToken') newPayload[key] = payload[key]
    })
    return await request({
      url: `/v1/pos/settings/marketplace/partners`,
      method: 'get',
      params: newPayload,
      ...(payload.cancelToken && { cancelToken: payload.cancelToken })
    })
  },
  async getMarketLinked({}, payload) {
    return await request({
      url: `/v1/pos/settings/marketplace/partners/devices/${payload}`,
      method: 'get'
    })
  },
  async addMarketplaces ({}, payload) {
    return request({
      url: `/v1/pos/settings/marketplace/partners`,
      method: 'post',
      data: payload
    })
  },
  async updateMarketplaces ({}, payload) {
    const newPayload = {}
    Object.keys(payload).forEach(key => {
      if (key !== 'id') newPayload[key] = payload[key]
    })
    return request({
      url: `/v1/pos/settings/marketplace/partners/${payload.id}`,
      method: 'put',
      data: newPayload
    })
  },
  async deleteMarketplaces ({}, id) {
    return request({
      url: `/v1/pos/settings/marketplace/partners/${id}`,
      method: 'delete'
    })
  },
  async updateMarketSellingType({}, payload) {
    return request({
      url: `/v1/pos/settings/marketplace/marketers/selling-type`,
      method: 'put',
      data: payload
    })
  },
  async getPartnerLinkedDevice({}, payload) {
    const newPayload = {}
    Object.keys(payload).forEach(key => {
      if (key !== 'id') newPayload[key] = payload[key]
    })
    return await request({
      url: 
      `/v1/pos/settings/marketplace/marketers/device-conditions/${payload.id}`,
      // `/v1/pos/settings/marketplace/partners/devices/${id}`,
      method: 'get',
      params: {
        ...newPayload
      }
    })
  },
  async getFilterMarketers ({ commit }, payload) {
    const type = payload.type
    const params = {
      page: payload.page,
      ...(payload.search && { search: payload.search })
    }
    const response = await request({
      url: `/v1/pos/${type}/marketers`,
      method: 'get',
      params,
      ...(payload.cancelToken && { cancelToken: payload.cancelToken })
    })
    if (response.data && response.data.length) {
      commit('SET_MARKETPLACE_FILTER', [...response.data].find(item => item.selected))
    }
    return response
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
