import request from 'utils/request'

const state = () => ({
  currencies: [],
  countries: [],
  timezones: [],
  currenciesUpdate: '',
  distance: {
    current: undefined,
    units: undefined
  }
})

const mutations = {
  SET_CURRENCIES(state, payload) {
    state.currencies = payload
  },
  SET_COUNTRIES(state, payload) {
    state.countries = payload
  },
  SET_TIMEZONES(state, payload) {
    state.timezones = payload
  },
  SET_UPDATE_CURRENCIES(state, payload) {
    state.currenciesUpdate = payload
  },
  SET_DISTANCE(state, payload) {
    state.distance = payload
  }
}

const actions = {
  async currencies({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/currency`,
      method: 'get'
    })
    commit('SET_CURRENCIES', response.data)
    return response
  },
  async countries({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/country`,
      method: 'get'
    })
    commit('SET_COUNTRIES', response.data)
    return response
  },
  async timezones({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/timezone`,
      method: 'get'
    })
    commit('SET_TIMEZONES', response)
    return response
  },
  async priceLock() {
    const response = await request({
      url: `/v1/pos/settings/price-lock`,
      method: 'get'
    })
    return response
  },
  async buybackPrefix() {
    const response = await request({
      url: '/v1/pos/settings/order-number',
      method: 'get'
    })
    return response
  },
  async distanceUnits({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/distance-units/show`,
      method: 'get'
    })
    commit('SET_DISTANCE', response.data)
    return response
  },
  async updateDistanceUnits({ commit }, payload) {
    return await request({
      url: `/v1/pos/settings/distance-units`,
      method: 'put',
      data: {
        distance_unit: payload
      }
    }).then(res => {
      commit('SET_DISTANCE', payload)
      return res
    }).catch(e => e)
  },
  async update({ commit }, payload) {
    const response = await request({
      url: `v1/pos/settings/update`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })

    commit('SET_UPDATE_CURRENCIES', response)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
