import request  from 'utils/request'

  const state = () => ({
    all: [],
    show: {},
    conditions: []
})

const mutations = {
  SET_ALL(state, payload) {
    state.all = payload
  },
  SET_SHOW(state, payload){
    state.show = {
      ...payload,
      // condition_term: JSON.parse(payload.condition_term)
    }
  },
  SET_CONDITIONS(state, payload) {
    state.conditions = payload
  }
}

const actions = {
  async all({ commit }, params) {
    const response = await request({
      url: `/v1/pos/settings/inventory-condition/all`,
      method: 'get',
      params: params
    })
    commit('SET_ALL', response)
    return response
  },
  async get({commit}, params){
    const response = await request({
      url: `/v1/pos/settings/inventory-condition`,
      method: 'get',
      params: params
    })
    commit('SET_CONDITIONS', response.data)
  },
  async show({commit}, payload){
    const response = await request({
      url: `/v1/pos/settings/inventory-condition/${payload}`,
      method: 'get'
    })
    commit('SET_SHOW', response.data)
  },
  async create({}, payload){
    await request({
      url: `/v1/pos/settings/inventory-condition`,
      method: 'post',
      data: payload
    })
  },
  async update({}, payload){
    await request({
      url: `/v1/pos/settings/inventory-condition/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async delete({}, payload){
    await request({
      url: `/v1/pos/settings/inventory-condition/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async getConditions({}, params){
    return await request({
      url: `/v1/pos/inventory/condition-option`,
      method: 'get',
      params
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
