const kebabCase = (string, caseType = null) => {
  if (!!string && typeof string === 'string') {
    const parsedString = string.replace(/\s/g, '-')
    if (caseType === 'lower') return parsedString.toLowerCase()
    if (caseType === 'upper') return parsedString.toUpperCase()
    return parsedString
  }
  return string
}
const camelCase = (string) => {
  return string.replace(/(\w+)(?:\s+|$)/g, function (_, word, index) {
    return (
      (index === 0
        ? word.charAt(0).toLowerCase()
        : word.charAt(0).toUpperCase()) + word.substr(1)
    )
  })
}
const lowerCase = string => typeof string === 'string' ? string.toLowerCase() : string
const upperCase = string => typeof string === 'string' && string ? string.toUpperCase() : string
const upperCaseFirstLetter = (string) =>
  !!string && typeof string === 'string' ? `${string.charAt(0).toUpperCase()}${string.slice(1)} ` : string
const titleCase = string => typeof string === 'string' ? string.split(' ').map(word => `${word[0].toUpperCase()}${word.substring(1, word.length)}`).join(' ') : string
const limit = (string, limit = 12) =>
  string.length > limit ? String(string).substring(0, limit) + ' ...' : string

const lastURLSegment = (string) => {
  return string.substring(string.lastIndexOf('/') + 1)
}
const getFileFormat = (string) => {
  return string.substring(string.lastIndexOf('.') + 1)
}
const stringToHex = (string) => {
  if (typeof string !== 'string') return '#00000'
  var hash = 0
  if (string.length === 0) return hash
  for (var i = 0; i < string.length; i++) {
    hash = string.charCodeAt(i) + ((hash << 5) - hash)
    hash = hash & hash
  }
  var color = '#'
  for (var i = 0; i < 3; i++) {
    var value = (hash >> (i * 8)) & 255
    color += ('00' + value.toString(16)).substr(-2)
  }
  return color
}
const splitToNewLine = (
  text = '',
  max = undefined,
  options = { useSuffix: false }
) => {
  if (typeof text !== 'string') return text
  return Array.from(text).reduce((acc, character, index) =>
    index % (!max ? text.length : max)
      ? `${acc}${character}`
      : `${acc}${options.useSuffix && '-'}\n${character}`
  )
}

export {
  kebabCase,
  camelCase,
  lowerCase,
  upperCase,
  titleCase,
  limit,
  upperCaseFirstLetter,
  lastURLSegment,
  getFileFormat,
  stringToHex,
  splitToNewLine
}