import request  from 'utils/request'

const state = () => ({
  popular: {
    current: [],
    categories: []
  }
})

const getters = {
}

const mutations = {
  SET_CURRENT_POPULAR_CATEGORIES(state, payload){
    state.popular.current = payload
  },
  SET_POPULAR_CATEGORIES(state, payload){
    state.popular.categories = payload
  }
}

const actions = {
  async allCurrentPopularCategories({commit}, _) {
    const response = await request({
      url: `/v1/pos/settings/popular`,
      method: 'get'
    })
    commit('SET_CURRENT_POPULAR_CATEGORIES', response)
  },
  async allPopularCategories({commit}, params){
    const response = await request({
      url: `/v1/pos/settings/popular/category`,
      method: 'get',
      params: params
    })
    commit('SET_POPULAR_CATEGORIES', response)
  },
  async updateCurrentPopularCategories(_, payload){
    await request({
      url: `/v1/pos/settings/popular`,
      method: 'post',
      data: {
        data: payload
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}