<template>
  <div class="flex items-center mr-12">
    <div class="mr-2">
      <svg
        width="80"
        height="80"
        viewBox="-18.5 -18.5 185 185"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        style="transform: rotate(-90deg)"
      >
        {{ calculatedOffset }}
        <circle
          r="64"
          cx="74"
          cy="74"
          fill="transparent"
          stroke="#e0e0e0"
          stroke-width="12"
          :stroke-dasharray="`${baseOffset}px`"
          stroke-dashoffset="0"
        ></circle>
        <circle
          r="64"
          cx="74"
          cy="74"
          stroke="#3b82f6"
          stroke-width="12"
          stroke-linecap="round"
          :stroke-dashoffset="`${calculatedOffset}px`"
          fill="transparent"
          :stroke-dasharray="`${baseOffset}px`"
        ></circle>
        <text
          :x="
            progressPercentage < 100
              ? progressPercentage < 25
                ? '50px'
                : '42px'
              : '40px'
          "
          y="87px"
          fill="#262626"
          font-size="32px"
          font-weight="bold"
          style="
            transform: rotate(90deg) translate(0, -144px);
            text-align: center;
          "
        ></text>
      </svg>
    </div>
    <div class="mr-4">
      <p>Status: {{ status }}</p>
      <h3 class="text-xl">Data Feed</h3>
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue'
import { useStore } from 'vuex'

export default defineComponent({
  name: 'NotificationDatafeed',
  props: {
    status: {
      type: String,
      default: 'Preparing Data'
    },
    showPercentage: {
      type: Boolean,
      default: true
    }
  },
  setup() {
    const store = useStore()
    const baseOffset = ref(401.92)
    const progressPercentage = computed(() => {
      const percentage = parseInt(
        `${store.state.integration.show.datafeed.progress_percentage}`
      )
      if (isNaN(percentage)) {
        console.warn('Data feed percentage value is not a number')
        return 0
      }
      return percentage
    })
    const calculatedOffset = computed(
      () =>
        baseOffset.value - (baseOffset.value * progressPercentage.value) / 100
    )
    return {
      progressPercentage,
      baseOffset,
      calculatedOffset
    }
  }
})
</script>
<style lang="scss"></style>
