import request from 'utils/request'

const state = () => ({
  loading: {
    connect: false,
    shippingProvider: {
      fetchCarriers: false,
      updateCarrier: false
    },
    shippingKit: {
      fetch: false,
      update: false
    },
    fetchStatus: false,
    integrationChecks: false
  },
  connect: {
    credentials: {
      api_key: null
    }
  },
  shippingProvider: {
    carriers: [],
    filters: [],
    selectedCarrier: null,
    selectedCarrierCache: null
  },
  shippingKit: {
    id: null,
    key: null,
    name: null,
    sort_code: [],
    textinfo: null,
    created_at: null,
    updated_at: null,
    subject: null,
    textinfo_order: null
  },
  status: {
    api_key: '',
    is_ready: 0
  }
})

const getters = {
  selectedCarrier(state) {
    if (state.shippingProvider.selectedCarrier === null)
      return {
        carrier_code: null,
        carrier_icon: null,
        carrier_nickname: null,
        carrier_uuid: null,
        service_code: null,
        service_name: null
      }
    return state.shippingProvider.selectedCarrier
  }
}

const mutations = {
  SET_CREDENTIAL(state, credentials) {
    state.connect.credentials.api_key = credentials.api_key
  },
  SET_CONNECT_LOADING(state, loadingState = false) {
    state.loading.connect = loadingState
  },
  SET_LOADING_FETCH_CARRIERS(state, loadingState = false) {
    state.loading.shippingProvider.fetchCarriers = loadingState
  },
  SET_LOADING_FETCH_SHIPPING_KIT(state, loadingState = false) {
    state.loading.shippingKit.fetch = loadingState
  },
  SET_LOADING_UPDATE_CARRIER(state, loadingState = false) {
    state.loading.shippingProvider.updateCarrier = loadingState
  },
  SET_LOADING_UPDATE_SHIPPING_KIT(state, loadingState = false) {
    state.loading.shippingKit.update = loadingState
  },
  SET_LOADING_INTEGRATION_CHECKS(state, loadingState = false) {
    state.loading.integrationChecks = loadingState
  },
  SET_LOADING_FETCH_STATUS(state, loadingState = false) {
    state.loading.status = loadingState
  },
  SET_CARRIERS(state, carriers) {
    state.shippingProvider.selectedCarrier = null
    state.shippingProvider.carriers = carriers
  },
  SET_CARRIER_FILTERS(state, filters = []) {
    state.shippingProvider.filters = []
    state.shippingProvider.filters = filters
  },
  SET_SELECTED_CARRIER(state, carrier = null) {
    if (!carrier) return
    state.shippingProvider.selectedCarrier = carrier
  },
  SET_SELECTED_CARRIER_BY_UUID(state, carrierUUID = null) {
    if (!carrierUUID) return
    const selectedCarrier = state.shippingProvider.carriers.find(
      (carrier) => carrier.carrier_uuid === carrierUUID
    )
    if (!selectedCarrier) return
    state.shippingProvider.selectedCarrier = selectedCarrier
  },
  SET_SELECTED_CARRIER_CACHE_BY_UUID(state, carrierUUID = null) {
    if (!carrierUUID) return
    const selectedCarrier = state.shippingProvider.carriers.find(
      (carrier) => carrier.carrier_uuid === carrierUUID
    )
    if (!selectedCarrier) return
    state.shippingProvider.selectedCarrierCache = selectedCarrier
  },
  SET_SHIPPING_KIT(state, data) {
    state.shippingKit = data
  },
  SET_STATUS(state, data) {
    state.status = data
  },
  RESET_SELECTED_CARRIER(state) {
    state.shippingProvider.selectedCarrier =
      state.shippingProvider.selectedCarrierCache || null
  }
}

const actions = {
  async fetchStatus({ commit }) {
    const response = await request({
      url: `/v1/pos/integrations/shipengine`,
      method: 'get'
    })
    commit('SET_STATUS', response)
  },
  async connect({ state }) {
    await request({
      url: `/v1/pos/integrations/shipengine/connection`,
      method: 'post',
      data: state.connect.credentials
    })
  },
  async fetchCarriers({ commit }) {
    const response = await request({
      url: `/v1/pos/integrations/shipengine/carriers`,
      method: 'get'
    })
    if (response.carriers) {
      const carriers = response.carriers.map((carrier) => ({
        carrier_id: carrier.carrier_id,
        carrier_code: carrier.carrier_code,
        carrier_icon: carrier.icon,
        carrier_name: carrier.name,
        carrier_nickname: carrier.nickname,
        carrier_uuid: `${carrier.carrier_id}|${carrier.service_code}`,
        service_code: carrier.service_code,
        service_name: carrier.name,
        is_selected: carrier.is_selected,
        friendly_name: carrier.friendly_name,
        disabled: carrier.return_label_supported === 0
      }))
      const selectedCarrier = carriers.filter(
        (carrier) => carrier.is_selected === 1
      )
      commit('SET_CARRIERS', carriers)
      if (selectedCarrier.length === 1) {
        commit('SET_SELECTED_CARRIER_BY_UUID', selectedCarrier[0].carrier_uuid)
        commit(
          'SET_SELECTED_CARRIER_CACHE_BY_UUID',
          selectedCarrier[0].carrier_uuid
        )
      }
    }
    if (response.filters) {
      commit('SET_CARRIER_FILTERS', response.filters)
    }
  },
  async updateCarrier({ state }) {
    if (!state.shippingProvider.selectedCarrier) return
    await request({
      url: `/v1/pos/integrations/shipengine/default-label`,
      method: 'post',
      data: {
        carrier_id: state.shippingProvider.selectedCarrier.carrier_id,
        carrier_name: state.shippingProvider.selectedCarrier.friendly_name
          ? state.shippingProvider.selectedCarrier.friendly_name
          : state.shippingProvider.selectedCarrier.carrier_name,
        service_code: state.shippingProvider.selectedCarrier.service_code,
        service_name: state.shippingProvider.selectedCarrier.service_name
      }
    })
  },
  async fetchShippingKit({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/text-info/shipengine-shipping-kit`,
      method: 'get'
    })
    commit('SET_SHIPPING_KIT', response)
  },
  async updateShippingKit({ state }) {
    await request({
      url: `/v1/pos/settings/text-info/shipengine-shipping-kit`,
      method: 'put',
      data: state.shippingKit
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
