import { reactive, computed } from 'vue'
import store from 'store'
import { isFunction } from 'utils/is'
import { anchorDownload } from 'utils/element'

const state = reactive({
  data: {
    ISBN: {
      latestUploadedFile: {
        last_update_data: null,
        pricing_data_filename: null,
        pricing_data_real_filename: null,
        is_file_already_used: 0,
        data_process_notification: 0,
        is_pricing_queue: 0
      },
      invalidData: {
        total: 0,
        rows: [],
        fileUrl: null,
        lastUpdatedAt: null
      }
    }
  },
  loading: {
    ISBN: {
      getUploadedFile: false,
      getInvalidData: false,
      deleteUploadedFile: false,
      downloadTemplateFile: false,
      processUploadedFile: false,
      closeResultNotification: false
    }
  }
})

export const useISBN = () => {
  const processingStatus = computed(() => store.state.product.book.processing)
  const getLatestUploadedFile = async (callback = () => {}) => {
    try {
      state.loading.ISBN.getUploadedFile = true
      const response = await store.dispatch(
        'product/book/getLatestISBNUploadedFile'
      )
      state.data.ISBN.latestUploadedFile = response
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.loading.ISBN.getUploadedFile = false
    }
  }
  const getInvalidData = async (callback = () => {}) => {
    try {
      state.loading.ISBN.getInvalidData = true
      const response = await store.dispatch('product/book/getInvalidISBNData')
      state.data.ISBN.invalidData.total = response?.total_data || 0
      state.data.ISBN.invalidData.rows = response?.data || []
      state.data.ISBN.invalidData.fileUrl = response?.file_url || null
      state.data.ISBN.invalidData.lastUpdatedAt = response?.last_update_data || null
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.loading.ISBN.getInvalidData = false
    }
  }
  const deleteUploadedFile = async (callback = () => {}) => {
    try {
      state.loading.ISBN.deleteUploadedFile = true
      await store.dispatch('product/book/removeUploadedFile')
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.loading.ISBN.deleteUploadedFile = false
    }
  }
  const downloadTemplateFile = async (callback = () => {}) => {
    try {
      state.loading.ISBN.downloadTemplateFile = true
      const response = await store.dispatch('product/book/downloadTemplateFile')
      anchorDownload(response, 'template-isbn', 'text/csv')
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.loading.ISBN.downloadTemplateFile = false
    }
  }
  const processUploadedFile = async (callback = () => {}) => {
    try {
      state.loading.ISBN.processUploadedFile = true
      await store.dispatch('product/book/processISBNUploadedFile')
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.loading.ISBN.processUploadedFile = false
    }
  }
  const closeResultNotification = async (callback = () => {}) => {
    try {
      state.loading.ISBN.closeResultNotification = true
      await store.dispatch('setting/update', {
        data_process_notification: 0
      })
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      state.loading.ISBN.closeResultNotification = false
    }
  }
  return {
    state: state.data.ISBN,
    loading: state.loading.ISBN,
    processingStatus,
    getLatestUploadedFile,
    getInvalidData,
    deleteUploadedFile,
    downloadTemplateFile,
    processUploadedFile,
    closeResultNotification
  }
}
