import { createApp } from 'vue'
import { createPinia } from 'pinia'
import { initializeMap, initializeTag, initializeTagManager, initializeTagFrame } from '@/utils/google-helper.js'
import store from 'store'
import router from 'router'
import * as Sentry from '@sentry/vue'
import { Integrations } from '@sentry/tracing'
import 'config/permission'
import { ElTable, ElTableColumn, ElDropdown, ElDropdownItem, ElDropdownMenu, ElDatePicker, ElMessage, ElLoading, ElPopover, ElRadioGroup, ElTooltip, ElTimePicker } from 'element-plus'
import CKEditor from '@ckeditor/ckeditor5-vue'
import RadioButton from '@/components/RadioButton.vue'
import './vendor/userpilot'
import Maska from 'maska'
import { whiteLabelConfig, observeMutation } from 'utils/white-label'

import App from '@/App.vue'
import 'element-plus/lib/theme-chalk/el-icon.css'
import 'element-plus/lib/theme-chalk/el-table.css'
import 'element-plus/lib/theme-chalk/el-table-column.css'
import 'element-plus/lib/theme-chalk/el-dropdown.css'
import 'element-plus/lib/theme-chalk/el-dropdown-item.css'
import 'element-plus/lib/theme-chalk/el-dropdown-menu.css'
import 'element-plus/lib/theme-chalk/el-date-picker.css'
import 'element-plus/lib/theme-chalk/el-message.css'
import 'element-plus/lib/theme-chalk/el-popover.css'
import 'element-plus/lib/theme-chalk/el-loading.css'
import 'element-plus/lib/theme-chalk/el-radio-group.css'
import 'element-plus/lib/theme-chalk/el-radio-button.css'
import 'element-plus/lib/theme-chalk/el-tooltip.css'
import 'element-plus/lib/theme-chalk/el-time-picker.css'
import 'vue-multiselect/dist/vue-multiselect.css'
import 'nprogress/nprogress.css'
import 'styles/main.scss'
import 'vue3-pdf-app/dist/icons/main.css'

// for remove error console from google need callback
const googles = () => {
  initializeTag('https://www.googletagmanager.com/gtag/js', 'UA-231665515-1')
  initializeTag('https://www.googletagmanager.com/gtag/js', 'G-XZ0QVFZYEV')
  initializeTagManager('GTM-PZQGKK2M')
  initializeTagFrame('GTM-PZQGKK2M')
}

const initGoogleMap = () => {
  return new Promise((resolve) => initializeMap(() => resolve(true)))
}

;(async () => {
  googles()
  await Promise.all([initGoogleMap(), whiteLabelConfig()])

  const app = createApp(App)
	const pinia = createPinia()
  if (import.meta.env.PROD) {
    Sentry.init({
      app,
      environment: import.meta.env.VITE_ENVIRONMENT,
      // release: `${process.env.VITE_NAME}@${process.env.VITE_VERSION}`,
      dsn: import.meta.env.VITE_SENTRY_DSN,
      integrations: [
        new Integrations.BrowserTracing({
          routingInstrumentation: Sentry.vueRouterInstrumentation(router),
          tracingOrigins: ['localhost', import.meta.env.VITE_API_URL, /^\//]
        })
      ],
      debug: import.meta.env.VITE_ENVIRONMENT !== 'production',
      tracesSampleRate:
        import.meta.env.VITE_ENVIRONMENT === 'production' ? 0.1 : 1,
      tracingOptions: {
        trackComponents: true
      },
      // Vue specific
      logErrors: import.meta.env.VITE_ENVIRONMENT !== 'production',
      attachProps: true,
      attachStacktrace: true
    })
  }

	app.use(pinia)
	app.use(store)
  app.use(router)
  app.use(ElMessage)
  app.use(ElLoading)
  app.use(CKEditor)
  app.use(Maska)
  app.component('Table', ElTable)
  app.component('TableColumn', ElTableColumn)
  app.component('Dropdown', ElDropdown)
  app.component('DropdownItem', ElDropdownItem)
  app.component('DropdownMenu', ElDropdownMenu)
  app.component('DatePicker', ElDatePicker)
  app.component('Popover', ElPopover)
  app.component('RadioGroup', ElRadioGroup)
  app.component('Tooltip', ElTooltip)
  app.component('Timepicker', ElTimePicker)
  app.component('RadioButton', RadioButton)

  const handleGoogleMapKeydown = (binding) => {
    const isAllowedCustomAddress = !binding.value.disallowCustomAddress
    const isAllowedCustomCountryCode = ['IE']
    const isBypassValidation = !!binding.value.bypassValidation
    const selfCountryCode = store.state.user.me?.country_code || null

    if (isBypassValidation) binding.instance.data[binding.value.valid] = true
    else if (
      isAllowedCustomAddress &&
      isAllowedCustomCountryCode.includes(selfCountryCode) &&
      selfCountryCode
    )
      binding.instance.data[binding.value.valid] =
        isAllowedCustomCountryCode.includes(selfCountryCode)
    else binding.instance.data[binding.value.valid] = false
  }
  const handleRestrictionChange = (binding) => {
    if (binding.value.restrictCountries)
      binding.value.autocomplete.componentRestrictions = {
        country: binding.value.restrictCountries
      }
  }

  app.directive('gmap', {
    beforeMount(el, binding, vnode, prevVnode) {
      const isValidBefore = binding.instance.data[binding.value.valid] === true

      if (el.tagName === 'DIV') el = el.children[1].children[0]

      let restrictedCountries = store.state.user.me?.country_code
        ? [store.state.user.me.country_code]
        : []
      if (binding.value.restrictCountries)
        restrictedCountries = binding.value.restrictCountries

      binding.value.autocomplete = new google.maps.places.Autocomplete(el, {
        types: ['address'],
        componentRestrictions: binding.value.noComponentRestriction
          ? null
          : {
              country: restrictedCountries
            }
      })

      const bindAddressComponents = async (
        addressComponents = [],
        location = {}
      ) => {
        const componentRules = {
          street_number: ['street_number'],
          route: ['street_address', 'route'],
          postal_code: ['postal_code', 'postal_code_prefix', 'plus_code'],
          administrative_area_level_1: [
            'administrative_area_level_1',
            'administrative_area_level_2',
            'administrative_area_level_3',
            'administrative_area_level_4',
            'administrative_area_level_5'
          ],
          locality: [
            'locality',
            'sublocality',
            'sublocality_level_1',
            'sublocality_level_2',
            'sublocality_level_3',
            'sublocality_level_4',
            'postal_town'
          ],
          country: ['country'],
          subpremise: ['subpremise']
        }

        // Reset component address when select new address
        for (const [_, componentKey] of Object.entries(binding.value)) {
          if (typeof componentKey === 'string')
            binding.instance.data[componentKey] = null
        }

        // let formattedAddress = ''
        for (const addressComponent of addressComponents) {
          // if (componentRules.street_number.includes(addressComponent.types[0]) || componentRules.administrative_area_level_1.includes(addressComponent.types[0])
          // ) {
          //   formattedAddress += `${addressComponent['short_name']} `
          // } else if (
          //   componentRules.route.includes(addressComponent.types[0]) ||
          //   componentRules.postal_code.includes(addressComponent.types[0]) ||
          //   componentRules.locality.includes(addressComponent.types[0])
          // ) {
          //   formattedAddress += `${addressComponent['short_name']}, `
          // } else if (componentRules.country.includes(addressComponent.types[0])) {
          //   formattedAddress += `${addressComponent['short_name']}`
          // } else if (addressComponent.types[0] === 'subpremise' && !binding.value['subpremise']) {
          //   const words = addressComponent['short_name'].split('')
          //   words[0] = words[0].toUpperCase()
          //   formattedAddress += `${words.join('')}, `
          // }

          for (const componentKey in componentRules) {
            const hasMatchedComponentRule = componentRules[
              componentKey
            ].includes(addressComponent.types[0])
            if (hasMatchedComponentRule) {
              if (
                binding.value[componentKey] !== undefined &&
                binding.instance.data.hasOwnProperty(
                  binding.value[componentKey]
                )
              ) {
                if (componentKey === 'route') {
                  const streetNumber = addressComponents.find(
                    (component) => component.types[0] === 'street_number'
                  )
                  binding.instance.data[binding.value[componentKey]] =
                    streetNumber
                      ? `${streetNumber.short_name} ${addressComponent.short_name}`
                      : addressComponent.short_name
                  continue
                }
                binding.instance.data[binding.value[componentKey]] =
                  addressComponent.short_name
              }
            }
          }
        }

        if (binding.value.formatted_address) {
          // binding.instance.data[binding.value.formatted_address] = formattedAddress
          let formattedAddress = location.formatted_address
          if(binding.instance.data?.suite) formattedAddress = formattedAddress.replaceAll(` ${binding.instance.data.suite}`, '')
          if(binding.instance.data?.subpremise) formattedAddress = formattedAddress.replaceAll(` ${binding.instance.data.subpremise}`, '')
          binding.instance.data[binding.value.formatted_address] = formattedAddress
        }
        const isExistGeoBindingValue = !!binding.value.geo
        if (isExistGeoBindingValue) {
          binding.instance.data[binding.value.geo.lat] =
            location.geometry.location.lat()
          binding.instance.data[binding.value.geo.lng] =
            location.geometry.location.lng()
        }
        binding.instance.data[binding.value.valid] = true
      }

      binding.value.autocomplete.addListener('place_changed', () => {
        const place = binding.value.autocomplete.getPlace()
        if (place) bindAddressComponents(place.address_components, place)
      })

      handleGoogleMapKeydown(binding)
      binding.instance.data[binding.value.valid] = isValidBefore // don't reset valid state if it was set to true before
      el.addEventListener('keydown', () => handleGoogleMapKeydown(binding))
    },
    updated(el, binding) {
      if (el.tagName === 'DIV') el = el.children[1].children[0]
      if (el.autocomplete !== 'no') {
        el.autocomplete = 'no'
      }
      handleRestrictionChange(binding)
    },
    unmounted(el, binding) {
      if (el.tagName === 'DIV') el = el.children[1].children[0]
      google.maps.event.clearInstanceListeners(el)
      el.removeEventListener('change', () => handleGoogleMapKeydown(binding))
      el.placeholder = ''
    }
  })

  app.directive('outside', {
    mounted: (el, binding) => {
      el.clickOutsideEvent = (event) => {
        if (!(el == event.target || el.contains(event.target))) {
          binding.value(event)
        }
      }
      document.addEventListener('click', el.clickOutsideEvent)
    },
    unmounted: (el, binding) => {
      document.removeEventListener('click', el.clickOutsideEvent)
    }
  })

  app.directive('table', {
    updated: (el, binding) => {
      const isShowSelectionAll = !!binding.value.isShowSelectionAll
      if (isShowSelectionAll) el.classList.add('--show-selection-all')
    }
  })

  app.directive('timepicker', {
    mounted: (_el, _binding, vnode) => {
      const el = vnode.el
      if (el)
        Array.from(el.getElementsByClassName('el-range-input')).forEach(
          (elRange) => elRange.setAttribute('readonly', '')
        )
    }
  })

  app.directive('intersect', {
    mounted(el, binding) {
      const observer = new IntersectionObserver(
        ([entry]) => {
          if (entry.isIntersecting) {
            binding.value() // panggil fungsi
          }
        },
        { threshold: 0.1 }
      )
      el._observer = observer
      observer.observe(el)
    },
    unmounted(el) {
      el._observer.disconnect()
    }
  })

  app.mount('#app')
  observeMutation()
})()


