import store from 'store'
import { computed, reactive, ref } from 'vue'
import { isFunction } from 'utils/is'

export const useNotification = () => {
  const state = computed(() => store.state.notification.popup)
  const openNotification = (componentName = null) => {
    store.commit('notification/OPEN_NOTIFICATION', componentName)
  }
  const closeNotification = (name) => {
    store.dispatch('notification/closeNotification', name)
  }
  const minimizeNotification = (name) => {
    store.commit('notification/MINIMIZE_NOTIFICATION', {
      isMinimize: true,
      name
    })
  }
  const maximizeNotification = (name) => {
    store.commit('notification/MINIMIZE_NOTIFICATION', {
      isMinimize: false,
      name,
    })
  }
  const exportDataOffersProgress = computed(() => store.state?.notification?.exportDataOffersProgress)
  const resetExportDataOffersProgress = () => {
    return store.commit('notification/RESET_EXPORT_DATA_OFFERS_PROGRESS', { root: true })
  }

  const loading = reactive({
    show: false,
    showDetail: false,
    markAllAsRead: false,
    markAllAsUnread: false,
    listType: false
  })

  const tab = [
    {
      label: 'Not read yet',
      value: 'unread',
    },
    {
      label: 'All',
      value: 'all',
    },
  ]

  const listType = ref([])

  const filter = reactive({
    page: 1,
    limit: 10,
    from: '',
    to: '',
    type: '',
    search: ''
  })

  const resetFilter = () => {
    filter.page = 1
    filter.limit = 10
    filter.from = ''
    filter.to = ''
    filter.type = ''
    filter.search = ''
    filter.type = ''
  }

  const notificationState = ref(null)
  const notificationDetail = ref(null)
  const actionShow = async (payload, callback = () => {}) => {
    try {
      loading.show = true
      const response = await store.dispatch(`notification/getNotification`, {
        ...payload
      })
      notificationState.value = response
      isFunction(callback) && callback()
    } finally {
      loading.show = false
    }
  }
  const actionShowDetail = async (payload, callback = () => {}) => {
    try {
      loading.showDetail = true
      const response = await store.dispatch(`notification/getNotificationDetail`, {
        ...payload
      })
      notificationDetail.value = response
      isFunction(callback) && callback(response)
    } finally {
      loading.showDetail = false
    }
  }
  const actionMarkAsRead = async (payload, callback = () => {}) => {
    try {
      loading.markAllAsRead = true
        const response = await store.dispatch(`notification/markAsRead`, {
        ...payload
      })
      isFunction(callback) && callback(response)
    } finally {
      loading.markAllAsRead = false
    }
  }
  const actionMarkAsUnread = async (payload, callback = () => {}) => {
    try {
      loading.markAllAsUnread = true
        const response = await store.dispatch(`notification/markAsUnread`, {
        ...payload
      })
      isFunction(callback) && callback(response)
    } finally {
      loading.markAllAsUnread = false
    }
  }
  const actionMarkAllAsRead = async (payload, callback = () => {}) => {
    try {
      loading.markAllAsRead = true
        const response = await store.dispatch(`notification/markAllAsRead`, {
        ...payload
      })
      isFunction(callback) && callback(response)
    } finally {
      loading.markAllAsRead = false
    }
  }
  const actionMarkAllAsUnread = async (payload, callback = () => {}) => {
    try {
      loading.markAllAsUnread = true
        const response = await store.dispatch(`notification/markAllAsUnread`, {
        ...payload
      })
      isFunction(callback) && callback(response)
    } finally {
      loading.markAllAsUnread = false
    }
  }
  const actionListType = async (payload, callback = () => {}) => {
    try {
      loading.listType = true
        const response = await store.dispatch(`notification/listTypeNotification`, {
        ...payload
      })
      listType.value = response
      isFunction(callback) && callback(response)
    } finally {
      loading.listType = false
    }
  }
  return {
    state,
    openNotification,
    closeNotification,
    minimizeNotification,
    maximizeNotification,
    loading,
    filter,
    tab,
    listType,
    resetFilter,
    notificationState,
    notificationDetail,
    actionShow,
    actionShowDetail,
    actionMarkAsRead,
    actionMarkAsUnread,
    actionMarkAllAsRead,
    actionMarkAllAsUnread,
    actionListType,
    exportDataOffersProgress,
    resetExportDataOffersProgress
  }
}
