export default (prefix = null) => ({
  props: {
    icon: {
      type: Boolean,
      default: false
    },
    iconBlock: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    classIcon() {
      return this.icon ? `${prefix + '-' || ''}icon` : ''
    },
    classIconBlock(){
      return this.iconBlock ? `input-icon-block` : ''
    }
  }
})