import request from 'utils/request'

const state = () => ({
  loading: {
    fetch: false,
    credential: {
      update: false
    }
  },
  credentials: {
    shareasale_api_secret: null,
    shareasale_merchant_account_id: null,
    shareasale_token: null,
    is_connected: 0
  }
})

const getters = {}

const mutations = {
  SET_LOADING_FETCH(state, status = false) {
    state.loading.fetch = status
  },
  SET_LOADING_UPDATE_CREDENTIAL(state, status = false) {
    state.loading.credential.update = status
  },
  SET_CREDENTIAL(state, credentials) {
    state.credentials = credentials
  }
}

const actions = {
  async fetchCredential({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/integration/share-a-sale`,
      method: 'get'
    })
    commit('SET_CREDENTIAL', {
      shareasale_api_secret: response.shareasale_api_secret,
      shareasale_merchant_account_id: response.shareasale_merchant_account_id,
      shareasale_token: response.shareasale_token,
      is_connected: response.is_connected
    })
  },
  async updateCredential({}, payload) {
    await request({
      url: `/v1/pos/settings/update/share-a-sale`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
