import request  from 'utils/request'

const state = () => ({
  all: [],
  show: {
    data: null
  },
  devices: []
})

const getters = {
}

const mutations = {
  SET_ALL(state, payload) {
    state.all = payload?.map(i => ({
      ...i,
      loading: false,
      child: i.child?.map(j => ({
        ...j,
        loading: false
      }))
    })) || []
  },
  SET_SHOW(state, payload) {
    state.show.data = payload
  },
  SET_DEVICES(state, payload) {
    state.devices = payload
  }
}

const actions = {
  async all({ commit } , payload) {
    const signalController = payload.signal
    delete payload.signal

    const response = await request({
      url: '/v1/pos/order-status',
      method: 'get',
      params: payload,
      signal: signalController
    })
    commit('SET_ALL', response.data)
    return response.data
  },
  async create({}, payload) {
    return await request({
      url: `/v1/pos/order-status`,
      method: 'post',
      data: payload
    })
  },
  async update({}, payload) {
    return await request({
      url: `/v1/pos/order-status/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async updateEmailTheme({}, payload){
    return await request({
      url: `/v1/pos/order-status/setting/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async delete({}, payload) {
    return await request({
      url: `/v1/pos/order-status/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async show({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/order-status/${payload.id}`,
      method: 'get',
      params: payload.params
    })

    commit('SET_SHOW', response)
    return response
  },
  async sort({}, payload){
    return await request({
      url: `/v1/pos/order-status/sort`,
      method: 'post',
      data: {
        sorted_id: payload.sorted_id,
        _method: 'PUT'
      },
      signal: payload.signal
    })
  },
  async previewEmailTemplate({}, payload){
    return await request({
      url: `/v1/pos/order-status/view`,
      method: 'post',
      data: payload
    })
  },
  async previewEmailTemplateById({}, payload){
    return await request({
      url: `/v1/pos/order-status/view/${payload.id}`,
      method: 'get',
      params: payload.params
    })
  },
  async previewEmailTemplateCreate({}, payload){
    return await request({
      url: `/v1/pos/order-status/view/create`,
      method: 'post',
      data: payload
    })
  },
  async showInitData(){
    return await request({
      url: '/v1/pos/order-status/init-attributes',
      method: 'get'
    })
  },
  async resetEmailTemplate({}, payload){
    return await request({
      url: `/v1/pos/order-status/reset/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async updateSendEmail({}, payload) {
    return await request({
      url: `/v1/pos/order-status/send-status`,
      method: 'put',
      data: payload
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}