import request  from 'utils/request'
import repairdesk from './integration/repairdesk'
import wholecell from './integration/wholecell'
import shipmondo from './integration/shipmondo'
import shipengine from './integration/shipengine'
import easypost from './integration/easypost'
import klaviyo from './integration/klaviyo'
import directToMobile from './integration/directToMobile'
import shareASale from './integration/shareASale'
import ghl from './integration/ghl'

const state = () => ({
  all: [],
  enabled_integration: [],
  show: {
    all: {},
    public_api: {},
    fedex: {
      config: {},
      shipping_kit: {}
    },
    ups: {
      config: {},
      shipping_kit: {}
    },
    usps: {
      shipping_kit: {},
      current_account: {},
      accounts: {},
      data: null
    },
    repairdesk: {

    },
    datafeed: {
      all: [],
      payload: null,
      show: {},
      progress_percentage: 0
    }
  },
  refresh_datafeed: false,
  data_feed_widget_url: ''
})

const getters = {

}

const mutations = {
  SET_ALL(state, payload){
    state.all = payload
  },
  SET_ENABLED_INTEGRATION(state, payload) {
    state.enabled_integration = payload
  },
  SET_SHOW(state, payload) {
    state.show.data = payload
  },
  SET_SHOW_FEDEX_CONFIG(state, payload){
    state.show.fedex.config = payload
  },
  SET_SHOW_FEDEX_SHIPPING_KIT(state, payload){
    state.show.fedex.shipping_kit = payload
  },
  SET_SHOW_USPS(state, payload){
    state.show.usps.data = payload
  },
  SET_SHOW_USPS_SHIPPING_KIT(state, payload){
    state.show.usps.shipping_kit = payload
  },
  SET_SHOW_USPS_ACCOUNTS(state, payload){
    state.show.usps.accounts = payload
  },
  SET_SHOW_USPS_CURRENT_ACCOUNT(state, payload){
    state.show.usps.current_account = payload
  },
  SET_SHOW_UPS_CONFIG(state, payload){
    state.show.ups.config = payload
  },
  SET_SHOW_UPS_SHIPPING_KIT(state, payload){
    state.show.ups.shipping_kit = payload
  },
  SET_SHOW_ALL(state, payload){
    state.show.all = payload
  },
  SET_SHOW_PUBLIC_API(state, payload){
    state.show.public_api = payload
  },
  SET_DATA_FEED_ALL(state, payload){
    state.show.datafeed.all = payload
  },
  SET_DATA_FEED_SHOW(state, payload){
    state.show.datafeed.show = payload
  },
  SET_REFRESH_DATA_FEED(state, payload){
    state.refresh_datafeed = payload
  },
  SET_WIDGET_URL_DATA_FEED(state, payload) {
    state.data_feed_widget_url = payload
  },
  SET_DATA_FEED_PROGRESS(state, payload){
    if(payload?.progress) state.show.datafeed.progress_percentage = payload.progress
  },
	SET_ENABLE_INTEGRATION(state, payload) {
		state.all[payload].is_enable = 1
	}
}

const actions = {
  async all({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/integrations`,
      method: 'get'
    })
    commit('SET_ALL', response)
    return response
  },
  async enabledIntegration({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/integrations/enabled-integration`,
      method: 'get'
    })
    commit('SET_ENABLED_INTEGRATION', response)
    return response
  },
  async update({commit}, payload){
    const response = await request({
      url: `/v1/pos/integrations/${payload.id}`,
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload.data
      }
    })
    return response
  },
  async showFedexConfig({state, commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/fedex-carrier',
      method: 'get'
    })
    commit('SET_SHOW_FEDEX_CONFIG', response)
    return response
  },
  async showFedexShippingKit({state, commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/text-info/fedex-shipping-kit',
      method: 'get'
    })
    commit('SET_SHOW_FEDEX_SHIPPING_KIT', response)
    return response
  },
  async showFedexConfigUpdate({commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/fedex-carrier',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showFedexShippingKitUpdate({commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/text-info/fedex-shipping-kit',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showUpsConfig({commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/ups-carrier',
      method: 'get'
    })
    commit('SET_SHOW_UPS_CONFIG', response)
    return response
  },
  async showUpsShippingKit({commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/text-info/ups-shipping-kit',
      method: 'get'
    })
    commit('SET_SHOW_UPS_SHIPPING_KIT', response)
    return response
  },
  async showUpsConfigUpdate({}, payload){
    const response = await request({
      url: '/v1/pos/settings/ups-carrier',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showUpsShippingKitUpdate({}, payload){
    const response = await request({
      url: '/v1/pos/settings/text-info/ups-shipping-kit',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showUspsShippingKit({state, commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/text-info/usps-shipping-kit',
      method: 'get'
    })
    commit('SET_SHOW_USPS_SHIPPING_KIT', response)
    return response
  },
  async showUspsAccounts({state, commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/usps-account',
      method: 'get'
    })
    commit('SET_SHOW_USPS_ACCOUNTS', response)
    return response
  },
  async showUspsAccountDelete({}, payload){
    return await request({
      url: `/v1/pos/settings/usps-account/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async showUspsCurrentAccount({state, commit}, payload){
    const response = await request({
      url: '/v1/pos/settings/usps-account/current',
      method: 'get'
    })
    commit('SET_SHOW_USPS_CURRENT_ACCOUNT', response)
    return response
  },
  async showUspsCurrentAccountUpdate({ state, commit }, payload) {
    return await request({
      url: `/v1/pos/settings/usps-account/current/${payload}`,
      method: 'post',
      data: {
        _method: 'PUT'
      }
    })
  },
  async showUspsCreate({}, payload) {
    return await request({
      url: `/v1/pos/settings/usps-account`,
      method: 'post',
      data: payload
    })
  },
  async showUspsUpdate({}, payload) {
    return await request({
      url: `/v1/pos/settings/usps-account/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async showUspsShippingKitUpdate({}, payload){
    const response = await request({
      url: '/v1/pos/settings/text-info/usps-shipping-kit',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showUspsDelete({}, payload) {
    return await request({
      url: `/v1/pos/settings/usps-account/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async showUsps({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/usps-account/${payload}`,
      method: 'get'
    })

    commit('SET_SHOW_USPS', response)
    return response
  },
  async showAll({commit}, payload) {
    const response = await request({
      url: `/v1/pos/settings/integration`,
      method:'get'
    })
    commit('SET_SHOW_ALL', response)
  },
  async updateAll({commit}, payload){
    const response = await request({
      url: `/v1/pos/settings/update`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showPublicApi({commit}, payload) {
    const response = await request({
      url: `/v1/pos/public`,
      method:'get'
    })
    commit('SET_SHOW_PUBLIC_API', response)
  },
  async showGeneratePublicApiKey({commit}, payload) {
    const response = await request({
      url: `/v1/pos/public/generate-api-key`,
      method:'post'
    })
    return response
  },
  async allDataFeed({commit}, payload) {
    const response = await request({
      url: `/v2/pos/integrations/datafeed`,
      method:'get',
      params: payload
    })
    commit('SET_DATA_FEED_ALL', response)
    commit('SET_REFRESH_DATA_FEED', false)
  },
  async showDataFeed({commit}, payload) {
    const response = await request({
      url: `/v1/pos/settings/datafeed`,
      method:'get'
    })
    commit('SET_DATA_FEED_SHOW', response)
  },
  async setRefreshDataFeed({commit}, payload) {
    commit('SET_REFRESH_DATA_FEED', payload)
  },
  async showWidgetURLDatafeed({ commit }) {
    const response = await request({
      url: `v1/pos/integrations/datafeed/widget-link`,
      method: 'get'
    })
    commit('SET_WIDGET_URL_DATA_FEED', response.widget_link)
  },
  async updateWidgetURLDatafeed({ commit }, payload) {
    await request({
      url: `v1/pos/integrations/datafeed/widget-link`,
      method: 'post',
      data: {
        widget_link: payload
      }
    })
    commit('SET_WIDGET_URL_DATA_FEED', payload)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
  modules: {
    repairdesk,
    wholecell,
    shipmondo,
    shipengine,
    easypost,
    klaviyo,
    directToMobile,
    shareASale,
    ghl
  }
}
