<template>
  <div :class="['pagination', small && '--small']">
    <div v-if="!hideShowEntries" class="md:mb-0 mb-4 flex items-center">
      Show
      <Select
        class="mx-3"
        :modelValue="limit"
        @update:modelValue="handleUpdateLimit"
        @select-option="handleClickLimitOption"
        :items="limits"
        :readonly="disabled"
        by="value"
        name="name"
        no-border
        :style="{
          width: '80px',
          boxShadow: 'unset'
        }"
      ></Select>
      entries
    </div>
    <div :class="['pagination__action']">
      <div :class="['pagination__paginate']">
        <div class="flex items-center">
          <a
            href="#"
            @click.prevent="jumpPage('prev')"
            :class="[
              'pagination__jump-back',
              page <= 1 && 'pagination__jump-back--disabled'
            ]"
          >
            <span class="material-icons">keyboard_double_arrow_left</span>
          </a>
          <a
            href="#"
            @click.prevent="updatePageNextPrev('prev')"
            :class="[
              'pagination__step-back',
              page <= 1 && 'pagination__step-back--disabled'
            ]"
          >
            <span class="material-icons">chevron_left</span>
          </a>
        </div>
        <span class="block"> Page </span>
        <div class="w-24" :title="`Page ${page}`">
          <Input
            v-model="page"
            @input="updatePage"
            @focusOut="handleLostFocus"
            @click="handleClickPageInput"
            type="text"
          />
        </div>
        <span class="block whitespace-nowrap"> of {{ data.last_page }} </span>
        <div class="flex items-center">
          <a
            href="#"
            @click.prevent="updatePageNextPrev('next')"
            :class="[
              'pagination__step-next',
              page >= data.last_page && 'pagination__step-next--disabled'
            ]"
          >
            <span class="material-icons">chevron_right</span>
          </a>
          <a
            href="#"
            @click.prevent="jumpPage('next')"
            :class="[
              'pagination__jump-next',
              page >= data.last_page && 'pagination__jump-next--disabled'
            ]"
          >
            <span class="material-icons">keyboard_double_arrow_right</span>
          </a>
        </div>
      </div>
      <span v-if="!hideDetailPage" class="pagination__summary whitespace-nowrap"
        >Showing {{ data.from }} to {{ data.to }} of
        {{ data.total }} entries</span
      >
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, watch, computed } from 'vue'
import debounce from 'utils/debounce'
import Input from 'bbUi/components/input/input.vue'
import Select from 'components/Select.vue'

export default defineComponent({
  components: {
    Input,
    Select
  },
  props: {
    limits: {
      type: Array,
      default: () => [10, 25, 50 ,100]
    },
    limit: {
      type: [Number, String],
      default: 1
    },
    page: {
      type: [Number, String],
      default: 10
    },
    data: {
      type: Object,
      default: () => ({})
    },
    hideShowEntries: {
      type: Boolean,
      default: false
    },
    hideDetailPage: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:page', 'update:limit', 'click-page', 'click-page-input', 'click-limit-option'],
  setup(props, { emit }) {
    const page = ref(null)
    const limit = ref(null)
    const savedPage = ref(null)

    page.value = props.page
    limit.value = props.limit

    const limits = computed(() => {
      let options = props.limits // [10, 25, 50, 100, props.limit && props.limit]
      options = options.reduce((options, nextOption) => {
        if (options.find((option) => option === nextOption)) return options
        options.push(nextOption)
        return options
      }, [])
      return options.map((option) => ({
        name: option,
        value: option
      }))
    })
    
    const updatePage = debounce((e) => {
      emit('click-page', true)

      if(props.disabled) return

      let currentPage = Number(page.value)

      if (currentPage <= 0) return;

      if (currentPage > props.data.last_page) {
        emit('update:page', props.data.last_page)
        page.value = props.data.last_page
        return
      } else if (currentPage < 1) {
        emit('update:page', 1)
        page.value = 1
        return
      }
      emit('update:page', currentPage)
    }, 300)
    const updatePageNextPrev = (type) => {
      emit('click-page', true)
      
      if(props.disabled) return

      let currentPage = Number(page.value)

      if (type === 'next' && currentPage < props.data.last_page)
        emit('update:page', ++currentPage)
      else if (type === 'prev' && currentPage > 1)
        emit('update:page', --currentPage)
    }
    const jumpPage = (type) => {
      const rules = {
        next: page.value < props.data.last_page,
        prev: page.value > 1
      }
      emit('click-page', true)
      if (props.disabled) return

      if (rules[type]) {
        emit('update:page', type === 'next' ? props.data.last_page : 1)
      }
    }
    const handleUpdateLimit = (newValue) => {
      if(props.disabled) return
      // emit('update:page', 1)
      emit('update:limit', newValue)
    }
    const handleClickPageInput = (ev) => {
      emit('click-page-input', ev)
      if(props.disabled){
        ev.target.blur()
      }
    }
    const handleClickLimitOption = () => {
      emit('click-limit-option', true)
    }
    const handleLostFocus = () => {
      if (!Number(page.value)) page.value = 1
      if (Number(page.value) > props.data.last_page) page.value = props.data.last_page

      return updatePage()
    }

    watch(
      () => ({ page: props.page, limit: props.limit }),
      (newValue) => {
        page.value = newValue.page
        limit.value = newValue.limit
      }
    )

    return {
      page,
      limit,
      limits,
      updatePage,
      jumpPage,
      updatePageNextPrev,
      handleLostFocus,
      handleUpdateLimit,
      handleClickPageInput,
      handleClickLimitOption
    }
  }
})
</script>
<style lang="scss">
.pagination {
  @apply flex md:flex-row flex-col md:justify-between items-center;
  input {
    text-align: center;
    border-radius: 8px !important;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .input__container {
    width: 100% !important;
  }
  .select__head {
    border-radius: 8px !important;
  }
  .pagination__step-back,
  .pagination__step-next,
  .pagination__jump-back,
  .pagination__jump-next {
    @apply text-grey-04 hover:text-teal-04;
    &--disabled {
      @apply text-grey-02 hover:text-grey-02 cursor-not-allowed;
    }
    .material-icons {
      font-size: 1.525rem !important;
    }
  }
  .pagination__action {
    @apply flex md:flex-row md:gap-x-3 flex-col items-center;
    .pagination__paginate {
      @apply flex items-center md:mb-0 mb-4 gap-2;
    }
  }
  &.--small {
    @apply items-center flex-col gap-y-4;
    .pagination__action {
      @apply flex-col w-full;
    }
    .pagination__summary {
      @apply mt-4;
    }
  }
}
</style>
