import request from 'utils/request'

const state = () => ({
  show: {
    shipping_insurance: {},
    shipping_rules: {},
    shipping_error_label: {},
    product_models: [],
    additional_settings: {
      is_printer_access_show: 0
    }
  }
})

const getters = {}

const mutations = {
  SET_SHOW_SHIPPING_INSURANCE(state, payload) {
    state.show.shipping_insurance = payload
  },
  SET_SHOW_SHIPPING_RULES(state, payload) {
    state.show.shipping_rules = {
      ...payload,
      update_data: {}
    }
  },
  SET_SHOW_SHIPPING_ERROR_LABEL(state, payload) {
    state.show.shipping_error_label = payload
  },
  SET_SHOW_PRODUCT_MODELS(state, payload) {
    state.show.product_models = payload
  },
  SET_SHOW_PRINTER_ACCESS(state, payload) {
    state.show.additional_settings.is_printer_access_show = payload
  }
}

const actions = {
  async showProductModels({ commit }) {
    // const response = await request({
    //   url: 'v1/pos/settings/product-category',
    //   method: 'get'
    // })

    commit('SET_SHOW_PRODUCT_MODELS', []) // response)
  },
  async showShippingInsurance({ commit }) {
    const response = await request({
      url: '/v1/pos/settings/shipping-insurance',
      method: 'get'
    })

    commit('SET_SHOW_SHIPPING_INSURANCE', response)
  },
  async showShippingInsuranceUpdate(_, payload) {
    const response = await request({
      url: '/v1/pos/settings/update',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showShippingRules({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/default-label',
      method: 'get',
      params: payload?.params ? payload.params : {}
    })

    commit('SET_SHOW_SHIPPING_RULES', response)
    commit('shipping/SET_SHIPPING_LABEL', response.param, { root: true })
    commit('shipping/SET_SHIPPING_LABELS', response.labels, { root: true })
    commit('shipping/SET_SHIPPING_CARRIERS', response, { root: true })
    commit('shipping/SET_ADVANCE_SHIPPING', response, { root: true })
    const hasDefaultLabel = response?.param?.default_label
    if (hasDefaultLabel)
      commit(
        'shipping/SET_SELECTED_SHIPPING_CARRIER',
        response.param.default_label,
        { root: true }
      )
    return response
  },
  async showShippingRulesUpdate(_, payload) {
    const response = await request({
      url: '/v1/pos/settings/default-label',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showEnableAdvancedShippingRulesUpdate({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/default-label',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showShippingErrorLabel({ commit }) {
    const response = await request({
      url: '/v1/pos/settings/text-info/failed-shipping-label'
    })

    commit('SET_SHOW_SHIPPING_ERROR_LABEL', response)
  },
  async showShippingErrorLabelUpdate(_, payload) {
    const response = await request({
      url: '/v1/pos/settings/text-info/failed-shipping-label',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async showPrinterAccess({ commit }) {
    const response = await request({
      url: '/v1/pos/settings/printer-access',
      method: 'get'
    })
    commit('SET_SHOW_PRINTER_ACCESS', response.is_printer_access_show)
  },
  async showPrinterAccessUpdate(_, payload) {
    const response = await request({
      url: '/v1/pos/settings/printer-access',
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    return response
  },
  async fetchShippingKit() {
    const response = await request({
      url: `/v1/pos/settings/shipping-template`,
      method: 'get'
    })
    return response
  },
  async fetchShippingKitTemplatePdf(_, param) {
    const response = await request({
      url: `/v1/pos/settings/shipping-template/pdf/${param}`,
      method: 'get',
      responseType: 'arraybuffer'
    })
    return response
  },
  async previewShippingKit({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/shipping-template/pdf`,
      method: 'post',
      data: payload,
      responseType: 'arraybuffer'
    })
    return response
  },
  async updateShippingKit({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/shipping-template`,
      method: 'post',
      data: payload
    })
    return response
  },
  async restoreShippingKit({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/shipping-template/default`,
      method: 'post',
      data: payload
    })
    return response
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
