import request from 'utils/request'

const credentials = {
  domain: null,
  logon_name: null,
  password: null
}

const state = () => ({
  credentials,
  cached_credentials: {
    domain: null,
    logon_name: null,
    password: null
  },
  loading: {
    credentials: {
      fetch: false,
      connect: false
    }
  }
})

const getters = {}

const mutations = {
  SET_CREDENTIAL(state, credentials = null) {
    if (credentials === null) return
    state.credentials = credentials
  },
  SET_CACHED_CREDENTIAL(state, credentials = null) {
    if (credentials === null) return
    state.cached_credentials = credentials
  },
  SET_LOADING_CREDENTIAL_FETCH(state, status = false) {
    state.loading.credentials.fetch = status
  },
  SET_LOADING_CREDENTIAL_CONNECT(state, status = false) {
    state.loading.credentials.connect = status
  },
  RESET_CREDENTIAL(state) {
    state.credentials = credentials
  }
}

const actions = {
  async connectCredential({ state }) {
    await request({
      url: `/v1/pos/integrations/ups-dtm/connect`,
      method: 'post',
      data: {
        ...state.credentials
      }
    })
  },
  async fetchCredential({ commit }) {
    const response = await request({
      url: `/v1/pos/integrations/ups-dtm`,
      method: 'get'
    })
    const { domain, logon_name, password } = response
    const credentials = {
      domain: domain ? domain : null,
      logon_name: logon_name ? logon_name : null,
      password: password ? password : null
    }
    commit('SET_CREDENTIAL', credentials)
    commit('SET_CACHED_CREDENTIAL', credentials)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
