import request  from 'utils/request'

const state = () => ({
  show: {
    privacy: {},
    terms: {}
  }
})

const getters = {
}

const mutations = {
  SET_SHOW_PRIVACY(state, payload){
    state.show.privacy = payload
  },
  SET_SHOW_TERMS(state, payload){
    state.show.terms = payload
  }
}

const actions = {
  async showPrivacy({commit}) {
    const response = await request({
      url: `/v1/pos/settings/text-info/privacy`,
      method: 'get'
    })
    commit('SET_SHOW_PRIVACY', response)
  },
  async showTerms({commit}) {
    const response = await request({
      url: `/v1/pos/settings/text-info/terms`,
      method: 'get'
    })
    commit('SET_SHOW_TERMS', response)
  },
  async updatePrivacy({}, payload){
    await request({
      url: `/v1/pos/settings/text-info/privacy`,
      method: 'post',
      data: {
        ...payload,
        _method:'PUT'
      }
    })
  },
  async updateTerms({}, payload){
    await request({
      url: `/v1/pos/settings/text-info/terms`,
      method: 'post',
      data: {
        ...payload,
        _method:'PUT'
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}