import request from 'utils/request'

const state = () => ({
  all: {},
  show: {},
  location: [],
  subscription: [],
  staff_notification: {
    locations: {}
  }
})

const getters = {}

const mutations = {
  SET_ALL(state, payload) {
    state.all = payload
  },
  SET_SHOW(state, payload) {
    state.show = payload
  },
  SET_LOCATION(state, payload) {
    state.location = payload
  },
  SET_SUBSCRIPTION(state, payload) {
    state.subscription = payload
  },
  SET_STAFF_NOTIFICATION_LOCATION(state, location) {
    state.staff_notification.locations = location
  }
}

const actions = {
  async all({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/email-template`,
      method: 'get',
      params: payload
    })
    commit('SET_ALL', response)
    return response
  },
  async show({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/email-template/${payload.id}`,
      method: 'get',
      params: {
        ...payload.params
      }
    })
    commit('SET_SHOW', response)
    return response
  },
  async update({}, payload) {
    return await request({
      url: `v1/pos/settings/email-template/update/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async location({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/email/location',
      method: 'get',
      params: payload
    })
    commit('SET_LOCATION', response)
  },
  async getSubscription({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/email/subscription${
        payload.id ? '/' + payload.id : ''
      }`,
      method: 'get',
      params: payload.params ? payload.params : {}
    })
    commit('SET_SUBSCRIPTION', response)
  },
  async storeSubscription(_, payload) {
    return await request({
      url: `/v1/pos/settings/email/subscription/${payload.id}`,
      method: 'post',
      data: { ...payload }
    })
  },
  async previewEmailReview(_, payload) {
    const response = await request({
      url: '/v1/pos/settings/review-email/view',
      method: 'post',
      data: payload
    })
    return response
  },
  async previewEmailTemplate(_, payload) {
    const response = await request({
      url: '/v1/pos/settings/email-template/view',
      method: 'post',
      data: payload
    })
    return response
  },
  async previewEmailTemplateById(_, id) {
    const response = await request({
      url: `/v1/pos/settings/email-template/view/${id}`,
      method: 'get'
    })
    return response
  },
  async resetEmailTemplate(_, id) {
    const response = await request({
      url: `/v1/pos/settings/email-template/reset/${id}`,
      method: 'post'
    })
    return response
  },
  async staffNotificationLocation({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/store-location',
      method: 'get',
      params: payload
    })

    commit('SET_STAFF_NOTIFICATION_LOCATION', response)
    return response
  },
  async mailServer() {
    return await request({
      url: '/v1/pos/settings/mail-server/domains',
      method: 'get'
    })
  },
  async addMailServer({}, payload) {
    return await request({
      url: '/v1/pos/settings/mail-server/domains',
      method: 'post',
      data: {
        email: payload.email,
        domain: payload.domain,
        continent_code: payload.region,
        sub_domain: payload.sub_domain
      }
    })
  },
  async resetMailServer({}) {
    return await request({
      url: '/v1/pos/settings/mail-server/domains',
      method: 'delete'
    })
  },
  async verifyMailServer({}) {
    return await request({
      url: '/v1/pos/settings/mail-server/domains/verify',
      method: 'post'
    })
  },
  async staffList({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/email-template/internal`,
      method: 'get',
      params: payload
    })

    return response
  },
  async staffForm({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/email-template/${payload.id}`,
      method: 'get',
      params: payload.data
    })

    return response
  },
  async generalList({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/email-template`,
      method: 'get',
      params: payload
    })

    return response
  },
  async generalForm({}, id) {
    const response = await request({
      url: `/v1/pos/settings/email-template/${id}`,
      method: 'get'
    })

    return response
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
