import axios from 'axios'
import request from 'utils/request'
const CancelToken = axios.CancelToken

let abortController = undefined

const cancelToken = {
  all: null
}

const state = () => ({
  all: {},
  devices: [],
  brands: [],
  categories: [],
  carriers: []
})

const getters = {}

const mutations = {
  SET_DEVICES(state, data) {
    state.devices = data
  },
  SET_BRANDS(state, data) {
    state.brands = data
  },
  SET_CATEGORIES(state, data) {
    state.categories = data.data
  },
  SET_ALL(state, data) {
    state.all = data
  },
  SET_CARRIERS(state, data) {
    state.carriers = data
  },
  SET_DEVICES_CHILD(state, data) {
    if (state.devices.data[data.manufacturer]) {
      state.devices.data[data.manufacturer] = state.devices.data[data.manufacturer].map(item => {
        if (item.id === data.id) {
          return {
            ...item,
            child: data.child,
            is_loading: false
          }
        }
        return item
      })
    }
    // [data.key].child = data.child
  }
}

const actions = {
  async all({ commit, state }, payload) {
    if (cancelToken.all !== null) cancelToken.all()
    if (abortController) abortController.abort()
    const controller = new AbortController()
    abortController = controller
    const url = `/v1/pos/settings/category/device`

    await request({
      method: 'get',
      url: url,
      params: payload,
      cancelToken: new CancelToken(function executor(c) {
        cancelToken.all = c
      }),
      signal: controller.signal
    }).then(response => {
      commit('SET_DEVICES', response.data)
      commit('SET_BRANDS', response.brand)
      commit('SET_ALL', response)
    }).catch(() => {
      commit('SET_DEVICES', [])
      commit('SET_BRANDS', [])
      commit('SET_ALL', {})
    })
  },
  async getChildren({ commit, state }, payload) {
    if (cancelToken.all !== null) cancelToken.all()
    if (abortController) abortController.abort()
    const controller = new AbortController()
    abortController = controller
    const url = `/v1/pos/settings/category/device/${payload.id}`

    const response = await request({
      method: 'get',
      url: url,
      params: payload.data,
      cancelToken: new CancelToken(function executor(c) {
        cancelToken.all = c
      })
    })
    return response
  },
  async allComputer({ commit }, payload) {
    const response = await request({
      method: 'get',
      url: `/v1/pos/settings/category/computer`,
      params: payload
    })
    commit('SET_DEVICES', response.data)
    commit('SET_BRANDS', response.brand)
  },
  async categories({ commit }) {
    const response = await request({
      method: 'get',
      url: `/v1/pos/settings/category/list`
    })

    commit('SET_CATEGORIES', response)
  },
  async allCategoryBuybackType (_, payload) {
    const response = await request({
      method: 'get',
      url: '/v1/pos/settings/category/buyback_type',
      params: payload
    })
    return response
  },
  async update({ commit, state, dispatch }, payload) {
    const response = await request({
      url: `/v1/pos/settings/category`,
      method: 'post',
      data: payload
    }).then((res) => {
      return res
    })
    return response
  },
  async updateBuybackType({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/category/buyback_type`,
      method: 'post',
      data: payload
    }).then((res) => {
      return res
    })
    return response
  },
  async getCarriers({ commit, state }) {
    const response = await request({
      method: 'get',
      url: '/v1/pos/settings/networks'
    })
    commit('SET_CARRIERS', response)
    return response
  },
  async updateCarriers({}, payload) {
    const response = await request({
      method: 'put',
      url: '/v1/pos/settings/networks',
      data: payload.data,
      cancelToken: payload.cancelToken,
      headers: {
        'Content-Type': 'application/json'
      }
    })
    return response
  },
  async exportActiveProductCatalog(_, payload){
    return await request({
      method: 'get',
      url: '/v1/pos/settings/export/product-catalog',
      params: payload.data
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
