const getBasicFingerprint = () => {
  const ua = navigator.userAgent
  const platform = navigator.platform
  const screenRes = `${screen.width}x${screen.height}`

  return `${ua}|${platform}|${screenRes}`
}
const generateRandomHex = (length = 16) => {
  const bytes = new Uint8Array(length)
  crypto.getRandomValues(bytes)

  return [...bytes].map(b => b.toString(16).padStart(2, '0')).join('')
}
const subtleDigest = async (data) => {
  const encoder = new TextEncoder()
  const buffer = encoder.encode(data)
  const hashBuffer = await crypto.subtle.digest('SHA-256', buffer)

  return [...new Uint8Array(hashBuffer)].map(b => b.toString(16).padStart(2, '0')).join('')
}
const shuffleString = (str) => {
  return str.split('').sort(() => 0.5 - Math.random()).join('')
}
export const generateSessionId = async () => {
  const fingerprint = getBasicFingerprint()
  const randomHex = generateRandomHex(32)
  const timestamp = Date.now().toString(36)
  const rawString = `${fingerprint}|${randomHex}|${timestamp}`
  const hashed = await subtleDigest(rawString)
  const shuffled = shuffleString(hashed)

  return `${shuffled.slice(0, 8)}${shuffled.slice(8, 12)}${shuffled.slice(12, 20)}${shuffled.slice(20, 32)}${shuffled.slice(32, 64)}`
  // return `${shuffled.slice(0, 8)}-${shuffled.slice(8, 12)}-${shuffled.slice(12, 20)}-${shuffled.slice(20, 32)}-${shuffled.slice(32, 64)}`
}
