import request from 'utils/request'

const state = () => ({
	all: {},
	show: {}
})

const getters = {}

const mutations = {
	SET_ALL(state, payload) {
		state.all = payload
	},
	SET_SHOW(state, payload) {
		state.show = payload
	}
}

const actions = {
	async all({ commit }, payload) {
		const response = await request({
			url: `/v1/pos/settings/customer-portal/revised-offers`,
			method: 'get',
			params: payload
		})
		commit('SET_ALL', response)
		return response
	},
	async show({ commit }, payload) {
		const response = await request({
			url: `/v1/pos/settings/customer-portal/revised-offers/${payload.id}`,
			method: 'get',
			params: {
				...payload.params
			}
		})
		commit('SET_SHOW', response)
		return response
	},
	async update({}, payload) {
		return await request({
			url: `/v1/pos/settings/customer-portal/revised-offers/${payload.id}`,
			method: 'put',
			data: {
				...payload.data,
				_method: 'PUT'
			}
		})
	},
	async previewRevisedOffer(_, payload) {
		const response = await request({
			url: '/v1/pos/settings/customer-portal/revised-offers/view',
			method: 'post',
			data: payload
		})
		return response
	},
	async previewRevisedOfferById(_, id) {
		const response = await request({
			url: `/v1/pos/settings/customer-portal/revised-offers/view/${id}`,
			method: 'get'
		})
		return response
	},
	async resetRevisedOffer(_, id) {
		const response = await request({
			url: `/v1/pos/settings/customer-portal/revised-offers/reset/${id}`,
			method: 'post'
		})
		return response
	},
}

export default {
	namespaced: true,
	state,
	getters,
	mutations,
	actions
}
