<template>
  <SplashLoading />
  <NotificationPopup />
  <Paywall
    v-model:show="statePaywall.show"
    :title="statePaywall.title"
    :description="statePaywall.description"
    :image-url="statePaywall.image_url"
    :package-name="statePaywall.package_name"
  />
  <router-view />
</template>
<script>
import { defineComponent, onMounted, onUnmounted } from 'vue'

import { usePaywall } from 'composable/paywall'

import SplashLoading from 'components/SplashLoading.vue'
import Paywall from 'components/Paywall.vue'
import NotificationPopup from 'components/Notification/NotificationPopup.vue'

import { initSessionKey, sessionListener } from 'utils/session'

export default defineComponent({
  components: {
    SplashLoading,
    Paywall,
    NotificationPopup
  },
  setup() {
    const { state: statePaywall } = usePaywall()
    onMounted(() => {
      initSessionKey()
      window.addEventListener('storage', sessionListener)
    })
    onUnmounted(() => {
      window.removeEventListener('storage', sessionListener)
    })
    return {
      statePaywall,
      isWhiteLabel
    }
  }
})
</script>
