import request  from 'utils/request'

const state = () => ({
  show: {
    checkRegister: {},
  }
})

const getters = {
}

const mutations = {
  SET_SHOW_CHECK_REGISTER(state, payload){
    state.show.checkRegister = payload
  },
}

const actions = {
  async showCheckRegister({commit}, payload){
    const response = await request({
      url: '/v1/pos/order/check-register-history',
      method: 'get',
      params: {
        buyback_type: 2,
        ...payload
      }
    })

    commit('SET_SHOW_CHECK_REGISTER', response)
    return response
  },
  async checkRegisterCSV(_, payload = {}) {
    return await request({
      url: `/v1/pos/order/check-register-history/csv`,
      method: 'get',
      params: payload,
      responseType: 'arraybuffer'
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}