import request  from 'utils/request'
import { CancelToken } from '@/utils/request';
let sourceToken = null

const state = () => ({
  statuses: [
    {
      id: 'all',
      description: null,
      short_name: 'All',
      status_name: 'All',
      total: 0
    },
    {
      id: 0,
      description: null,
      short_name: 'Available',
      status_name: 'Available',
      total: 0
    },
    {
      id: 1,
      description: null,
      short_name: 'Pending',
      status_name: 'Pending',
      total: 0
    },
    {
      id: 2,
      description: null,
      short_name: 'Sold',
      status_name: 'Sold',
      total: 0
    },
    {
      id: -1,
      description: null,
      short_name: 'Deleted',
      status_name: 'Deleted',
      total: 0
    },
    {
      id: 4,
      description: null,
      short_name: 'Moved',
      status_name: 'Moved',
      total: 0
    },
    {
      id: 5,
      description: null,
      short_name: 'Not Available',
      statis_name: 'Not Available',
      total: 0
    }
  ],
  createTag: null,
  payload: null,
  filter: null,
  all: [],
  deeplink: null,
  show: {
    data: null,
    pictures: [],
    networks: [],
    devices: [],
    processors: [],
    hardDrives: [],
    memories: [],
    graphicsCards: [],
    conditions: [],
    transferHistories: [],
    sellingHistories: [],
    morePrice: null,
    updateMorePrice: null
  },
  data_match: {
    post: null,
    get: null
  },
  backmarket: [],
  update: null,
  alert: {
    default: !localStorage.closedInventoryAlertDefault ? true : false,
    pending: !localStorage.closedInventoryAlertPending ? true : false
  },
  move_locations: []
})

const getters = {
}

const mutations = {
  SET_FILTER(state, payload) {
    state.filter = payload
  },
  SET_PAYLOAD(state, payload) {
    state.payload = payload
  },
  SET_ALL(state, payload) {
    state.all = payload
  },
  SET_DEEPLINK(state, payload) {
    state.deeplink = payload
  },
  SET_DATA_MATCH(state, payload) {
    state.data_match.post = payload
  },
  SET_GET_DATA_MATCH(state, payload) {
    state.data_match.get = payload
  },
  SET_UPDATE(state, payload) {
    state.update = payload
  },
  SET_CREATE_TAG(state, payload) {
    state.createTag = payload
  },
  SET_SHOW(state, payload) {
    state.show.data = payload
  },
  SET_BACKMARKET(state, payload) {
    state.backmarket = payload
  },
  SET_SHOW_PICTURES(state, payload) {
    state.show.pictures = payload
  },
  SET_SHOW_NETWORKS(state, payload) {
    state.show.networks = payload
  },
  SET_SHOW_DEVICES(state, payload) {
    state.show.devices = payload
  },
  SET_SHOW_PROCESSORS(state, payload) {
    state.show.processors = payload
  },
  SET_SHOW_HARD_DRIVES(state, payload) {
    state.show.hardDrives = payload
  },
  SET_SHOW_MEMORIES(state, payload) {
    state.show.memories = payload
  },
  SET_SHOW_GRAPHICS_CARDS(state, payload) {
    state.show.graphicsCards = payload
  },
  SET_SHOW_CONDITIONS(state, payload) {
    state.show.conditions = payload
  },
  SET_SHOW_TRANSFER_HISTORIES(state, payload) {
    state.show.transferHistories = payload
  },
  SET_SHOW_SELLING_HISTORIES(state, payload) {
    state.show.sellingHistories = payload
  },
  SET_STORE_MORE_PRICE(state, payload) {
    state.show.morePrice = payload
  },
  SET_UPDATE_MORE_PRICE(state, payload) {
    state.show.updateMorePrice = payload
  },
  SET_ALERT_DEFAULT(state, payload) {
    state.alert.default = payload
  },
  SET_ALERT_PENDING(state, payload) {
    state.alert.pending = payload
  },
  SET_MOVE_LOCATIONS(state, payload) {
    state.move_locations = payload
  }
}

const actions = {
  async filter({ commit } , payload) {
    const response = await request({
      url: '/v1/pos/inventory/filter',
      method: 'get',
      params: payload
    })

    commit('SET_FILTER', {...response, ...{date: [payload.from, payload.to]}})
  },
  async all({ commit }, payload) {
    let response = await request({
      url: '/v2/pos/inventory/all',
      method: 'get',
      params: payload
    })

    response = { status: payload.status, ...response }

    commit('SET_PAYLOAD', payload)
    commit('SET_ALL', response)
  },
  async deeplink({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/inventory/deeplink',
      method: 'get',
      params: payload
    })

    commit('SET_DEEPLINK', response)
  },
  async dataMatch({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/inventory/data-match',
      method: 'post',
      data: payload,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })

    commit ('SET_DATA_MATCH', response)
  },
  async getDataMatch({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/inventory/data-match',
      method: 'get',
      params: payload
    })

    commit ('SET_GET_DATA_MATCH', response)
  },
  removeDataMatch({ commit }) {
    commit ('SET_DATA_MATCH', null)
  },
  async create({ rootState }, payload) {
    return await request({
      url: '/v1/pos/inventory',
      method: 'post',
      data: payload
    })
  },
  async update({ commit }, payload) {
    if(sourceToken) {
      sourceToken.cancel('Request canceled')
    }
    sourceToken = CancelToken.source()
    const response = await request.post(`/v1/pos/inventory/${payload.id}`, payload.data, {
      signal: payload.signal
    })
    commit("SET_UPDATE", response)
    return response
  },
  async updateNotes({ rootState }, payload) {
    return await request({
      url: `/v1/pos/inventory/notes/${payload.id}`,
      method: 'post',
      data: {
        notes: payload.data,
        _method: 'PUT'
      }
    })
  },
  async updateSingleSoldPrice({}, payload) {
    return await request({
      url: `/v1/pos/inventory/sold-price/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async delete({}, payload) {
    return await request({
      url: `/v1/pos/inventory/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async restore({}, payload) {
    return await request({
      url: `/v1/pos/inventory/restore/${payload}`,
      method: 'post',
    })
  },
  async reset({}, payload) {
    return await request({
      url: `/v1/pos/inventory/reset/${payload}`,
      method: 'post',
      data: {
        _method: 'PUT'
      }
    })
  },
  async createTag({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/inventory/create-tag/${payload.id}`,
      method: `POST`,
      data: payload
    })

    commit ('SET_CREATE_TAG', response)
  },
  async show({ commit }, payload) {
    const response = await request.get(`/v1/pos/inventory/show/${payload.id}`, {
      signal: payload.signal
    })

    commit('SET_SHOW', response)
  },
  async searchBackmarket({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/inventory/search-backmarket`,
      method: 'get',
      params: payload
    })

    commit('SET_BACKMARKET', response)
  },
  async showPictures({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/inventory/picture`,
      method: 'get',
      params: payload
    })

    commit('SET_SHOW_PICTURES', response)
  },
  async showNetworks({ commit }) {
    const response = await request({
      url: `/v1/pos/inventory/networks`,
      method: 'get'
    })

    commit('SET_SHOW_NETWORKS', response)
  },
  async showTransferHistories({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/inventory/transfer-history`,
      method: 'get',
      params: {
        inventory_id: payload
      }
    })

    commit('SET_SHOW_TRANSFER_HISTORIES', response)
  },
  async showSellingHistories({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/inventory/selling-history/${payload}`,
      method: 'get',
    })

    commit('SET_SHOW_SELLING_HISTORIES', response)

    return response
  },
  async storeMorePrice({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/inventory/more-price`,
      method: 'post',
      data: payload
    })

    commit('SET_STORE_MORE_PRICE', response)

    return response
  },
  async updateMorePrice({ commit }, payload) {
    await request({
      url: `/v1/pos/inventory/more-price/${payload.id}`,
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload,
      },
    })
  },
  async deleteMorePrice({ commit }, id) {
    await request({
      url: `/v1/pos/inventory/more-price/${id}`,
      method: 'post',
      data: {
        _method: "DELETE"
      }
    })
  },
  async downloadCSV({ commit, state }, params) {
    return await request({
      url: `/v1/pos/inventory/csv`,
      method: 'get',
      params: { ...state.payload, ...params},
      responseType: 'arraybuffer'
    })
  },
  async deleteShopify(_, payload){
    return await request({
      url: `/v1/pos/inventory/delete/shopify/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  alertDefault({commit}, payload) {
    if (!payload) localStorage.setItem('closedInventoryAlertDefault', 1)
    commit('SET_ALERT_DEFAULT', payload)
  },
  alertPending({commit}, payload) {
    if (!payload) localStorage.setItem('closedInventoryAlertPending', 1)
    commit('SET_ALERT_PENDING', payload)
  },
  async allMoveLocation({commit}, payload) {
    const response = await request({
      url: `/v1/pos/inventory/move-location`,
      method: 'get',
    })
    commit('SET_MOVE_LOCATIONS', response)
  },
  async moveLocation({commit}, payload) {
    return await request({
      url: `/v1/pos/inventory/move-location/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async moveLocationBulk({commit}, payload) {
    return await request({
      url: `/v1/pos/inventory/move-location/bulk`,
      method: 'post',
      data: payload
    })
  },
  async showSellBulkInstantly({commit}, payload){
    return await request({
      url: `/v1/pos/inventory/sell-instantly/show`,
      method: 'get',
      params: payload
    })
  },
  async getSellBulkInstantly(_, payload){
    return await request({
      url: `/v1/pos/inventory/sell-instantly`,
      method: 'get',
      params: payload
    })
  },
  async sellBulkInstantly(_, payload){
    return await request({
      url: `/v1/pos/inventory/sell-instantly`,
      method: 'post',
      data: payload
    })
  },
  async reconcile(_, payload) {
    const response = await request({
      url: '/v1/pos/inventory/data-match',
      method: 'post',
      data: payload
    })
    return response
  },
  async updateStatus(_, payload){
    const response = await request({
      url: `/v1/pos/inventory/status/${payload.id}`,
      method: 'put',
      data: payload.data
    })
    return response
  },
  async getColors(_, payload) {
    const response = await request({
      url: `/v1/pos/inventory/color-option`,
      method: 'get',
      params: {
        ...payload
      }
    })

    return response
  },
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}