import request  from 'utils/request'

const state = () => ({
  // statuses: [
  //   {
  //     id: 1,
  //     description: null,
  //     short_name: 'Active',
  //     status_name: 'Active',
  //     total: 0
  //   },
  //   {
  //     id: 0,
  //     description: null,
  //     short_name: 'Archived',
  //     status_name: 'Archived',
  //     total: 0
  //   }
  // ],
  all: [],
  show: {
    data: null
  }
})

const getters = {
}

const mutations = {
  SET_ALL(state, payload) {
    state.all = payload
  },
  SET_SHOW(state, payload) {
    state.show.data = payload
  }
}

const actions = {
  async all({ commit } , payload) {
    // TODO: Search by search instead separate address name etc
    const response = await request({
      url: '/v1/pos/settings/account',
      method: 'get',
      params: payload
    })

    commit('SET_ALL', response)
  },
  async create({}, payload) {
    return await request({
      url: `/v1/pos/settings/account`,
      method: 'post',
      data: payload
    })
  },
  async update({}, payload) {
    return await request({
      url: `/v1/pos/settings/account/${payload.id}`,
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload.data
      }
    })
  },
  async delete({}, payload) {
    return await request({
      url: `/v1/pos/settings/account/${payload}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async show({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/account/${payload}`,
      method: 'get'
    })

    commit('SET_SHOW', response)
    return response
  },
  async permission({ commit }, payload) {
    return await request({
      url: `/v1/pos/settings/user-permission/${payload}`,
      method: 'get'
    })
  },
  async permissionUpdate({}, payload) {
    return await request({
      url: `/v1/pos/settings/user-permission/${payload.id}`,
      method: 'post',
      data: payload.data
    })
  },
  async getMenu({}, payload){
    return await request({
      url: `/v1/pos/settings/user-permission-v2${`${!!payload ? `/${payload}` : '' }`}`,
      method: 'get',
    })
  },
  async inviteAccount({}, payload){
    return await request({
      url: `/v1/pos/settings/account/invite`,
      method: 'post',
      data: payload
    })
  },
  async inviteAccountResend({}, payload){
    return await request({
      url: `/v1/pos/settings/account/invite/resend/${payload}`,
      method: 'post'
    })
  },
  async inviteAccountRevoke({}, payload){
    return await request({
      url: `/v1/pos/settings/account/invite/revoke/${payload}`,
      method: 'post'
    })
  },
  async inviteTotalInvited(){
    const response = await request({
      url: `/v1/pos/settings/account`,
      params: {
        invited: 1
      }
    })
    return response.total_invite_sent
  },
  async getLocations({}, payload={params: {}}){
    return await request({
      url: `/v1/pos/settings/location-permission`,
      method: 'get',
      params: payload.params
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}