import request from 'utils/request'

const state = () => ({
  conversionRateChart: {},
  conversionRateDevices: {},
  conversionRateFilters: {
    source: []
  },
  conversionRateChartFilters: {
    model: [],
    source: [],
    device: []
  }
})

const getters = {}

const mutations = {
  SET_CONVERSION_RATE_CHART(state, payload) {
    state.conversionRateChart = payload
  },
  SET_CONVERSION_RATE_DEVICES(state, payload) {
    state.conversionRateDevices = payload
  },
  SET_CONVERSION_RATE_CHART_FILTERS(state, payload) {
    state.conversionRateChartFilters = payload
  },
  SET_CONVERSION_RATE_FILTERS(state, payload) {
    state.conversionRateFilters = payload
  }
}

const actions = {
  async showConversionRateChart({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/dashboard/convertionrate-chart',
      method: 'get',
      params: payload
    })
    commit('SET_CONVERSION_RATE_CHART', response)
  },
  async showConversionRateDevices({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/dashboard/device-rates',
      method: 'get',
      params: payload
    })
    commit('SET_CONVERSION_RATE_DEVICES', response)
  },
  async showConversionRateDeviceDetail(_, { id, params }) {
    const response = await request({
      url: `/v1/pos/dashboard/device-rates/${id}`,
      method: 'get',
      params
    })
    return response
  },
  async showConversionRateChartFilters({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/dashboard/convertionrate-filter',
      method: 'get',
      params: payload
    })
    commit('SET_CONVERSION_RATE_CHART_FILTERS', response)
  },
  async showConversionRateFilters({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/dashboard/device-rates-filter',
      method: 'get',
      params: payload
    })
    commit('SET_CONVERSION_RATE_FILTERS', response)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
