import request  from 'utils/request'

const state = () => ({
  all: {}
})

const getters = {
}

const mutations = {
  SET_ALL(state, payload){
    state.all = payload
  }
}

const actions = {
  async all({commit}, payload) {
    const response = await request({
      url: '/v1/pos/order/shipping-label',
      method: 'get',
      params: payload
    })
    commit('SET_ALL', response)
    return response
  },
  async shippingKitPrintBatch({}, payload){
    return await request({
      url: '/v1/pos/print/batch-shipping-kit',
      method: 'post',
      data: payload,
      responseType: 'arraybuffer'
    })
  },
  async addressLabelPrintBatch({}, payload){
    return await request({
      url: '/v1/pos/print/batch-address-label',
      method: 'post',
      data: payload
    })
  },
  async addressLabelPrintStatusBatchUpdate({}, payload){
    return await request({
      url: '/v1/pos/order/batch-status-print',
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}