import request from 'utils/request'
import { isObject } from 'utils/is'

const state = () => ({
	all: {},
	shipping_labels: [],
	shipping_label: {
		is_shipping_cost_visible: false,
		is_enable_shipping_cost: false
	},
	advance_shipping_labels: [],
	shipping_carriers: [],
	selected_shipping_carrier: null,
	advance_shipping: {
		is_enable: false,
		rules: [
			{
				name: 'condition',
				alternate_name: 'condition',
				is_enable: false,
				options: [],
				selected_option: [],
				statement: 'If',
				placeholder: 'Select Condition',
				selected_carrier: null
			},
			{
				name: 'model',
				alternate_name: 'device',
				is_enable: false,
				options: [],
				selected_option: [],
				statement: 'If',
				placeholder: 'Select Model',
				selected_carrier: null
			},
			{
				name: 'order',
				alternate_name: 'order',
				is_enable: false,
				options: [],
				selected_option: null,
				statement: 'If Offer',
				placeholder: 'Select Offer',
				selected_carrier: null,
				over_value: 0
			}
		],
		carriers: [],
		is_enable_multiple_label: '0'
	},
	processing: {
		uploadProgress: 0,
    downloadProgress: 0,
    invalid: false,
    totalInvalid: 0,
    dateInvalid: null,
    typeInvalid: 'zip',
    loadedCheckProgress: false,
    pathDownload: null,
    typeDownload: 'zip',
    downloadProgressInvalid: 0,
    pathDownloadInvalid: null,
    typeDownloadInvalid: 'zip',
    loadingDownload: false,
    loadingDownloadInvalid: false
	}
})

const getters = {}

const mutations = {
	SET_ALL(state, payload) {
		state.all = payload
	},
	SET_SHIPPING_LABEL(state, payload) {
		state.shipping_label.is_enable_shipping_cost =
			payload.is_enable_shipping_cost == 0 ? false : true
		state.shipping_label.is_enable_shipping_cost = payload.is_enable_shipping_cost
	},
	SET_SHIPPING_LABELS(state, payload) {
		if (!Array.isArray(payload)) return
		state.shipping_labels = payload
	},
	SET_ADVANCE_SHIPPING_LABELS(state, payload) {
		state.advance_shipping_labels = payload
	},
	SET_LABEL_SHIPPING_COST(state, payload) {
		state.shipping_label.is_enable_shipping_cost = payload
	},
	SET_LABEL_SHIPPING_COST_VISIBLE(state, payload) {
		state.shipping_label.is_shipping_cost_visible = payload
	},
	SET_ADVANCE_SHIPPING_ENABLED_STATUS(state, status = false) {
		state.advance_shipping.is_enable = status
	},
	SET_SHIPPING_CARRIERS(state, data) {
		state.shipping_carriers = []
		state.selected_shipping_carrier = null

		if (!isObject(data)) return
		const { shipping_rules_carriers: shippingCarriers } = data

		if (!Array.isArray(shippingCarriers)) return

		state.shipping_carriers = shippingCarriers.map(carrier => ({
			carrier_name: carrier.platform_name,
			carriers: carrier.carriers
		}))

		// const { buyback_carriers: buybackCarriers, other_carriers: otherCarriers } =
		//   data

		// state.shipping_carriers = [
		//   ...(otherCarriers
		//     ? [
		//         {
		//           carrier_name:
		//             otherCarriers.plaform_name || otherCarriers.platform_name,
		//           carriers: otherCarriers.carriers
		//         }
		//       ]
		//     : []),
		//   ...(buybackCarriers.length > 0
		//     ? [
		//         {
		//           carrier_name: 'Reusely.com',
		//           carriers: buybackCarriers
		//         }
		//       ]
		//     : [])
		// ]
	},
	SET_SELECTED_SHIPPING_CARRIER(state, carrier) {
		state.selected_shipping_carrier = carrier
	},
	SET_ADVANCE_SHIPPING(state, data) {
		if (!data.param) return
		if (!data.param.shipping_rules) return

		const sortingRules = data.param?.shipping_rules?.sort || []

		state.advance_shipping.is_enable = data.param.is_enable_shipping == 1

		for (const [ruleIndex, rule] of Object.entries(state.advance_shipping.rules)) {
			switch (rule.name) {
				case 'condition':
					state.advance_shipping.rules[ruleIndex].options = Array.isArray(data.param.conditions)
						? data.param.conditions.map(condition => ({
								name: condition.name,
								value: condition.name
							}))
						: []
					state.advance_shipping.rules[ruleIndex].is_enable =
						data.param.shipping_rules.condition.is_enable == 1
					state.advance_shipping.rules[ruleIndex].selected_option =
						data.param.shipping_rules.condition.selected || []
					state.advance_shipping.rules[ruleIndex].selected_carrier =
						data.param.shipping_rules.condition.carrier
					break
				case 'model':
					state.advance_shipping.rules[ruleIndex].options = Array.isArray(data.param.devices)
						? data.param.devices.map(device => ({
								name: device.name,
								value: device.name
							}))
						: []
					state.advance_shipping.rules[ruleIndex].is_enable =
						data.param.shipping_rules.model.is_enable == 1
					state.advance_shipping.rules[ruleIndex].selected_option =
						data.param.shipping_rules.model.selected || []
					state.advance_shipping.rules[ruleIndex].selected_carrier =
						data.param.shipping_rules.model.carrier
					break
				case 'order':
					state.advance_shipping.rules[ruleIndex].options = [
						{ name: 'Greater Than or Equal to', value: '>=' },
						{ name: 'Less Than or Equal to', value: '<=' },
						{ name: 'Equal to', value: '==' },
						{ name: 'Greater Than', value: '>' },
						{ name: 'Less Than', value: '<' }
					]
					state.advance_shipping.rules[ruleIndex].is_enable =
						data.param.shipping_rules.order.is_enable == 1
					state.advance_shipping.rules[ruleIndex].selected_option =
						data.param.shipping_rules.order.opr || null
					state.advance_shipping.rules[ruleIndex].selected_carrier =
						data.param.over_value_label || null
					state.advance_shipping.rules[ruleIndex].over_value = data.param.over_value || 0
					break
			}
		}

		const sortedShippingRules = []
		sortingRules.forEach(sortingRule => {
			const matchedRule = state.advance_shipping.rules.find(
				shippingRule => shippingRule?.alternate_name === sortingRule
			)
			if (matchedRule) sortedShippingRules.push(matchedRule)
		})

		state.advance_shipping.rules = sortedShippingRules
		state.advance_shipping.carriers = data.shipping_rules_carriers
			? data.shipping_rules_carriers
			: []
	},
	SET_ADVANCE_SHIPPING_RULES(state, rules = []) {
		state.advance_shipping.rules = rules
	},
	SET_ADVANCE_SHIPPING_SORTING_RULES(state, sorting = []) {
		state.advance_shipping.sorting_rules = sorting
	},
	UNSET_ADVANCE_SHIPPING_RULES(state) {
		state.advance_shipping.rules.forEach((rule, index) => {
			switch (rule.alternate_name) {
				case 'order':
					state.advance_shipping.rules[index].selected_option = null
					break
				default:
					state.advance_shipping.rules[index].selected_option = []
			}
			state.advance_shipping.rules[index].is_enable = false
			state.advance_shipping.rules[index].selected_carrier = null
		})
	},
	SET_ADVANCE_SHIPPING_ENABLED_MULTIPLE(state, status = false) {
		state.advance_shipping.is_enable_multiple_label = status
	},
	SET_PROCESSING_DOWNLOAD_SHIPPING_KIT_PROGRESS(state, data) {
    state.processing.downloadProgress = data.progress || 0
    state.processing.pathDownload = data?.path || null
    state.processing.typeDownload = data.type
    state.processing.loadingDownload = data.loading || false
  },
}

const actions = {
	async all({ commit }, payload) {
		const response = await request({
			url: `/v1/pos/order/shipping-label-histories`,
			method: 'get',
			params: payload
		})
		commit('SET_ALL', response)
		return response
	},
	async shippingLabels({ commit }) {
		const response = await request({
			url: `/v1/pos/settings/default-label`,
			method: 'get'
		})
		commit('SET_SHIPPING_LABEL', response.param)
		commit('SET_SHIPPING_LABELS', response.labels)
		commit('SET_ADVANCE_SHIPPING_LABELS', response.advance_labels)
		commit('SET_SHIPPING_CARRIERS', response)
		commit('SET_ADVANCE_SHIPPING', response)

		const hasDefaultLabel = response?.param?.default_label
		if (hasDefaultLabel) commit('SET_SELECTED_SHIPPING_CARRIER', response.param.default_label)
	},
	async shippingLabelCreate({}, payload) {
		return await request({
			url: `/v1/pos/order/shipping-label`,
			method: 'post',
			data: payload,
			responseType: 'arraybuffer'
		})
	},
	async shippingLabelRePrint({}, payload) {
		return await request({
			url: `/v1/pos/order/reprint-shipping-label/${payload}`,
			method: 'get',
			responseType: 'arraybuffer'
		})
	},
	async shippingLabelHistory({}, payload) {
		return await request({
			url: `/v1/pos/order/track-shipping-label/${payload}`,
			method: 'get'
		})
	},
	async updateAdvanceShippingLabel({ state }, payload) {
		let data = {
			contidion_enable: false,
			model_enable: false,
			order_enable: false,
			selected_contidion: null,
			selected_models: null,
			contidion_carrier: null,
			model_carrier: null,
			opr: null,
			over_value_label: null,
			over_value: null,
			sort: null,
			is_enable_shipping: state.advance_shipping.is_enable ? 1 : 0,
			is_enable_shipping_cost: state.shipping_label.is_enable_shipping_cost ? 1 : 0,
			...payload
		}

		state.advance_shipping.rules.forEach(rule => {
			if (rule.name === 'condition') {
				data.contidion_enable = rule.is_enable ? 1 : 0
				data.contidion_carrier = rule.selected_carrier
				data.selected_contidion = JSON.stringify(rule.selected_option)
			} else if (rule.name === 'model') {
				data.model_enable = rule.is_enable ? 1 : 0
				data.model_carrier = rule.selected_carrier
				data.selected_models = JSON.stringify(rule.selected_option)
			} else if (rule.name === 'order') {
				data.order_enable = rule.is_enable ? 1 : 0
				data.over_value = rule.over_value
				data.over_value_label = rule.selected_carrier
				data.opr = rule.selected_option
			}
		})

		data.sort = JSON.stringify(state.advance_shipping.rules.map(rule => rule.alternate_name))

		await request({
			url: '/v1/pos/settings/default-label',
			method: 'put',
			data
		})
	}
}

export default {
	namespaced: true,
	state,
	getters,
	mutations,
	actions
}
