<template>
  <div whitelabel="remove">
    <Transition name="slide-fade">
      <div v-if="show" class="paywall">
        <div class="paywall-popup">
          <div
            class="paywall-popup__image"
            :style="{
              backgroundImage: `url(${imageUrl})`,
              backgroundPosition: 'center',
              backgroundSize: 'cover'
            }"
          ></div>
          <div class="paywall-popup__content">
            <slot>
              <div>
                <h4 class="paywall-popup__title inline-block">{{ title }}  <span class="paywall-popup__badge inline-block font-normal" :style="{fontSize: '1rem'}">{{packageName}}</span></h4>
              </div>
              <p class="paywall-popup__description">
                {{ description }}
              </p>
            </slot>
          </div>
          <div class="paywall-popup__footer">
            <slot name="footer">
              <div class="flex items-center justify-between">
                <a href="#!" class="px-2" @click.prevent="handleClose"
                  >Close</a
                >
                <Button @click="handleGetPro" :loading="isLoadingBilling" color="teal">Get {{packageName}}</Button>
              </div>
            </slot>
          </div>
        </div>
      </div>
    </Transition>
  </div>
</template>
<script>
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import Button from 'bbUi/components/button/button.vue'
export default defineComponent({
  components: {
    Button
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    },
    imageUrl: {
      type: String,
      default: ''
    },
    packageName: {
      type: String,
      default: 'Pro'
    }
  },
  emits: ['update:show'],
  setup(props, { emit }) {
    const store = useStore()
    const router = useRouter()
    const isLoadingBilling = ref(false)
    const handleClose = () => {
      emit('update:show', false)
    }
    const handleGetPro = async() => {
      try{
        isLoadingBilling.value = true
        router.push({
          name: 'settingBilling'
        })
        // const response = await store.dispatch('billing/stripePortal')
        // if(response.url) window.open(response.url, '_self', 'noreferrer noopener')
        // else throw new Error('Invalid URL')
      }catch(err){
        console.error(err)
      }finally{
        isLoadingBilling.value = false
      }
    }
    return {
      isLoadingBilling,
      handleClose,
      handleGetPro
    }
  }
})
</script>
