.paywall {
  @apply flex items-center fixed top-0 left-0 w-screen h-screen overflow-hidden;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 2000;
  &-popup {
    @apply max-w-lg w-full mx-auto;
    &__image {
      @apply bg-white;
      min-height: 200px;
    }
    &__content {
      @apply p-6 bg-white;
    }
    &__footer {
      @apply p-6 bg-white;
    }
    &__title {
      @apply font-medium text-2xl mx-3;
    }
    &__badge {
      @apply rounded-full text-white py-1 px-4 bg-green-04;
    }
    &__description {
      @apply mt-6;
    }
  }
  &-popup > div:last-child {
    @apply rounded-b-lg border-t border-grey-01;
  }
  &-popup > div:first-child {
    @apply rounded-t-lg border-b border-grey-01;
  }
}
</style>
