import { socket, events } from 'constants/socket'
import { ElMessage } from 'element-plus'
import { socketCompany } from './socketCompany'

const state = () => ({})

const getters = {}

const mutations = {}

const actions = {
  init(_, payload) {
    socket.init(payload)
  },
  connect({ commit, dispatch, rootState }, payload) {
    events.subscribe('device-diagnostic', (data) => {
      commit('user/SET_BBB_SCAN', data, { root: true })
    })
    events.subscribe('shipping-label', (data) => {
      commit('buyback/online/SET_SHOW_SHIPPING_LABEL_BY_EVENT', data, {
        root: true
      })
    })
    events.subscribe('datafeed-process', (data) => {
      if (!data?.data) return
      if (data.data.progress === 100) setTimeout(async () => {
        await dispatch('user/me', null, {
          root: true
        })
        ElMessage({
          type: 'success',
          message: 'Upload Successful!'
        })
        dispatch('notification/closeNotification', 'NotificationDatafeed', { root: true })
      }, 1000)
      commit('notification/OPEN_NOTIFICATION', 'NotificationDatafeed', {
        root: true
      }),
        commit('integration/SET_DATA_FEED_PROGRESS', data.data, {
          root: true
        })
    })
    events.subscribe('books-pricing-process', (data) => {
      if (!data?.data) return
      if (data.data.progress === 100)
        setTimeout(async () => {
          ElMessage({
            type: 'success',
            message: 'Upload Successful!'
          })
          dispatch('notification/closeNotification', 'NotificationPricingBooks', { root: true })
          dispatch('notification/closeNotification', 'NotificationPricingBooksInvalid', { root: true })
          commit('product/book/SET_PROCESSING_PROGRESS', {
            progress: 0
          }, { root: true })
          await Promise.all([
            dispatch('user/me', null, {
              root: true
            }),
            dispatch('setting/buybackDevice/categories', null, { root: true })
          ])
          commit('notification/SET_NOTIFICATION_CLOSABLE', {
            name: 'NotificationPricingBooks',
            closable: true
          }, { root: true })
          const hasInvalidData = data.data.is_invalid_data == 1
          if (hasInvalidData)
            commit(
              'notification/OPEN_NOTIFICATION',
              'NotificationPricingBooksInvalid',
              { root: true }
            )
        }, 1500)
      commit('product/book/SET_PROCESSING_PROGRESS', data.data, { root: true })
    })
    events.subscribe('ghl-uninstall', (data) => {
      if (!data || !data.data) {
        commit('integration/ghl/SET_UNINSTALL', false, { root: true })
      } else commit('integration/ghl/SET_UNINSTALL', data, { root: true })
    })
    events.subscribe('import-pricing-progress', (data) => {
      if (!data?.data) return
      if (data.data.progress === 100) {
        setTimeout(async () => {
          ElMessage({
            type: 'success',
            message: 'Import Successful!'
          })
          dispatch('notification/closeNotification', 'NotificationPriceUpload', { root: true })
          dispatch('notification/closeNotification', 'NotificationPriceUploadInvalid', { root: true })
          commit('pricing/SET_PROCESSING_UPLOAD_PROGRESS', {
            progress: 0
          }, { root: true })
          const res = await Promise.all([
            dispatch('pricing/getPriceUploadInvalidCheck', null, { root: true }),
            dispatch('user/me', null, {
              root: true
            })
          ])
          commit('notification/SET_NOTIFICATION_CLOSABLE', {
            name: 'NotificationPriceUpload',
            closeable: true
          }, { root: true })
          const hasInvalidData = (res[0]?.data?.is_failed || 0) == 1
          commit('pricing/SET_PROCESSING_UPLOAD_INVALID', {
            invalid: hasInvalidData,
            total: res[0]?.data?.total_rows_invalid || 0,
            totalSuccess: res[0]?.data?.total_rows_success || 0,
            date: res[0]?.data?.last_update || null,
            type: res[0]?.data.type || 'CSV'
          }, { root: true })
          if (hasInvalidData) {
            commit(
              'notification/OPEN_NOTIFICATION',
              'NotificationPriceUploadInvalid',
              { root: true }
            )
          }
        }, 1500)
      }
      commit('pricing/SET_PROCESSING_UPLOAD_PROGRESS', data.data, { root: true })
    })
    events.subscribe('export-pricing-progress', (data) => {
      if (!data?.data) return
      if (data.data.progress > 0 && data.data.progress < 100) {
        dispatch('notification/closeNotification', 'NotificationPriceUploadDownload', { root: true })
        commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS', {
          progress: data.data?.progress || 0,
          path: data.data?.path || null,
          type: data.data?.file || 'csv',
          loading: true
        }, { root: true })
        commit(
          'notification/OPEN_NOTIFICATION',
          'NotificationPriceUploadDownload',
          { root: true }
        )
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationPriceUploadDownload',
          closable: false
        }, { root: true })
      } else if (data.data.progress === 100) {
        commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS', {
          progress: data.data?.progress,
          path: data.data?.path,
          type: data.data?.file,
          loading: true
        }, { root: true })
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationPriceUploadDownload',
          closable: true
        }, { root: true })
      }
    })
    events.subscribe('export-pricing-failed-progress', (data) => {
      if (!data?.data) return
      if (data.data.progress > 0 && data.data.progress < 100) {
        dispatch('notification/closeNotification', 'NotificationPriceUploadDownloadInvalid', { root: true })
        commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID', {
          progress: data.data?.progress || 0,
          path: data.data?.path || null,
          type: data.data?.file || 'csv',
          loading: true
        }, { root: true })
        commit(
          'notification/OPEN_NOTIFICATION',
          'NotificationPriceUploadDownloadInvalid',
          { root: true }
        )
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationPriceUploadDownloadInvalid',
          closable: false
        }, { root: true })
      } else if (data.data.progress === 100) {
        commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID', {
          progress: data.data?.progress,
          path: data.data?.path,
          type: data.data?.file,
          loading: true
        }, { root: true })
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationPriceUploadDownloadInvalid',
          closable: true
        }, { root: true })
      }
    })
    events.subscribe('alert-message', (data) => {
      if (!data?.data) return

      ElMessage({
        message: data.data.message
      })
    })
    events.subscribe('shipping-kit-batch', (data) => {
      if (!data?.data) return
      if (data.data.progress > 0 && data.data.progress < 100) {
        dispatch('notification/closeNotification', 'NotificationShippingKitDownload', { root: true })
        commit('shipping/SET_PROCESSING_DOWNLOAD_SHIPPING_KIT_PROGRESS', {
          progress: data.data?.progress || 0,
          path: data.data?.path || null,
          type: data.data?.file || 'zip',
          loading: true
        }, { root: true })
        commit(
          'notification/OPEN_NOTIFICATION',
          'NotificationShippingKitDownload',
          { root: true }
        )
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationShippingKitDownload',
          closable: false
        }, { root: true })
      } else if (data.data.progress === 100) {
        commit('shipping/SET_PROCESSING_DOWNLOAD_SHIPPING_KIT_PROGRESS', {
          progress: data.data?.progress,
          path: data.data?.path,
          type: data.data?.file,
          loading: true
        }, { root: true })
        commit('notification/SET_NOTIFICATION_CLOSABLE', {
          name: 'NotificationShippingKitDownload',
          closable: true
        }, { root: true })
      }
    })
    events.subscribe('offer-has-changes', (data) => {
      if (!data?.data) return
      ElMessage({
        message: data.data.offer_changes_notes
      })
      commit('buyback/SET_SHOW_ORDER_TO_RELOADS', data.data, { root: true })
    })
    // Export CSV Order https://app.clickup.com/t/86czhxq08
    events.subscribe('export_order_csv', ({ data }) => {
      if (!data) return
      
      ElMessage({
        message:  data?.message
      })

      commit('notification/SET_EXPORT_DATA_OFFERS_PROGRESS', data, { root: true })
    })
    // Export CSV ORder

    socketCompany(commit);
      
    socket.connecting(payload?.id, payload?.tenantId)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}