import { ALERT_ID } from 'constants/alerts'
import request from 'utils/request'

const state = () => ({
  popup: {
    isShow: true,
    isMinimized: false,
    component: [],
    closable: false
  },
  isHasUnreadNotification: null,
  exportDataOffersProgress: {},
})

const getters = {}

const mutations = {
  OPEN_NOTIFICATION(state, componentName = null) {
    if (!state.popup.isShow) state.popup.isShow = true
    if (!!state.popup.component.length) {
      if (!state.popup.component.map(i => i.name).includes(componentName)) {
        state.popup.component.push({
          name: componentName,
          closable: false,
          isMinimized: false
        })
      }
    } else {
      state.popup.component = [{
        name: componentName,
        closable: false,
        isMinimized: false
      }]
    }
  },
  CLOSE_NOTIFICATION(state, componentName = null) {
    if (!!state.popup.isShow && !!state.popup.component.length) {
      state.popup.component = state.popup.component.filter(i => i.name !== componentName)
    }
  },
  SET_COMPONENT(state, componentName = null) {
    if (!!state.popup.component.length) {
      if (!state.popup.component.includes(componentName)) {
        state.popup.component.push({
          name: componentName,
          closable: false,
          isMinimized: false
        })
      }
    } else {
      state.popup.component = [{
        name: componentName,
        closable: false,
        isMinimized: false
      }]
    }
  },
  SET_NOTIFICATION_CLOSABLE(state, data) {
    if (!!state.popup.component.length) {
      const index = state.popup.component.findIndex(i => i.name === data.name)
      state.popup.component[index].closable = data.closable
    }
  },
  MINIMIZE_NOTIFICATION(state, data) {
    if (!!state.popup.component.length) {
      const index = state.popup.component.findIndex(i => i.name === data.name)
      state.popup.component[index].isMinimized = data.isMinimize
    }
  },
  SET_HAS_UNREAD_NOTIFICATION(state, data) {
    state.isHasUnreadNotification = data.has_unread_notifications
  },
  SET_EXPORT_DATA_OFFERS_PROGRESS(state, payload) {
    state.exportDataOffersProgress = payload
  },
  RESET_EXPORT_DATA_OFFERS_PROGRESS(state) {
    state.exportDataOffersProgress = {}
  },
}

const actions = {
  async closeNotification({ dispatch, commit }, componentName) {
    if (componentName === 'NotificationPriceUploadInvalid') {
      commit('CLOSE_NOTIFICATION', componentName)
      await dispatch('alert/dismissAlert', {
        module: 'pricing', id: ALERT_ID.PRICING.INVALID_PRICE_UPLOAD_POPUP
      }, { root: true })
    } else commit('CLOSE_NOTIFICATION', componentName)
  },
  async getNotification({}, payload) {
    try {
       return await request({
        url: '/v1/pos/notification',
        method: 'get',
        params: {
          ...payload
        }
      })
    } catch (error) {
      console.log(error)
    }
  },
  async getNotificationDetail({}, payload) {
    try {
       return await request({
        url: `/v1/pos/notification/show/${payload.id}`,
        method: 'get',
      })
    } catch (error) {
      console.log(error)
    }
  },
  async markAsRead({}, payload) {
    try {
       return await request({
        url: '/v1/pos/notification/mark-as-read',
        method: 'post',
        data: {
          ...payload
        }
      })
    } catch (error) {
      console.log(error)
    }
  },
  async markAsUnread({}, payload) {
    try {
       return await request({
        url: '/v1/pos/notification/mark-as-unread',
        method: 'post',
        data: {
          ...payload
        }
      })
    } catch (error) {
      console.log(error)
    }
  },
  async markAllAsRead({}, payload) {
    try {
       return await request({
        url: '/v1/pos/notification/mark-all-as-read',
        method: 'post',
        data: {
          ...payload
        }
      })
    } catch (error) {
      console.log(error)
    }
  },
  async markAllAsUnread({}, payload) {
    try {
       return await request({
        url: '/v1/pos/notification/mark-all-as-unread',
        method: 'post',
        data: {
          ...payload
        }
      })
    } catch (error) {
      console.log(error)
    }
  },
  async listTypeNotification({}, payload) {
    try {
       return await request({
        url: '/v1/pos/notification/filter-option/type',
        method: 'get',
        params: {
          ...payload
        }
      })
    } catch (error) {
      console.log(error)
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
