const SESSION_KEY_KEYNAME = 'session-key'
const state = {
	session_key: null
}

const generateRandomKey = () => Math.random().toString(36).slice(2)
const getCurrentSessionKey = () => localStorage.getItem(SESSION_KEY_KEYNAME)
const initNewSession = () => {
	const newSessionKey = generateRandomKey()
	state.session_key = newSessionKey
	localStorage.setItem(SESSION_KEY_KEYNAME, newSessionKey)
}
const initSessionKey = () => {
	const currentSessionKey = getCurrentSessionKey()
	const hasCurrentSessionKey = currentSessionKey !== null
	if (hasCurrentSessionKey) {
		state.session_key = currentSessionKey
		return
	}
	initNewSession()
}
const destroySession = () => localStorage.removeItem(SESSION_KEY_KEYNAME)
const sessionListener = (session) => {
	if (session.key === SESSION_KEY_KEYNAME) {
		if (state.session_key !== session.newValue) location.reload()
	}
}

export {
	state,
	generateRandomKey,
	getCurrentSessionKey,
	initNewSession,
	initSessionKey,
	destroySession,
	sessionListener
}
