export const uniqueArray = items => {
  return items.filter((item, index, self) => self.indexOf(item) === index)
}

export const addMixinPrefix = (items, prefix) => {
  return items.map(item => item((prefix)))
}

export const objectIndexer = (obj, keys) => {
  if( typeof keys === 'string' ) keys = keys.split('.')
  if( typeof obj !== 'object' || !Array.isArray(keys) ) return obj
  if( keys.length === 0 ) return obj
  let indexedObject = obj[keys[0]]
  keys.shift()
  return objectIndexer(indexedObject, keys)
}