import { ALERT_ID } from 'constants/alerts'
import request from 'utils/request'
import { setSessionExpiration } from 'utils/local-storage'

const defineRestrictedRoutes = (routes = [], restrictedRoutes = []) => {
  for (let route of routes) {
    const isRestricted = route.status == 0
    if (isRestricted) restrictedRoutes.push(route.slug)
    const hasSubChild = route.child?.length > 0
    if (hasSubChild) defineRestrictedRoutes(route.child, restrictedRoutes)
  }
  return restrictedRoutes
}

const state = () => ({
  me: null,
  loggedIn: false,
  permission: false,
  package: null,
  isTrialEnd: 0,
  isDatamatchProcessing: 0,
  isUnpaidBilling: 0,
  restrictedRoutes: [],
  bbbScan: undefined,
  checkoutStatus: false,
  timeLogged: undefined,
	socketHit: 0
})

const getters = {
}

const mutations = {
  SET_LOGGED_IN(state, payload) {
    if (payload.still_login) {
      const dateTime = new Date(Date.now()).getTime() + 3600000
      state.timeLogged = dateTime
    } else {
      state.timeLogged = undefined
    }

    state.loggedIn = payload.still_login
    state.permission = payload.permission
    state.isTrialEnd = payload.is_trial_end && !payload.subscription_id ? 1 : 0
    state.isDatamatchProcessing = payload.is_datamatch_processing
    state.isUnpaidBilling = payload.is_unpaid_billing
    const loginTime = payload.login_time
    const isExistLoginTime = !!loginTime || loginTime !== null
    if(isExistLoginTime) setSessionExpiration(loginTime) // expired login time, unix format
  },
  SET_USER(state, payload) {
    state.me = {
      ...payload,
      companies: payload.companies && Array.isArray(payload.companies) ? payload.companies : []
    }
    const mainMenuRoutes = payload.menu_v2.menus || []
    const settingMenuRoutes = payload.menu_v2.setting || []
    state.restrictedRoutes = defineRestrictedRoutes([...mainMenuRoutes, ...settingMenuRoutes])
    window.COMPANY_CURRENCY = state.me.currency
    window.COMPANY_COUNTRY_CODE = state.me.country_code
  },
  SET_PACKAGE(state, payload) {
    if (payload === 1) state.package = 'local'
    else if (payload === 2) state.package = 'national'
    else if (payload === 3) state.package = 'company'
    else if (payload === 4) state.package = 'enterprise'
  },
  SET_BBB_SCAN(state, payload) {
    state.bbbScan = payload
    localStorage.setItem('bbb_scan', JSON.stringify(state.bbbScan))
  },
  SET_CHECKOUT_STATUS(state, payload) {
    state.checkoutStatus = payload
  },
  SET_LOG_OUT(state) {
    state.me = null
    state.loggedIn = false
    state.permission = false
    state.package = null
    state.isTrialEnd = 0
    state.isDatamatchProcessing = 0
    state.isUnpaidBilling = 0
    state.restrictedRoutes = []
    state.bbbScan = undefined
    state.checkoutStatus = false
    state.timeLogged = undefined
  },
	SET_SOCKET_HIT(state, payload) {
		state.socketHit = payload
	}
}

const actions = {
  async loginStatus({ commit, state }, payload) {
    const dateNow = new Date(Date.now())
    const diff = state.timeLogged - dateNow.getTime()
    if (diff === 0 || state.timeLogged === undefined) {
      const response = await request({
        url: `/v1/login-status`,
        method: 'get',
        params: payload
      })
      commit('SET_LOGGED_IN', response)
    }
  },
  async login({ }, payload) {
    const response = await request({
      url: `/v1/pos/login`,
      method: 'post',
      data: payload
    })
    return response
  },
  async loginToken({ }, payload) {
    return await request({
      url: `/v1/pos/login/token`,
      method: 'post',
      data: {
        ...(
          typeof payload !== 'object'
            ? { token: payload }
            : {
              token: payload.token,
              company_token: payload.company
            }
        )
      }
    })
  },
  async forgotPassword({}, payload) {
    return await request({
      url: '/v1/pos/forgot/request/admin',
      method: 'post',
      data: payload
    })
  },
  async resetPassword({}, payload) {
    return await request({
      url: '/v1/pos/forgot/reset/admin',
      method: 'post',
      data: payload
    })
  },
  async profileUpdate({}, payload) {
    return await request({
      url: '/v1/pos/admin',
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload
      }
    })
  },
  async passwordUpdate({}, payload) {
    return await request({
      url: '/v1/pos/admin/update-password',
      method: 'post',
      data: {
        _method: 'PUT',
        ...payload
      }
    })
  },
  async passwordCheck({}, payload) {
    return await request({
      url: '/v1/pos/admin/check-password',
      method: 'post',
      data: payload
    })
  },
  async logout({ commit }, payload) {
    await request({
      url: '/v1/pos/logout',
      method: 'post'
    })
    commit('SET_LOG_OUT')
    commit('alert/RESET_ALERTS', null, { root: true })
    window.localStorage.setItem('token', '')
		window.localStorage.removeItem('region_domain')
		window.localStorage.removeItem('region_socket')
  },
  async me({ commit, dispatch, rootState, state, rootGetters}, params={}) {
    const response = await request({
      url: '/v1/pos/me',
      method: 'get',
      params
    })
    window.email = response.email
    window.name = `${response.first_name} ${response.last_name}`

    commit('SET_USER', response)
    commit('SET_PACKAGE', response.package_id)

    if (response.data_process) {
      const hasBackgroundProcessPricing =
        response.data_process.is_run_queue_pricing == 1 &&
        parseInt(response.data_process.percentage_data_processing) < 100
      if (hasBackgroundProcessPricing) {
        commit(
          'product/book/SET_PROCESSING_PROGRESS',
          {
            progress: parseInt(response.data_process.percentage_data_processing)
          },
          {
            root: true
          }
        )
        commit('notification/OPEN_NOTIFICATION', 'NotificationPricingBooks', {
          root: true
        })
      }
      const hasBackgroundProcessDatafeed = parseInt(`${response.data_process.datafeed_percentage}`) < 100 && parseInt(`${response.data_process.datafeed_percentage}`) > 0
      if (hasBackgroundProcessDatafeed) {
        commit('integration/SET_DATA_FEED_PROGRESS', { progress: parseInt(`${response.data_process.datafeed_percentage}`) }, {
          root: true
        })
        commit('notification/OPEN_NOTIFICATION', 'NotificationDatafeed', {
          root: true
        })
      }
      if (response.package_id == 17) {
        const hasBackgroundProcessUploadPricing = parseInt(response.data_process.is_import_pricing_running) || 0
        if (hasBackgroundProcessUploadPricing) {
          commit('notification/OPEN_NOTIFICATION', 'NotificationPriceUpload', {
            root: true
          })
          commit('pricing/SET_PROCESSING_UPLOAD_PROGRESS', {
            progress: parseInt(response.data_process.is_import_pricing_running)
          }, { root: true })
        }
        const isAlertPrice = rootGetters['alert/isAlertActive']('pricing', ALERT_ID.PRICING.INVALID_PRICE_UPLOAD)
        const isPopupInvalidPrice = rootGetters['alert/isAlertActive']('pricing', ALERT_ID.PRICING.INVALID_PRICE_UPLOAD_POPUP)
        if (isAlertPrice === undefined) {
          dispatch('alert/triggerAlert', {
            module: 'pricing', id: ALERT_ID.PRICING.INVALID_PRICE_UPLOAD
          }, { root: true })
        }
        if (isPopupInvalidPrice === undefined) {
          dispatch('alert/triggerAlert', {
            module: 'pricing', id: ALERT_ID.PRICING.INVALID_PRICE_UPLOAD_POPUP
          }, { root: true })
        }
        if (!rootState.pricing?.processing?.loadedCheckProgress) {
          await dispatch('pricing/getPriceUploadInvalidCheck', null, { root: true })
            .then(res => {
              commit('pricing/SET_PROCESSING_UPLOAD_INVALID', {
                invalid: res.data.is_failed == 1,
                total: res.data.total_rows_invalid,
                totalSuccess: res.data.total_rows_success,
                date: res.data.last_update,
                type: res.data.type
              }, { root: true })
              if (rootState.pricing.processing.invalid && !!isPopupInvalidPrice) {
                commit('notification/OPEN_NOTIFICATION', 'NotificationPriceUploadInvalid', {
                  root: true
                })
                commit('notification/SET_NOTIFICATION_CLOSABLE', {
                  name: 'NotificationPriceUploadInvalid',
                  closable: true
                }, { root: true })
              }
            })
            .catch(() => {
              commit('pricing/SET_PROCESSING_UPLOAD_INVALID', {
                invalid: false,
                total: 0,
                totalSuccess: 0,
                date: null,
                type: 'CSV'
              }, { root: true })
            })
        }
      }
      const isDownloadTemplate = rootState.user.me.data_process.is_export_pricing_running
      const isDownloadInvalidTemplate = rootState.user.me.data_process.is_export_pricing_failed_running
      if (isDownloadTemplate == 1) {
        commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS', {
          progress: 1,
          path: null,
          type: 'csv',
          loading: true
        }, { root: true })
        commit(
          'notification/OPEN_NOTIFICATION',
          'NotificationPriceUploadDownload',
          { root: true }
        )
      }
      if (isDownloadInvalidTemplate == 1) {
        commit('pricing/SET_PROCESSING_DOWNLOAD_PROGRESS_INVALID', {
          progress: 1,
          path: null,
          type: 'csv',
          loading: true
        }, { root: true })
        commit(
          'notification/OPEN_NOTIFICATION',
          'NotificationPriceUploadDownloadInvalid',
          { root: true }
        )
      }
    }
    localStorage.removeItem('bbb_scan')

		if (state.socketHit === 0) {
			dispatch(
				'socket/init',
				{
					token: localStorage.getItem('token'),
					buyback_company_token: response.buyback_company_token
				},
				{ root: true }
			)
			dispatch(
				'socket/connect',
				{
					id: response.socket_io_chanel_id,
					tenantId: response.tenant_id
				},
				{ root: true }
			)

			commit('SET_SOCKET_HIT', 1)
		}
  },
  async setConnection({}, payload) {
    await request({
      url: '/v1/config/set-connection',
      method: 'post',
      // data: {
      //   session_id: payload
      // }
      data: payload
    })
  },
  async checkout({}, payload) {
    const response = await request({
      url: '/v1/checkout/billing/success',
      method: 'get',
      params: {
        session_id: payload
      }
    })
    return response
  },
  async register({ }, payload) {
    return await request({
      url: '/v4/register',
      method: 'post',
      data: payload
    })
  },
  async createTenant({ }, payload) {
    return await request({
      url: '/v1/config/create-tenant',
      method: 'post',
      data: payload
    })
  },
  async switchCompany({}, payload) {
    await request({
      url: '/v1/pos/settings/account/switch',
      method: 'post',
      data: payload
    })
  },
  async verify({}, payload) {
    try {
      return await request({
        url: '/v4/register/verify',
        method: 'post',
        data: payload.data,
        params: payload.params
      })
    }
    catch(e) {
    }
  },
  async verifyInvitationCode({}, payload){
    return await request({
      url: '/v4/register/verify/code',
      method: 'post',
      data: payload.data,
      params: payload.params
    })
  },
  async registerCheckout({}, payload){
    return await request({
      url: '/v4/register/checkout',
      method: 'post',
      data: payload
    })
  },
  async getCurrencies(){
    return await request({
      url: '/v4/register/currency',
      method: 'get'
    })
  },
  async getCountries(){
    return await request({
      url: '/v4/register/country',
      method: 'get'
    })
  },
  async verifyInvite({}, payload){
    return await request({
      url: `/v1/admin/verify`,
      method: 'post',
      params: payload.params,
      data: payload.data
    })
  },
  async getVerifyInvite({}, params){
    return await request({
      url: `/v1/admin/verify`,
      method: 'get',
      params: params
    })
  },
  async intercomTags() {
    await request({
      url: '/v1/pos/intercom',
      method: 'get'
    })
  },
  async requestVerificationCode(_, payload){
    return await request({
      url: '/v1/pos/settings/account/sendcode',
      method: 'post',
      data: payload
    })
  },
  async requestVerificationCodeLogin(_, payload){
    return await request({
      url: '/v1/pos/login/sendcode',
      method: 'post',
      data: payload
    })
  },
  async verifyVerificationCode(_, payload){
    return await request({
      url: '/v1/pos/settings/account/sendcode/verify',
      method: 'post',
      data: payload
    })
  },
  resetScanQR({ commit }, payload) {
    const oldData = localStorage.getItem('bbb_scan') && localStorage.getItem('bbb_scan') != 'undefined'
      ? JSON.parse(localStorage.getItem('bbb_scan'))
      : undefined

    if (oldData) {
      localStorage.setItem('bbb_scan', JSON.stringify({
        ...oldData,
        [`${payload.key}`]: payload.value
      }))
      commit('SET_BBB_SCAN', JSON.parse(localStorage.getItem('bbb_scan')))
    }
  },
  removeScanQR({ commit }) {
    localStorage.removeItem('bbb_scan')
    commit('SET_BBB_SCAN', undefined)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
