import { computed, ref } from 'vue'
import store from 'store'
import { isFunction } from 'utils/is'
import { anchorDownload, anchorPrint } from 'utils/element'

const isWhiteLabel = computed(() => window.isWhiteLabel || false)

const usePrint = () => {
  const loading = ref(false)

  const printKit = async (payload, callback = () => {}) => {
    loading.value = true
    try {
      const response = await store.dispatch(
        `buyback/whiteLabelPrintShippingKit`,
        payload
      )
      anchorDownload(response, 'shipping-kit', 'application/octet-stream')
      isFunction(callback) && callback(response)
    } finally {
      loading.value = false
    }
  }

  const printQR = async (payload, callback = () => {}) => {
    loading.value = true
    try {
      const response = await store.dispatch(
        `buyback/whiteLabelPrintShippingQRCode`,
        payload
      )
      anchorPrint(response, 'image/png', false)
    } finally {
      loading.value = false
      isFunction(callback) && callback()
    }
  }

  return {
    loading,
    printKit,
    printQR
  }
}

const useFeatureFlag = () => {
  const isUsedProductOrigin = computed(() => window.location.origin.includes('usedproducts'))
  const isAllowMultipleAccount = computed(() => {
    return !isUsedProductOrigin.value
  })
  const isWhitelabelWidget = computed(() => {
    return !isUsedProductOrigin.value
  })
  const isAllowPriceEditorCustomRequest = computed(() => {
    return !isUsedProductOrigin.value
  })
  const isShowFacebookPixelLearnMoreButton = computed(() => {
    return !isUsedProductOrigin.value
  })
  const features = computed(() => ({
    multipleAccount: isAllowMultipleAccount.value,
    whitelabelWidget: isWhitelabelWidget.value,
    priceEditor: {
      customRequest: isAllowPriceEditorCustomRequest.value
    },
    integration: {
      facebookPixel: {
        learnMoreButton: isShowFacebookPixelLearnMoreButton.value
      }
    }
  }))
  return {
    features
  }
}

export { isWhiteLabel, usePrint, useFeatureFlag }
