import { parsePhoneNumber } from 'libphonenumber-js'
import { currencies, currencyByCountries } from 'constants/currency'

const money = (
  number = '',
  options = { currency: undefined, country: undefined },
  decimal = undefined
) => {
  const { currency = window.COMPANY_CURRENCY, country = window.COMPANY_COUNTRY_CODE } = options
  if (number === null || number === undefined) number = '0'
  const n = number.toString().replaceAll(',', '')
  let currencyRule =
    currencies.find((item) => item.currency === currency) ||
    currencies.find((item) => item.currency === 'ALL')
  const currencyRuleByCountry = currencyByCountries.find(
    (item) => item.country === country
  )
  if (currencyRuleByCountry) currencyRule = currencyRuleByCountry

  const noDecimal = number % 1 === 0
  let decimalPlaces = noDecimal
    ? currencyRule?.clear_decimal
      ? 0
      : currencyRule?.rounded || 2
    : currencyRule?.rounded || 2
  if (decimal !== undefined && noDecimal) decimalPlaces = 0
  // return parseFloat(n).toLocaleString('en-US', {
  //   minimumFractionDigits: 2
  // })
  const format = new Intl.NumberFormat(currencyRule?.iso || 'en-US', {
    ...(decimal === undefined && { minimumFractionDigits: decimalPlaces }),
    maximumFractionDigits: decimalPlaces
  }).format(n)

  return format
}

const currency = (me, value = '') => {
  // Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(value);
  // const num = value.toString().replaceAll(',', '')
  // const n = parseFloat(num).toLocaleString('en-US', {
  //   minimumFractionDigits: 2
  // })
  const n = money(value, {
    currency: me.currency
  })

  return me.currency_prefix ? `${me.currency_symbol}${n}` : `${n} ${me.currency_symbol}`
}
const toNumber = ({ rule, value }) => {
  if (rule) {
    const removedThousand = value
      .replace(/[^0-9,.]/g, '')
      .split(rule.thousand)
      .join('')
    const removedDecimal = removedThousand.split(rule.decimal).join('.')
    return Number(removedDecimal)
  }
  return value
}

const filterNumber = $event => {
  const keyCode = ($event.keyCode ? $event.keyCode : $event.which)
  if (keyCode < 48 || keyCode > 57) {
    $event.preventDefault()
  }
}

const filterDecimal = $event => {
  const chars = [...$event.target.value]
  const keyCode = ($event.keyCode ? $event.keyCode : $event.which)

  if (keyCode < 48 || keyCode > 57) {
    if (keyCode != 46) {
      $event.preventDefault()
    }
    if (keyCode == 46) {
      chars.map(char => {
        if (char === '.') {
          $event.preventDefault()
        }
      })
    }
  }
}

const filterNumberAllowDecimal = $event => {
  const keyCode = ($event.keyCode ? $event.keyCode : $event.which)
  if (keyCode == 46) return
  if (keyCode < 48 || keyCode > 57) {
    $event.preventDefault()
  }
}

const phoneNumber = (number, format='NATIONAL') => {
  if(!!number){
    try{
      const parsedPhoneNumber = parsePhoneNumber(String(number))
      if(format === 'INTERNATIONAL') return parsedPhoneNumber.formatInternational()
      return parsedPhoneNumber.formatNational()
    }catch{
      return number
    }
  }
  return number
}

function randomNumberBetween(min=0, max=100) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

const fileSizeFormat = (sizeInBytes) => {
  if (sizeInBytes > Math.pow(1000, 3))
    return `${(sizeInBytes / Math.pow(1000, 3)).toFixed(2)} TB`
  if (sizeInBytes > Math.pow(1000, 2))
    return `${(sizeInBytes / Math.pow(1000, 2)).toFixed(2)} MB`
  if (sizeInBytes > Math.pow(1000, 1))
    return `${(sizeInBytes / Math.pow(1000, 1)).toFixed(2)} KB`
  return `${sizeInBytes} Bytes`
}

export {
  money,
  currency,
  toNumber,
  filterNumber,
  filterDecimal,
  filterNumberAllowDecimal,
  phoneNumber,
  randomNumberBetween,
  fileSizeFormat
}