import router from 'router'
import store from 'store'
import settings from './setting'
import Progress from 'nprogress'
import { Userpilot } from 'userpilot'
import { state } from 'composable/loading'
import { initNewSession } from 'utils/session'
import { useSetting } from 'store/pinia/setting'
import { tryCatchRequest } from '@/utils/request'

var timeouts = []
var countdown = null
var countdownTime = 1000 * 86400
var userbackLoaded = undefined

Progress.configure({ showSpinner: false });
// User module
// Permission module
// Internalization

const getPageTitle = (title, company = undefined) => {
  if(title) {
    return (company && `${title} - ${company}`) || `${title} - ${settings.title}`
  }
  return `${company}` || `${settings.title}`
}

router.beforeEach(async (to, from, next) => {
  const storePiniaSetting = useSetting()
  // timeouts.push(setTimeout(() => {
  //   window.location.reload()
  // }, 10000))

  Progress.start()
  const currentPath = to.path.split('/').filter(item => item).shift()
  if (
    currentPath &&
    store.state.user.me &&
    store.state.setting.marketplace &&
    !store.state.setting.marketplace.marketplace_filter &&
    ['order', 'order-local'].includes(currentPath)
  ) {
    // REMOVE MARKETPLACE FEATURE
      // await store.dispatch('setting/marketplace/getFilterMarketers', {
      //   type: currentPath === 'order-local' ? 'local-order' : 'order',
      //   page: 1
      // })
    // REMOVE MARKETPLACE FEATURE
  } else if (!['order', 'order-local'].includes(currentPath) && store.state.setting.marketplace.marketplace_filter) {
    store.commit('setting/marketplace/SET_MARKETPLACE_FILTER', undefined)
  }
  if(to.path === '/dashboard' && from.name === 'login') state.splashLoading.isLoading = true
  if(to.name === 'login' && to.query.token) {
    try {
			await store.dispatch('user/logout')

			window.localStorage.removeItem('region_domain')
			window.localStorage.removeItem('region_socket')

			const onLoginToken = async () => {
				const response = await store.dispatch('user/loginToken', to.query.token)

				if (response.re_login) {
					window.localStorage.setItem('region_domain', response.domain)
					window.localStorage.setItem('region_socket', response.socket)

					await onLoginToken()

					return
				}

				window.localStorage.setItem('token', to.query.token)

				initNewSession()
				next({
					path: '/dashboard'
				})
			}

			await onLoginToken()
    }
    catch(e) {
      await store.dispatch('user/logout')
      next(`/login`)
      Progress.done()
    }
  }

  if(to.name === 'verify' && from.name === undefined) {
    window.localStorage.removeItem('connection_name')
    next('/login')
  }

  // release timeout before hit login status API
  clearTimeout(countdown)
	if(!['shippingKit', 'shippingQRCode', 'registerCheckout', 'error'].includes(to.name)) {
		try {
			await store.dispatch('user/loginStatus', {path: `/${to.fullPath.split('/')[1]}`})
		}
		catch(e) {
			if (e.response.status >= 404) {
				const isRegionDomainExist = !!window.localStorage.getItem('region_domain') || !!window.localStorage.getItem('region_socket')

				window.localStorage.removeItem('region_domain')
				window.localStorage.removeItem('region_socket')

				if (isRegionDomainExist) window.location.reload()
			}
		}
	}

  let checkout = false

  if (to.name === 'checkout') {
    checkout = true
  }

  if (to.name === 'settingPlanProcessing') {
    checkout = true
  }

  if (to.name === 'settingPlan') {
    checkout = true
  }

  // renew timeout then auto logout user when timeout is over
  const excludedPath = ['login', 'register', 'verify', 'forgotPassword', 'resetPassword', 'checkout', 'shippingKit', 'shippingQRCode']

  if(!excludedPath.includes(to.name)){
    countdown = setTimeout(async () => {
      await store.dispatch('user/logout')
      store.commit('SET_ROUTES', [])
      store.state.routes.forEach(item => {
        router.removeRoute(item.name)
      })
      window.CustomerSupport.use('shutdown')
      window.location.href='/login?expired=1'
    }, countdownTime)
  }

  if(store.state.user.loggedIn) {
    if (to.name === 'inviteVerify') {
      await store.dispatch('user/logout')
      next()
      Progress.done()
    }
    if (to.name === 'registerCheckout' && (store.state.user.isTrialEnd || store.state.user.isUnpaidBilling || to.query.session_id)) {
      try {
        await store.dispatch('billing/checkoutRemoveLocation')
      } catch (error) {
        console.log('Ignored error in checkoutRemoveLocation:', error)
      } finally {
        await store.dispatch('user/registerCheckout', {
          session_id: to.query.session_id
        }).then(() => {
          store.commit('user/SET_CHECKOUT_STATUS', true)
        }).catch(() => {
          store.commit('user/SET_CHECKOUT_STATUS', false)
        })
      }
      await store.dispatch('user/logout')
      next()
      Progress.done()
    }

    if(store.state.user.permission){
      if(to.meta.auth === false) {
        if (!checkout) {
          next({
            path: '/dashboard'
          })
        } else {
          next()
        }
        Progress.done()
      }
      else {
        if(!store.state.user.me) {
          try {
            await store.dispatch('user/me')
            storePiniaSetting.setMenu(store.state.user.me.menu_v2)
            if (!storePiniaSetting.company.form) await storePiniaSetting.$getSettingCompanyProfile()
            if (!storePiniaSetting.countries.result) await storePiniaSetting.$getSettingCountry()
            if (!storePiniaSetting.currencies.result) await storePiniaSetting.$getSettingCurrency()
            if (!window.isWhiteLabel) await store.dispatch('user/intercomTags')
            window.email = store.state.user.me.email
            store.dispatch('generateRoutes', [])
            store.state.routes.forEach(item => {
              router.addRoute(item)
            })

            // Add onboarding validation, redirect to register onboarding page if is_onboarding === true
            const isUnfinishedOnboarding = store.state.user.me.onboarding.is_onboarding === true
            const isCurrentRouteOutsideOnboarding = to.matched.length === 0 || to.matched[0]?.name !== 'onboarding'
            if (isUnfinishedOnboarding && isCurrentRouteOutsideOnboarding) {
              next({
                name: 'onboarding'
              })
              return
            }
            next({
              ...to,
              replace: true
            })
          }
          catch(e) {
            // await store.dispatch('user/logout')
            // Show error
            // next(`/login?redirect=${to.path}`)
            Progress.done()
          }
        }
        else {
          if (to.name !== 'settingBilling' && (!!store.state.user.isUnpaidBilling || !!store.state.user.isTrialEnd)) {
            return next('/setting/plans-billings')
          }
          if (to.name) {
            const isRestrictedRoute =
              store.state.user.restrictedRoutes.includes(to.name) ||
              store.state.user.restrictedRoutes.includes(to.meta.route_group)
            if (isRestrictedRoute) next('/dashboard')
          }
          if (to.name === 'settingMarketplace' && window.isWhiteLabel) {
            return next({
              name: 'setting',
              replace: true
            })
          }
          next()
        }
      }
    }else{
      next({
        path: '/dashboard'
      })
    }

  }
  else {
    if (to.name === 'registerCheckout') {
      try {
        await store.dispatch('billing/checkoutRemoveLocation')
      } catch (error) {
        console.log('Ignored error in checkoutRemoveLocation:', error)
      } finally {
        await store.dispatch('user/registerCheckout', {
          session_id: to.query.session_id
        }).then(() => {
          store.commit('user/SET_CHECKOUT_STATUS', true)
        }).catch(() => {
          store.commit('user/SET_CHECKOUT_STATUS', false)
        })
      }
    }

    if (to.path.includes('highlevel-config') && !!to.query.code) {
      localStorage.setItem('goHighLevelToken', JSON.stringify({
        path: to.path,
        query: to.query
      }))
    }

    if((!to.meta.auth && to.meta.auth !== false) || to.meta.auth === false) {
      if(to.path === '/' || to.name === undefined) next('/login')
      else next()
    }
    else {
      next(`/login?redirect=${to.path}`)
      Progress.done()
    }
  }

  if (typeof userbackLoaded !== 'boolean') {
    userbackLoaded = Userback?.isLoaded() || false
    console.log(
      `%cUserback ${userbackLoaded ? 'Loaded' : 'Unloaded'}!`,
      `color: #383737; background: ${userbackLoaded ? '#8AFF8F' : '#FF496C'}; padding: 4px 12px; border-radius: 6px;`
    )
  }

  observeBody('body')
})

router.afterEach((to, from) => {
  timeouts.forEach(timeout => clearInterval(timeout))
  timeouts = []
  Progress.done()
  state.splashLoading.isLoading = false
  if (window.isWhiteLabel) {
    document.title = getPageTitle(to.meta.title, window.whiteLabel.name)
  } else document.title = getPageTitle(to.meta.title)
  if (from.path === '/login' && !!localStorage.getItem('goHighLevelToken')) {
    localStorage.removeItem('goHighLevelToken')
  }
  Userpilot.reload()
})
