import axios from 'axios'
import request  from 'utils/request'

let cancelTokenStarting = undefined

const state = () => ({
  show: {
    data: null,
    update: null,
  },
  showFAQ: {
    data: null,
    update: null
  },
  showCustomStartingPoint: {
    Brands: [],
    Categories: [],
    Devices: []
  },
  dataSortOrder: []
})

const getters = {
}

const mutations = {
  SET_SHOW(state, response) {
    if (window.isWhiteLabel) {
      let source = response.source.split('\n')
      const indexSource = source.findIndex(item => item.includes('disableFloatButton'))
      source.splice(indexSource, 0, '            whiteLabel: true,')
      response.source = source.join('\n')
    }
    state.show.data = response
  },
  SET_UPDATE(state, response) {
    state.show.update = response
  },
  SET_SHOW_FAQ(state, response) {
    state.showFAQ.data = response
  },
  SET_UPDATE_FAQ(state, response) {
    state.showFAQ.update = response
  },
  SET_SHOW_CUSTOM_STARTING_POINT(state, response){
    state.showCustomStartingPoint.Brands = response.result.Brands
    state.showCustomStartingPoint.Categories = response.result.Categories
    state.showCustomStartingPoint.Devices = response.result.Devices
    state.showCustomStartingPoint.Models = response.result.Models
  },
  SET_SORT_ORDER(state, payload) {
    state.dataSortOrder = payload
  }
}

const actions = {
  async show({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/widget`,
      method: 'get'
    })

    commit('SET_SHOW', response)
    return response
  },
  async update({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/widget`,
      method: `put`,
      data: {
        _method: 'PUT',
        ...payload
      }
    })

    commit ('SET_UPDATE', response)
  },
  async showFAQ({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/faq`,
      method: 'get',
      params: {
        faq_category_id: 4
      }
    })
    commit('SET_SHOW_FAQ', response)
    return response
  },
  async addFAQ({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/faq`,
      method: 'post',
      data: {
        ...payload
      }
    })
    commit ('SET_UPDATE_FAQ', response)
  },
  async deleteFAQ({ commit }, id) {
    const response = await request({
      url: `/v1/pos/settings/faq/${id}`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
    commit ('SET_UPDATE_FAQ', response)
  },
  async updateFAQ({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/faq/${payload.id}`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
    commit ('SET_UPDATE_FAQ', response)
  },
  async sortFAQ({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/faq/sort`,
      method: 'post',
      data: {
        sorted_id: payload,
        _method: 'PUT'
      }
    })
    commit ('SET_UPDATE_FAQ', response)
  },
  async removeBranding(_, payload){
    return await request({
      url: `/v1/pos/settings/widget/remove-brand`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })
  },
  async customStartingPoint({commit}, params){
    if (cancelTokenStarting) {
      cancelTokenStarting.cancel('Cancelled')
    }
    cancelTokenStarting = axios.CancelToken.source()

    const response = await request({
      url: `/v1/pos/settings/widget/custom-starting-point`,
      params: params,
      cancelToken: cancelTokenStarting.token
    })
    commit('SET_SHOW_CUSTOM_STARTING_POINT', response)
  },
  async showSortOrder({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/widget/devices',
      method: 'get'
    })
    const data = {
      key: response.key,
      ...(response.products && { data: response.products }),
      ...(response.brands && { data: response.brands }),
      ...(response.categories && { data: response.categories })
    }
    commit('SET_SORT_ORDER', data)
    return data
  },
  async showSortOrderChild({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/widget/devices/${payload.slug}`,
      method: 'get',
      params: {
        key: payload.key
      }
    })
    return response
  },
  async updateSortOrder({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/widget/devices`,
      method: 'put',
      data: payload
    })
    return response
  },
  async showLanguages() {
    const response = await request({
      url: '/v1/pos/settings/lang/list',
      method: 'get'
    })

    return response
  },
  async updateLanguage({}, payload) {
    const response = await request({
      url: '/v1/pos/settings/lang',
      method: 'post',
      data: payload
    })

    return response
  },
  async showCustomPurchasing({}) {
    const response = await request({
      url: `/v1/pos/settings/widget/purchasing-method/custom-purcashing-button`,
      method: 'get'
    })

    return response
  },
  async updateCustomPurchasing({}, payload) {
    const response = await request({
      url: `/v1/pos/settings/widget/purchasing-method/custom-purcashing-button`,
      method: 'post',
      data: payload,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    return response
  },
  async locations({}, payload) {
    const newPayload = { ...payload }

    newPayload.cancelToken && delete newPayload.cancelToken

    const response = await request({
      url: '/v1/pos/settings/widget/custom-starting-point/location',
      method: 'get',
      params: newPayload,
      ...(payload.cancelToken && { cancelToken: payload.cancelToken })
    })

    return response
  },
  async combinations({}, payload) {
    const newPayload = { ...payload }

    newPayload.cancelToken && delete newPayload.cancelToken

    const response = await request({
      url: '/v1/pos/settings/widget/custom-starting-point/combination',
      method: 'get',
      params: newPayload,
      ...(payload.cancelToken && { cancelToken: payload.cancelToken })
    })

    return response
  },
  async combinationToken({}, payload) {
    const newPayload = { ...payload }

    newPayload.cancelToken && delete newPayload.cancelToken

    const response = await request({
      url: '/v1/pos/settings/widget/custom-starting-point/combination-token',
      method: 'get',
      params: newPayload,
      ...(payload.cancelToken && { cancelToken: payload.cancelToken })
    })

    return response
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}