import request from 'utils/request'

const state = () => ({
  loading: {
    customDomain: {
      add: false,
      verify: false,
      reset: false
    },
    customerDomain: false,
    general: {
      language: false
    }
  },
  customerDomain: {
    domain: null,
    dnsRecords: [],
    isSSLProcessing: false,
    customerPortalLogin: null,
    customerPortalFavicon: null,
    defaultDomain: null
  },
  customDomain: {
    domain: null,
    verified: false
  },
  general: {
    language: null
  }
})

const getters = {
  isCustomDomain(state) {
    return state.customerDomain.domain !== null
  },
  isVerifiedCustomDomain(state) {
    return state.customDomain.verified
  },
  isSSLProcessing(state) {
    return state.customerDomain.isSSLProcessing
  },
  customerPortalLogin(state) {
    return state.customerDomain.customerPortalLogin
  }
}

const mutations = {
  SET_CUSTOM_DOMAIN(state, customDomain = null) {
    if (customDomain === null) return
    state.customDomain.domain = customDomain.domain
  },
  SET_LOADING_CUSTOM_DOMAIN(state, loadingState = false) {
    state.loading.customDomain.add = loadingState
  },
  SET_LOADING_VERIFY_CUSTOM_DOMAIN(state, loadingState = false) {
    state.loading.customDomain.verify = loadingState
  },
  SET_LOADING_RESET_CUSTOM_DOMAIN(state, loadingState = false) {
    state.loading.customDomain.reset = loadingState
  },
  SET_LOADING_CUSTOMER_DOMAIN(state, loadingState = false) {
    state.loading.customerDomain = loadingState
  },
  SET_LOADING_LANGUAGE(state, loadingState = false) {
    state.loading.general.language = loadingState
  },
  SET_CUSTOMER_DOMAIN(state, customerDomain = {}) {
    if (customerDomain === null || customerDomain === undefined) return
    const {
      domain,
      dns_records: dnsRecords,
      ssl_processing: sslProcessing,
      customer_portal_login: customerPortalLogin,
      customer_portal_favicon: customerPortalFavicon,
      default_domain: defaultDomain
    } = customerDomain
    state.customerDomain.domain = domain ? domain : null
    state.customerDomain.isSSLProcessing = sslProcessing == 1
    state.customerDomain.customerPortalLogin = customerPortalLogin
    state.customerDomain.customerPortalFavicon = customerPortalFavicon
    state.customerDomain.defaultDomain = defaultDomain
    if (Array.isArray(dnsRecords)) {
      state.customerDomain.dnsRecords = dnsRecords
      if (dnsRecords.length === 1)
        state.customDomain.verified = dnsRecords[0].status === 'Verified'
    }
  },
  SET_LANGUAGE(state, lang = null) {
    if (!lang) return
    state.general.language = lang
  }
}

const actions = {
  async getCustomerDomain({ commit }) {
    const response = await request({
      method: 'get',
      url: '/v1/pos/settings/customer-portal/domains/show'
    })
    commit('SET_CUSTOMER_DOMAIN', response)
    commit('SET_LANGUAGE', response.customer_portal_lang_code)
  },
  async customDomain({ state }) {
    await request({
      method: 'post',
      url: '/v1/pos/settings/customer-portal/domains',
      data: {
        domain: state.customDomain.domain
      }
    })
  },
  async verifyCustomDomain() {
    return await request({
      method: 'post',
      url: '/v1/pos/settings/customer-portal/domains/verify'
    })
  },
  async resetCustomDomain() {
    return await request({
      method: 'post',
      url: '/v1/pos/settings/customer-portal/domains/reset'
    })
  },
  async uploadIcon(store, payload) {
    await request({
      url: `/v1/pos/settings/customer-portal/upload`,
      method: 'post',
      data: payload,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    await store.dispatch('getCustomerDomain')
  },
  async updateLanguage(_, payload) {
    await request({
      url: `/v1/pos/settings/customer-portal/lang`,
      method: 'post',
      data: payload
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
