import request  from 'utils/request'

const state = () => ({
  data: [],
  upgrade: {},
  checkout: {}
})

const getters = {
}

const mutations = {
  SET_ALL(state, payload) {
    state.data = payload
  },
  SET_UPGRADE(state, payload) {
    state.upgrade = payload
  },
  SET_CHECKOUT(state, payload) {
    state.checkout = payload
  }
}

const actions = {
  async all({ commit } , payload) {
    // TODO: Search by search instead separate address name etc
    const response = await request({
      url: '/v1/all-packages',
      method: 'get',
      params: payload
    })

    commit('SET_ALL', response)
    return response
  },
  async upgrade({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/billing/upgrade/plan',
      method: 'post',
      data: payload,
    })

    commit('SET_UPGRADE', response)
  },
  async checkout({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/billing/checkout/session',
      method: 'post',
      data: payload,
    })
    commit('SET_CHECKOUT', response)
  },
  async showUsage(){
    return await request({
      url: '/v1/pos/billing/report/usage',
      method: 'get'
    })
  },
  async showUsageChart({}, params){
    return await request({
      url: '/v1/pos/billing/report/usage/chart',
      method: 'get',
      params
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}