import request  from 'utils/request'

const state = () => ({
  payment: {},
  subscription: {},
  invoice: [],
  plan: {}
})

const getters = {
}

const mutations = {
  SET_PAYMENT(state, payload) {
    state.payment = payload
  },
  SET_SUBSCRIPTION(state, payload) {
    state.subscription = payload
  },
  SET_INVOICE(state, payload){
      state.invoice = payload
  },
  SET_PLAN(state, payload){
      state.plan = payload
  }
}

const actions = {
//   async plan({ commit }) {
//     const response = await request({
//       url: '/v1/pos/billing/show/payment',
//       method: 'get'
//     })

//     commit('SET_PAYMENT', response)
//   },
  async showPayment({commit}, payload) {
    const response = await request({
      url: `/v1/pos/billing/show/payment`,
      method: 'post',
      data: payload
    })

    commit('SET_PAYMENT', response)
    return response
  },
  async showSubscription({commit}, payload) {
    const response = await request({
      url: `/v1/pos/billing/subscription/info`,
      method: 'post',
      data: payload
    })

    commit('SET_SUBSCRIPTION', response)
    return response
  },
  async getInvoice({commit}, payload) {
    const response = await request({
      url: `/v1/pos/billing/invoice`,
      method: 'post',
      data: payload
    })

    commit('SET_INVOICE', response)
    return response
  },
  async cancelSubscription({commit}, payload) {
    const response = await request({
      url: `/v1/pos/billing/subscription/cancel`,
      method: 'post',
      data: payload
    })
    return response
  },

  async showPlan({commit}, payload) {
    const response = await request({
      url: `/v1/pos/billing/invoice/up`,
      method: 'post',
      data: payload
    })
    commit('SET_PLAN', response)
    return response
  },

  async updatePayment({commit}, payload) {
    const response = await request({
      url: `/v1/pos/billing/add/payment`,
      method: 'post',
      data: payload
    })
    
    return response
  },

  async updateBilling({commit}, payload) {
    const response = await request({
      url: `/v1/pos/billing/update/payment`,
      method: 'post',
      data: payload
    })
    
    return response
  },
  async stripePortal() {
    return await request({
      url: `/v1/pos/billing/customer/portal`,
      method: 'get'
    })
  },
  async unpaidBilling(_, payload){
    const response = await request({
      url: `/v1/pos/billing/invoice/unpaid`,
      method: 'post'
    })
    return response
  },
  async showLocation() {
    const response = await request({
      url: `/v1/pos/settings/store-location`,
      method: 'get',
      params: {
        page: 1,
        limit: 1000,
        is_addtional_info: 1
      }
    })
    
    return response
  },
  async removeLocation({}, payload) {
    return await request({
      url: `/v1/pos/settings/store-location/unsubscribe`,
      method: 'post',
      data: payload
    })
  },
  async checkoutRemoveLocation({}, payload) {
    return await request({
      url: `/v1/pos/settings/store-location/unsubscribe`,
      method: 'post',
      data: {
        _method: 'DELETE'
      }
    })
  },
  async getBillingPackages() {
    return await request({
      url: `/v1/pos/billing/package`,
      method: 'get'
    })
  },
  async checkoutBillingWithPriceId(_, payload){
    return await request({
      url: `/v1/pos/billing/package/checkout`,
      method: 'post',
      data: payload.data
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}