const on = (element, event, handler, useCapture = false) => {
  if(element && event && handler) {
    element.addEventListener(event, handler, useCapture)
  }
}

/* istanbul ignore next */
const off = (element, event, handler, useCapture = false) => {
  if (element && event && handler) {
    element.removeEventListener(event, handler, useCapture)
  }
}

const deepRemoveClasses = (element, classesToRemove=[]) => {
  if(!element) return
  const childrens = Array.from(element.childNodes)
  const isHasChildren = childrens.length > 0
  if(isHasChildren){
    for(let children of childrens){
      deepRemoveClasses(children, classesToRemove)
    }
  }
  if(element.classList){
    for(let classes of classesToRemove){
      element.classList.remove(classes)
    }
  } 
}

export {
  on,
  off,
  deepRemoveClasses
}