import request from 'utils/request'

const state = () => ({
  orders: null,
  customers: null,
  affiliates: null,
  chart: null,
  location: {},
  locationFiltered: {},
  locationPremission: {}
})

const getters = {
  GET_ORDERS_FORMATTED(state) {
    return {
      items: state.orders && state.orders.orders || [],
      total: state.orders && state.orders.total_orders || 0,
      totalDevices: state.orders && state.orders.total_devices || 0
    }
  },
  GET_FORMATTED: (state) => (stateName) => {
    // For customers, affiliates & chart
    return {
      items: state[stateName] || []
    }
  }
}

const mutations = {
  SET_ORDERS(state, payload) {
    state.orders = payload
  },
  SET_CUSTOMERS(state, payload) {
    state.customers = payload
  },
  SET_AFFILIATES(state, payload) {
    state.affiliates = payload
  },
  SET_CHART(state, payload) {
    state.chart = payload
  },
  SET_LOCATION(state, payload) {
    state.location = payload
  },
  SET_LOCATION_FILTERED(state, payload) {
    state.locationFiltered = payload
  },
  SET_LOCATION_PERMISSION(state, payload) {
    state.locationPremission = payload
  }
}

const actions = {
  async changeLocation({ }, payload) {
    const fd = new FormData();
    fd.append('location_id', payload.location_id);
    const response = await request({
      url: "/v1/pos/settings/store-location/switch",
      method: 'post',
      data: fd
    })
    return response
  },
  async locations({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/store-location',
      method: 'get',
      params: payload
    })

    if (!payload.admin_id) {
      commit('SET_LOCATION', response)
    } else commit('SET_LOCATION_FILTERED', response)
    return response
  },
  async locationsPermission({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/settings/location-permission',
      method: 'get',
      params: payload
    })

    commit('SET_LOCATION_PERMISSION', response)
    return response
  },
  async orders({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/dashboard/order/${payload.id}`,
      method: 'get',
      params: payload.data
    })

    commit('SET_ORDERS', response)
    return response
  },
  async arrivingToday() {
    return await request({
      url: `/v1/pos/dashboard/order-arriving-today`,
      method: 'get',
    })
  },
  async inTransit() {
    return await request({
      url: `/v1/pos/dashboard/order-in-transit`,
      method: 'get',
    })
  },
  async customers({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/dashboard/top-customer',
      method: 'get',
      params: payload
    })

    commit('SET_CUSTOMERS', response)
    return response
  },
  async affiliates({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/dashboard/top-sponsor',
      method: 'get',
      params: payload
    })

    commit('SET_AFFILIATES', response)
    return response
  },
  async chart({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/dashboard/chart',
      method: 'post',
      data: payload
    })

    commit('SET_CHART', response)
    return response
  },
  async scanIn({ }, payload) {
    return await request({
      url: '/v1/pos/dashboard/scan-online-buyback',
      method: 'post',
      data: payload
    })
  },
  async search({}, payload) {
    return await request({
      url: '/v1/pos/search',
      method: 'post',
      data: {
        q: payload.q
      },
      ...( payload.cancelToken && { cancelToken: payload.cancelToken })
    })
  },
  async personalize({}, payload) {
    return await request({
      url: '/v1/pos/order-status/dashboard/setting',
      method: 'get',
      params: payload
    })
  },
  async updatePersonalize({}, payload) {
    return await request({
      url: '/v1/pos/order-status/dashboard/setting',
      method: 'put',
      data: payload.data,
      signal: payload.signal
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}