<template>
  <div class="inline-block">
    <Button
      v-if="!$slots.trigger"
      color="white"
      @click="handleShow">
      <div
        :class="[
          'transition-all duration-300 ease-in-out',
          'text-grey-03 hover:text-grey-04 flex items-center gap-x-3'
        ]">
        <span class="material-icons">table_view</span>
        <span>View Invalid Data</span>
      </div>
    </Button>
    <slot
      v-if="$slots.trigger"
      name="trigger"
      v-bind="{
        click: handleShow
      }">
    </slot>
  </div>
  <Modal
    v-model="modal"
    size="md"
    no-close
    hide-footer>
    <template #header>
      <div>
        <span class="font-medium -ml-6">
          Invalid Data Format
        </span>
        <a v-if="!loading" href="#" class="modal__content-header-close top-0 right-0" @click.prevent="handleClose">✕</a>
      </div>
    </template>
    <template #default>
      <div v-loading="loading" class="space-y-8 -mx-6">
        <div
          :class="[
            'bg-red-02 bg-opacity-30',
            'rounded-md p-4 text-red-04',
            'transition-opacity ease-in-out',
            'duration-300',
            loading ? 'opacity-0' : 'opacity-100'
          ]">
          <div class="flex gap-4">
            <span class="material-icons" style="line-height: 1;">warning</span>
            <div class="space-y-2">
              <p>We identified <b>{{ state.totalItems }}</b> rows of data that don't meet the specified format requirements.</p>
              <p>To assist you, you can download a {{ state.typeFile }} file containing only the data that does not meet the required format. This file will help you identify and correct the formatting issues.</p>
              <Button
                :disabled="loading || state.loadingDownload || statePricing?.downloadProgressInvalid > 0"
                color="teal"
                :view="!state.loadingDownload || statePricing?.downloadProgressInvalid > 0 ? 'outlined' : ''"
                dense
                @click="handleDownload">
                <div class="flex items-center justify-center gap-x-2">
                  <span
                    class="material-icons"
                    style="list-style: 1; font-size: 1.25rem !important;">
                    download
                  </span>
                  <p>{{ state.typeFile }}</p>
                </div>
              </Button>
              <Transition name="fade">
                <div v-if="state.loadingDownload || statePricing?.downloadProgressInvalid > 0">
                  <p class="text-left text-sm text-grey-03">Downloading {{ state.typeFile }} template...</p>
                  <div class="pt-1">
                    <div class="w-full bg-grey-01 bg-opacity-80 rounded-full h-1">
                      <div
                        class="bg-teal-04 h-1 rounded-full transition-all ease-out duration-300"
                        :style="{ width: `${statePricing.downloadProgressInvalid}%` }">
                      </div>
                    </div>
                  </div>
                </div>
              </Transition>
            </div>
          </div>
        </div>
        <PriceUploadInvalidTable
          v-model:pagination="state.pagination"
          :loading="loading"
          :date="state.dateUpdate"
          :items="state.items"
          @change-page="handleChangePage"
          @change-limit-page="handleChangePageLimit"
        />
      </div>
    </template>
  </Modal>
  <Modal
    v-model="modalWarning"
    size="xs"
    hide-header
    hide-footer>
    <template #default>
      <div class="py-10 space-y-8">
        <div class="space-y-4">
          <div class="flex items-center justify-center flex-col gap-y-4">
            <img
              src="@/assets/icon/warning.svg"
              alt="Warning icon"
            />
            <h6>Download in Progress</h6>
            <p class="text-center text-grey-02">A template file is currently downloading {{ state.typeFile || 'CSV/XLSX' }}. If you close this window, your download will be interrupted.</p>
          </div>
        </div>
        <div>
          <p class="text-left text-sm">Downloading Progress</p>
          <div class="w-full bg-grey-01 bg-opacity-80 rounded-full h-1">
            <div
              class="bg-teal-04 h-1 rounded-full transition-all ease-out duration-300"
              :style="{ width: `${progress}%` }">
            </div>
          </div>
        </div>
        <div class="flex items-center justify-center gap-x-6">
          <a href="#" @click.prevent="handleCloseWarning">Continue Download</a>
          <Button color="orange" @click="handleConfirmWarning">Close Anyway</Button>
        </div>
      </div>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, ref } from 'vue'

import { usePriceUploadInvalid } from 'composable/pricing'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'

import PriceUploadInvalidTable from './PriceUploadInvalidTable.vue'

export default defineComponent({
  name: 'PriceUploadInvalid',
  components: {
    Modal,
    Button,
    PriceUploadInvalidTable
  },
  emits: ['submit'],
  setup(props, ctx) {
    const {
      state,
      modal,
      loading,
      progress,
      statePricing,
      modalWarning,
      handleShow,
      handleClose,
      handleDownload,
      handleShowList,
      handleCloseWarning,
      handleCancelDownload
    } = usePriceUploadInvalid()
    const handleChangePage = async (e) => {
      state.value.pagination.page = e
      await handleShowList()
    }
    const handleChangePageLimit = async (e) => {
      state.value.pagination.page = 1
      state.value.pagination.limit = e
      await handleShowList()
    }
    const handleConfirmWarning = () => handleCancelDownload()

    return {
      state,
      modal,
      loading,
      progress,
      statePricing,
      modalWarning,
      handleShow,
      handleClose,
      handleDownload,
      handleChangePage,
      handleChangePageLimit,
      handleCloseWarning,
      handleCancelDownload,
      handleConfirmWarning
    }
  }
})
</script>
