import { ref, reactive, computed } from 'vue'
import store from 'store'
import { authRoutes, superRoutes } from 'router'
import { useStore } from 'vuex'
import router from 'router'
import { isFunction } from 'utils/is'
import { destroySession } from 'utils/session'

const state = reactive({
  twoFactorAuth: {
    is_send: false,
    modal: {
      activate: false,
      deactivate: false
    },
    last_saved_phone_number: '',
    data: {
      phone_number: '',
      phone_code: ''
    },
    loading: {
      phone_number_send: false,
      verify_code: false,
      deactivate: false
    },
  }
})

const useUser = () => {
  const intercomPanel = ref(false)
  const intercomLauncher = computed({
    get: () => ({
      index: new Date().getTime(),
      launcher: (window && !!window.Intercom)
    })
  })
  const composeStore = useStore()
  const loading = reactive({
    me: false
  })
  const me = computed(() => {
    return composeStore.state.user.me
  })
  const widgetOptions = computed(() => ({
    apiKey: me.value?.api_key ? me.value.api_key : '',
    tenantId: me.value?.tenant_id ? me.value.tenant_id : '',
    companyToken: me.value?.buyback_company_token ? me.value.buyback_company_token : ''
  }))
  const isTrialEnd = computed(() => {
    return composeStore.state.user.isTrialEnd
  })
  const isUnpaidBilling = computed(() => {
    return composeStore.state.user.isUnpaidBilling == 1
  })
  const isCanceledSubscription = computed(() => {
    return me.value.subscription_status == 3
  })
  const isUseIBAN = computed(() => me.value.country_code === 'IE')
  const isCanViewConfidential = computed(() => me.value.is_view_confidential == 1)
  const isLegacyPlan = computed(() => me.value.package_id !== 9)
  const isMailinLocation = computed(() => me.value.is_location_mail_in == 1)
  const isProPlan = computed(() => [4, 7, 13, 15, 17, 18].includes(me.value.package_id))
  const isPaywalledWidgetPlatformWatermark = computed(() => ![4, 7, 13, 15, 17, 18, 19].includes(me.value.package_id))
  const isRemoveBrandFree = computed(() => me.value.is_remove_brand_free)
  const isEnablePhoneCheck = computed(() => me.value.is_integration_phonecheck_enable === 1)
  const hasDataFeedAccess = computed(() => me.value.is_datafeed == 1)
  const hasPublicApiAccess = computed(() => me.value.is_datafeed == 1)
  const menu = computed(() => {
    return authRoutes.filter(route1 => me.value.menu_v2.menus.some(route2 => route2.slug === route1.name))
  })
  const bankTransferConfig = computed(() => {
    const configs = {
      routing_number: {
        label: me.value.country_code === 'IE' ? 'IBAN' : 'Routing Number'
      },
      account_number: {
        label: me.value.country_code === 'IE' ? 'BIC' : 'Account Number'
      }
    }
    return configs
  })
  const dateByTimezone = computed(() => new Date(new Date().toLocaleString('en-US', {timeZone: me.value.timezone})))
  const currencySymbol = computed(() => me.value.currency_symbol)
  const backgroundProcessData = computed(() => me.value.data_process)
  const isDisableCustomProduct = computed(() => {
    const companies = [716] // [golfadmin@weplayagain.com]
    return companies.includes(me.value.company_id)
  })
  const isCurrentlyProcessingDatafeed =  computed(() => {
    return parseInt(`${me.value.data_process.datafeed_percentage}`) < 100
  })
  const action = async (callback=() =>{}) => {
    try{
      loading.me = true
      await store.dispatch('user/me')
      isFunction(callback) && callback(null)
    }catch(err){
      isFunction(callback) && callback(err)
    }finally{
      loading.me = false
    }
  } 

  return {
    me,
    loading,
    intercomPanel,
    intercomLauncher,
    isTrialEnd,
    isUnpaidBilling,
    isCanceledSubscription,
    isUseIBAN,
    isCanViewConfidential,
    isLegacyPlan,
    isMailinLocation,
    isProPlan,
    isEnablePhoneCheck,
    isCurrentlyProcessingDatafeed,
    hasDataFeedAccess,
    hasPublicApiAccess,
    isDisableCustomProduct,
    isPaywalledWidgetPlatformWatermark,
    isRemoveBrandFree,
    menu,
    bankTransferConfig,
    dateByTimezone,
    widgetOptions,
    currencySymbol,
    backgroundProcessData,
    action
  }
}

const useLogin = () => {
  const composeStore = useStore()
  const data = reactive({
    email: null,
    password: null,
    phone_code: null,
    is_remember: 0,
    login_time: Math.floor(Date.now()/1000)
  })
  const modal = reactive({
    two_factor_auth: false
  })
  const loading = ref(false)
  const loadingLoginStatus = ref(false)
  const loadingResendTwoFactorAuth = ref(false)
  const resendCountdown = ref(null)
  const resendCountdownTimer = ref(0)
  
  const setResendCountdownTimer = (duration=60) => {
    resendCountdownTimer.value = duration
    resendCountdown.value = setInterval(() => {
      if( resendCountdownTimer.value === 0){
        return clearInterval(resendCountdown.value)
      }
      --resendCountdownTimer.value
    }, 1000)
  }
  const unsetResendCountdownTimer = () => {
    if(resendCountdown.value) clearInterval(resendCountdown.value)
    resendCountdownTimer.value = 0
  }
  const login = async (callback = () => { }) => {
    loading.value = true
    try {
      const response = await store.dispatch('user/login', data)
      isFunction(callback) && callback(response)
    }
    finally {
      loading.value = false
    }
  }
  const resendTwoFactorAuthCode = async (callback=()=>{}) => {
    try{
      loadingResendTwoFactorAuth.value = true
      const payload = {
        email: data.email,
        password: data.password
      }
      await store.dispatch('user/requestVerificationCodeLogin', payload)
      isFunction(callback) && callback()
    }finally{
      loadingResendTwoFactorAuth.value = false
    }
  }
  const loginStatus = async (callback = () => {}) => {
    try{
      loadingLoginStatus.value = true
      await store.dispatch('user/loginStatus', '/')
      isFunction(callback) && callback()
    }finally{
      loadingLoginStatus.value = false
    }
  }
  const datamatchStatus = computed(() => composeStore.state.user.isDatamatchProcessing)

  return {
    modal,
    loading,
    loadingLoginStatus,
    loadingResendTwoFactorAuth,
    datamatchStatus,
    data,
    resendCountdown,
    resendCountdownTimer,
    setResendCountdownTimer,
    unsetResendCountdownTimer,
    login,
    loginStatus,
    resendTwoFactorAuthCode
  }
}

const useRegister = () => {
  const data = reactive({
    first_name: null,
    last_name: null,
    company_name: null,
    country_code: null,
    country_name: null,
    currency: null,
    currency_symbol: null,
    phone_number: null,
    email: null,
    password: null,
    confirm_password: null,
    package_id: null,
    referral: null,
    location_type: null
  })
  const countries = ref([])
  const currencies = ref([])
  const loading = reactive({
    all: false,
    countries: false,
    currencies: false
  })
  const action = async (callback = () => { }) => {
    loading.all = true
    try {
      const response = await store.dispatch('user/register', data)
      isFunction(callback) && callback(response)
    }
    finally {
      loading.all = false
    }
  }
  const actionCountries = async (callback=()=>{}) => {
    try{
      loading.countries = true
      const response = await store.dispatch('user/getCountries')
      countries.value = response
      isFunction(callback) && callback()
    }finally{
      loading.countries = false
    }
  }
  const actionCurrencies = async (callback=()=>{}) => {
    try{
      loading.currencies = true
      const response = await store.dispatch('user/getCurrencies')
      currencies.value = response
      isFunction(callback) && callback()
    }finally{
      loading.currencies = false
    }
  }

  return {
    data,
    countries,
    currencies,
    loading,
    action,
    actionCurrencies,
    actionCountries
  }
}

