const useProfile = () => {
  const me = useUser().me
  const data = reactive({
    first_name: me.value.first_name,
    last_name: me.value.last_name,
    email: me.value.email
  })
  const loading = ref(false)
  const modal = ref(false)
  const action = async (callback = () => { }) => {
    loading.value = true
    try {
      const response = await store.dispatch('user/profileUpdate', data)
      isFunction(callback) && callback(response)
      await store.dispatch('user/me')
      modal.value = false
    }
    finally {
      loading.value = false
    }
  }

  return {
    action,
    loading,
    modal,
    data
  }
}

const useChangePassword = () => {
  const data = reactive({
    current_password: null,
    new_password: null,
    confirm_password: null
  })
  const showPassword = reactive({
    current_password: null,
    new_password: null,
    confirm_password: null
  })
  const loading = ref(false)
  const modal = ref(false)
  const unsetData = () => {
    data.current_password = null
    data.new_password = null
    data.confirm_password = null
  }
  const action = async (callback = () => { }) => {
    loading.value = true
    try {
      const response = await store.dispatch('user/passwordUpdate', data)
      isFunction(callback) && callback(response)
      modal.value = false
      unsetData()
    }
    finally {
      loading.value = false
    }
  }

  return {
    action,
    showPassword,
    loading,
    modal,
    data
  }
}

const useLogout = () => {
  const logout = async (callback = () => { }) => {
    await store.dispatch('user/logout')
    window?.CustomerSupport?.use('shutdown')
    store?.state?.routes?.forEach(item => {
      router.removeRoute(item.name)
    })
    destroySession()
    location.href = '/login'
    store.commit('SET_ROUTES', [])
    isFunction(callback) && callback()
  }

  return {
    logout
  }
}

const useVerifyInviteUser = () => {
  const loading = reactive({
    all: false,
    submit: false
  })
  const formData = reactive({
    first_name: '',
    last_name: '',
    job_title: '',
    password: '',
    password_confirmation: ''
  })
  const action = async (token, isExist, callback=()=>{}) => {
    try{
      loading.submit = true
      const payload = {
        ...(!isExist && { data: formData }),
        params: {
          token
        }
      }
      await store.dispatch('user/verifyInvite', payload)
      isFunction(callback) && callback()
    }finally{
      loading.submit = false
    }
  }
  const actionVerifyInvite = async (token, callback=()=>{}, errCallback=()=>{}) => {
    try{
      loading.all = true
      const payload = {
        token
      }
      const response = await store.dispatch('user/getVerifyInvite', payload)
      isFunction(callback) && callback(response)
    }catch(e){
      isFunction(errCallback) && errCallback(e)
    }finally{
      loading.all = false
    }
  }
  return {
    loading,
    formData,
    action,
    actionVerifyInvite
  }
}

const useTwoFactorAuth = () => {
  const resendCountdown = ref(null)
  const resendCountdownTimer = ref(0)

  const unsetData = () => {
    state.twoFactorAuth.data = {
      phone_number: '',
      phone_code: ''
    }
    state.twoFactorAuth.is_send = false
  }
  const openModal = () => state.twoFactorAuth.modal.activate = true
  const openModalDeactivate = () => state.twoFactorAuth.modal.deactivate = true
  const setResendCountdownTimer = (duration=60) => {
    resendCountdownTimer.value = duration
    resendCountdown.value = setInterval(() => {
      if( resendCountdownTimer.value === 0){
        return clearInterval(resendCountdown.value)
      }
      --resendCountdownTimer.value
    }, 1000)
  }
  const unsetResendCountdownTimer = () => {
    if(resendCountdown.value) clearInterval(resendCountdown.value)
    resendCountdownTimer.value = 0
  }
  const closeModal = () => {
    state.twoFactorAuth.modal.activate = false
    unsetResendCountdownTimer()
    unsetData()
  }
  const closeModalDeactivate = () => state.twoFactorAuth.modal.deactivate = false
  const actionSend = async (callback=()=>{}) => {
    try{
      state.twoFactorAuth.loading.phone_number_send = true
      const payload = {
        phone_number: state.twoFactorAuth.data.phone_number
      }
      await store.dispatch('user/requestVerificationCode', payload)
      state.twoFactorAuth.is_send = true
      state.twoFactorAuth.data.phone_code = ''
      state.twoFactorAuth.last_saved_phone_number = state.twoFactorAuth.data.phone_number
      isFunction(callback) && callback()
    }finally{
      state.twoFactorAuth.loading.phone_number_send = false
    }

  }
  const actionVerifyCode = async (callback=()=>{}) => {
    try{
      state.twoFactorAuth.loading.verify_code = true
      const payload = {
        phone_number: state.twoFactorAuth.last_saved_phone_number,
        phone_code: state.twoFactorAuth.data.phone_code
      }
      await store.dispatch('user/verifyVerificationCode', payload)
      await store.dispatch('user/me')
      closeModal()
      isFunction(callback) && callback()
    }finally{
      state.twoFactorAuth.loading.verify_code = false
    }
  }
  const actionDeactivate = async (callback=()=>{}) => {
    try{
      state.twoFactorAuth.loading.deactivate = true
      await store.dispatch('user/verifyVerificationCode', state.twoFactorAuth.data)
      await store.dispatch('user/me')
      closeModalDeactivate()
      isFunction(callback) && callback()
    }finally{
      state.twoFactorAuth.loading.deactivate = false
    }
  }

  return {
    resendCountdown,
    resendCountdownTimer,
    setResendCountdownTimer,
    unsetResendCountdownTimer,
    openModal,
    openModalDeactivate,
    closeModal,
    closeModalDeactivate,
    unsetData,
    actionSend,
    actionVerifyCode,
    actionDeactivate
  }
}

