import request from 'utils/request'

const state = () => ({})

const actions = {
  async getCustomTableViews(_, { params, signal }) {
    const response = await request({
      method: 'GET',
      url: '/v1/pos/custom-table',
      params,
      signal: signal
    })
    return response
  },
  async getCustomTableColumns(_, params) {
    const response = await request({
      method: 'GET',
      url: `/v1/pos/custom-table/all`,
      params
    })
    return response
  },
  async getCustomTableColumnsById(_, payload) {
    const response = await request({
      method: 'GET',
      url: [`/v1/pos/custom-table`, payload.id].join('/')
    })
    return response
  },
  async createCustomTableView(_, payload) {
    const response = await request({
      method: 'POST',
      url: '/v1/pos/custom-table',
      data: payload
    })
    return response
  },
  async updateCustomTableView(_, payload) {
    const response = await request({
      method: 'PUT',
      url: [`/v1/pos/custom-table`, payload.id].join('/'),
      data: payload.data
    })
    return response
  },
  async switchCustomTableView(_, payload) {
    const response = await request({
      method: 'POST',
      url: '/v1/pos/custom-table/switch',
      data: payload
    })
    return response
  },
  async deleteCustomTableView(_, payload) {
    const response = await request({
      method: 'DELETE',
      url: [`/v1/pos/custom-table`, payload.id].join('/'),
      data: payload.data
    })
    return response
  }
}

export default {
  namespaced: true,
  state,
  actions,
  modules: {}
}
