import request from 'utils/request'

const state = () => ({})

const getters = {}

const mutations = {}

const actions = {
  searchItems: async (_, payload) => {
    return request({
      url: `/v1/pos/bulk-quote/search`,
      method: 'get',
      params: payload.params,
      signal: payload.signal
    })
  },
  createBulkQuote: async (_, payload) => {
    return request({
      url: `/v1/pos/bulk-quote`,
      method: 'post',
      data: payload
    })
  },
  getBulkQuoteItems: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/order-item', payload.id || ''].join('/'),
      method: 'get'
    })
  },
  getBulkQuoteItemInfo: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/item-info', payload.id].join('/'),
      method: 'GET'
    })
  },
  createQuoteItem: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/order-item', payload.id].join('/'),
      method: 'POST',
      data: payload.data
    })
  },
  updateQuotePercentage: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/quote-price', payload.id].join('/'),
      method: 'POST',
      data: payload.data,
      signal: payload.signal
    })
  },
  updateQuoteItem: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/order-item', payload.id].join('/'),
      method: 'PUT',
      data: payload.data,
      signal: payload.signal
    })
  },
  deleteQuoteItem: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/order-item', payload.id].join('/'),
      method: 'DELETE',
      data: payload.data
    })
  },
  getQuoteStatuses: async () => {
    return request({
      url: '/v1/pos/order/order-status?buyback=bulk-quote',
      method: 'GET'
    })
  },
  updateQuoteStatus: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/status', payload.id].join('/'),
      method: 'PUT',
      data: payload.data
    })
  },
  getConditionsByProductId: async (_, payload) => {
    return request({
      url: `/v1/buyback/devices/all`,
      method: 'GET',
      params: payload.params
    })
  },
  getConditionsByCategoryId: async (_, payload) => {
    return request({
      url: `/v1/buyback/devices/condition-category`,
      method: 'GET',
      params: payload.params
    })
  },
  getItemColors: async () => {
    return request({
      url: '/v1/pos/order/color-option',
      method: 'GET'
    })
  },
  getSpecs: async (_, payload) => {
    return request({
      url: '/v1/buyback/devices/all',
      method: 'GET',
      params: payload.params
    })
  },
  reconcileItem: async (_, payload) => {
    return request({
      url: ['/v1/pos/bulk-quote/reconcile', payload.id].join('/'),
      method: 'POST',
      data: payload.data
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
