<template>
  <div class="flex p-4">
    <div class="mr-6">
      <img
        width="70"
        height="70"
        alt="Warning Icon"
        src="@/assets/icon/attention-orange.svg"
      />
    </div>
    <div class="mr-4">
      <h3 class="text-xl">Invalid Format Data</h3>
      <p class="mb-4">
        Some Data in CSV File Does Not Meet <br />
        Required Format.
      </p>
      <Button @click="handleViewErrorData" color="teal" view="outlined">
        View Error Data <i class="material-icons">chevron_right</i>
      </Button>
    </div>
  </div>
</template>
<script>
import { defineComponent } from 'vue'
import { useRouter } from 'vue-router'
import { useNotification } from 'composable/notification'
import Button from 'bbUi/components/button/button.vue'
export default defineComponent({
  components: {
    Button
  },
  setup() {
    const router = useRouter()
    const { closeNotification } = useNotification()
    const handleViewErrorData = async () => {
      await router.push({
        name: 'settingBuybackDeviceTab',
        params: {
          category: 'books'
        },
        query: {
          isbn: 'invalid'
        }
      })
      closeNotification()
    }
    return {
      handleViewErrorData
    }
  }
})
</script>
