import request from 'utils/request'

const credentials = {
  klaviyo_private_api_key: null,
  klaviyo_sms_disclosure: null,
  key: '',
  name: '',
  sort_code: [],
  subject: '',
  textinfo: ''
}

const state = () => ({
  credentials,
  loading: {
    credentials: {
      fetch: false,
      connect: false,
      update: false,
      message: false
    }
  }
})

const getters = {}

const mutations = {
  SET_CREDENTIAL(state, credentials = null) {
    if (credentials === null) return
    state.credentials = credentials
  },
  SET_LOADING_CREDENTIAL_FETCH(state, status = false) {
    state.loading.credentials.fetch = status
  },
  SET_LOADING_CREDENTIAL_CONNECT(state, status = false) {
    state.loading.credentials.connect = status
  },
  SET_LOADING_CREDENTIAL_UPDATE(state, status = false) {
    state.loading.credentials.update = status
  },
  SET_LOADING_MESSAGE_FETCH(state, status = false) {
    state.loading.credentials.message = status
  },
  RESET_CREDENTIAL(state) {
    state.credentials = credentials
  }
}

const actions = {
  async connectCredential({ state }, payload) {
    const formData = (payload && { ...payload }) || { ...state.credentials }
    await request({
      url: `/v1/pos/integrations/klaviyo/connection`,
      method: 'post',
      data: formData
    })
  },
  async fetchCredential({ commit }) {
    const response = await request({
      url: `/v1/pos/settings/integration`,
      method: 'get'
    })
    const { klaviyo_private_api_key, klaviyo_sms_disclosure } = response
    const credentials = {
      klaviyo_private_api_key,
      klaviyo_sms_disclosure
    }
    commit('SET_CREDENTIAL', credentials)
    return response
  },
  async fetchMesssage({ commit, state }) {
    const response = await request({
      url: `/v1/pos/settings/text-info/klaviyo-approval-message`,
      method: 'get'
    })
    const credentials = {
      ...state.credentials,
      ...response
    }
    commit('SET_CREDENTIAL', credentials)
    return response
  },
  async resetMessage() {
    const response = await request({
      url: `/v1/pos/settings/text-info/restore/klaviyo-approval-message`,
      method: 'put'
    })
    return response
  },
  async updateCredential({ state }, payload) {
    const formData = (payload && { ...payload, _method: 'PUT' }) || { ...state.credentials, _method: 'PUT' }
    await request({
      url: `/v1/pos/settings/update`,
      method: 'post',
      data: formData
    })
  },
  async updateMessage({ state }, payload) {
    const formData = (payload && { ...payload, _method: 'PUT' }) || { ...state.credentials, _method: 'PUT' }
    await request({
      url: '/v1/pos/settings/text-info/klaviyo-approval-message',
      method: 'post',
      data: formData
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
