import request from 'utils/request'

const state = () => ({
  loading: {
    config: {
      fetch: false,
      update: false
    }
  },
  config: {
    identifier: null,
    identifier_options: []
  }
})

const getters = {}

const mutations = {
  SET_CONFIG(state, payload = null) {
    if (payload === null) return
    Object.assign(state.config, payload)
  },
  SET_LOADING_CONFIG_FETCH(state, status = false) {
    state.loading.config.fetch = status
  },
  SET_LOADING_CONFIG_UPDATE(state, status = false) {
    state.loading.config.update = status
  }
}

const actions = {
  async locations({ commit }, payload) {
    const response = await request({
      url: '/v1/pos/integrations/repairdesk',
      method: 'get',
      ...(payload && {
        params: {
          ...(payload.search && { search: payload.search }),
          page: payload.page,
          limit: payload.limit
        }
      }),
      ...(payload &&
        payload.cancelToken && { cancelToken: payload.cancelToken })
    })
    const { repairdesk_setting } = response
    commit('SET_CONFIG', {
      identifier: repairdesk_setting.repairdesk_section || null,
      identifier_options: repairdesk_setting.repairdesk_section_list || []
    })
    return response
  },
  async testConnection({}, payload) {
    return await request({
      url: `/v1/pos/integrations/repairdesk`,
      method: 'post',
      params: payload
    })
  },
  async removeConnection({}, id) {
    return await request({
      url: `/v1/pos/integrations/repairdesk/${id}`,
      method: 'delete'
    })
  },
  async updateConfig({ state }) {
    await request({
      url: '/v1/pos/settings/update',
      method: 'post',
      data: {
        repairdesk_section: state.config.identifier,
        _method: 'PUT'
      }
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
