import { onDemand } from '@/utils/script-loader'
import { isFunction } from 'utils/is'

export const initializeMap = (callback=()=>{}) => {
  window.initMap = isFunction(callback) ? callback : () => {}
  const gmapUrl = 'https://maps.googleapis.com/maps/api/js?key=AIzaSyDQZjflWRdOfW-UK00jAC8ZhCb2mAzbM4s&libraries=places&callback=initMap'
  onDemand(gmapUrl).load()
}

export const initializeTag = (url, id) => {
  const script = document.createElement('script')
  script.src = `${url}?id=${id}`
  const head = document.head
  head.appendChild(script)
  script.onload = () => {
    const secondScript = document.createElement('script')
    secondScript.innerText = `window.dataLayer = window.dataLayer || [];function gtag() { dataLayer.push(arguments) }gtag('js', new Date());gtag('config', '${id}')
    `
    head.appendChild(secondScript)
  }
}

export const initializeTagManager = (id) => {
  const head = document.head
  const script = document.createElement('script')
  script.innerText =`
    window.dataLayer = window.dataLayer || [];
    (function(w, d, s, l, i) {
      w[l] = w[l] || [];
      w[l].push({
          'gtm.start': new Date().getTime(),
          event: 'gtm.js'
      });
      var f = d.getElementsByTagName(s)[0],
          j = d.createElement(s),
          dl = l != 'dataLayer' ? '&l=' + l : '';
      j.async = true;
      j.src =
          'https://www.googletagmanager.com/gtm.js?id=' + i + dl;
      f.parentNode.insertBefore(j, f);
    })
    (window, document, 'script', 'dataLayer', '${id}');
  `
    head.appendChild(script)
}

export const initializeTagFrame = (id) => {
  const noscript = document.createElement('noscript')
  const iframe = document.createElement('iframe')
  iframe.setAttribute('src', `https://www.googletagmanager.com/ns.html?id=${id}`)
  iframe.setAttribute('width', 0)
  iframe.setAttribute('height', 0)
  iframe.style.display = 'none'
  iframe.style.visibility = 'hidden'
  noscript.appendChild(iframe)

  const body = document.getElementsByTagName('body')[0]
  body.appendChild(noscript)
}
