import request  from 'utils/request'

const state = () => ({
  all: {},
  v2: { all: null },
  show: {},
  update: null
})

const getters = {
}

const mutations = {
  SET_ALL(state, data) {
    state.all = data
  },
  SET_ALL_V2(state, data) {
    state.v2.all = data
  },
  SET_SHOW(state, data){
    state.show = data
  },
  SET_UPDATE_META(state, data) {
    state.update = data
  }
}

const actions = {
  async all({ commit }, payload) {
    const response = await request({
      method: 'get',
      url: `/v1/pos/meta`,
      params: payload
    })
    commit('SET_ALL', response)
  },
  async allV2({ commit }, payload) {
    const response = await request({
      method: 'get',
      url: `/v1/pos/meta-v2`,
      params: payload
    })
    commit('SET_ALL_V2', response)
  },
  async show({ commit }, payload){
    const response = await request({
      method: 'get',
      url: `/v1/pos/meta/${payload}`
    })
    commit('SET_SHOW', response)
  },
  async create(_, payload){
    const response = await request({
      method: 'post',
      url: '/v1/pos/meta',
      data: payload
    })
    return response
  },
  async update(_, payload){
    const response = await request({
      method: 'post',
      url: `/v1/pos/meta/${payload.id}`,
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
    return response
  },
  async updateMeta({ commit }, payload) {
    const response = await request({
      url: `/v1/pos/settings/update`,
      method: 'post',
      data: {
        ...payload,
        _method: 'PUT'
      }
    })

    commit('SET_UPDATE_META', response)
  },
  async delete(_, payload){
    const response = await request({
      method: 'post',
      url: `/v1/pos/meta/${payload}`,
      data: {
        _method: 'DELETE'
      }
    })
    return response
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}