export const onDemand = (src, waitForPageLoad = true) => {
  let isLoaded = false
  let isLoading = false
  const callbacks = []

  const load = (callback = () => {}) => {
    if (isLoaded) return callback()
    if (!isLoading) {
      if (isLoaded) return callback()

      callbacks.push(callback)

      if (!isLoading) {
        isLoading = true

        if (!waitForPageLoad || document.readyState === 'complete')
          _loadScript()
        else window.addEventListener('load', () => _loadScript())
      }
    }
  }

  const _loadScript = () => {
    const script = document.createElement('script')
    script.src = src
    script.onload = () => _invokeCallbacks()
    document.getElementsByTagName('head')[0].appendChild(script)
  }

  const _invokeCallbacks = () => {
    isLoaded = true
    callbacks.forEach((callback) => callback())
  }

  return {
    load
  }
}
