import request  from 'utils/request'

const state = () => ({
})

const getters = {
}

const mutations = {
}

const actions = {
  async all({}, payload) {
    return await request({
      url: `/v1/pos/settings/payment-method`,
      method: 'get',
      params: payload
    })
  },
  async update({}, payload) {
    return await request({
      url: `/v1/pos/settings/payment-method/${payload.id}`,
      method: 'post',
      data: {
        ...payload.data,
        _method: 'PUT'
      }
    })
  },
  async updateSwitch({}, payload) {
    return await request({
      url: `/v1/pos/settings/payment-method/switch`,
      method: 'post',
      data: payload
    })
  },
  async location({}, payload) {
    return await request({
      url: `/v1/pos/settings/payment-method/location/${payload.id}`,
      method: 'get',
      params: payload.data
    })
  },
  async locationUpdate({}, payload) {
    return await request({
      url: `/v1/pos/settings/payment-method`,
      method: 'post',
      data: payload
    })
  },
  async createCustom({}, payload) {
    return await request({
      url: `/v1/pos/settings/payment-method/custom`,
      method: 'post',
      data: payload,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  async updateCustom({}, payload) {
    return await request({
      url: `/v1/pos/settings/payment-method/custom/${payload.id}`,
      method: 'post',
      data: payload.data,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  async deleteCustom({}, id) {
    return await request({
      url: `/v1/pos/settings/payment-method/custom/${id}`,
      method: 'delete'
    })
  },
  async updateExpedited({}, payload) {
    return await request({
      url: '/v1/pos/settings/payment-method/expedited/update',
      method: 'put',
      data: payload
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}